import {isEmpty, has} from 'lodash';
import {SUB_URL, PARAMS_DIVIDER, QUERY_PARAMS_DEVIDER} from '../constants/constants';
import {getParametersFromPath, redirectUrl, getParamsFromUri} from './sn-pd-router.util';
import {getTypeFieldsMap, getDefaultType, queryParamsEnabled} from './sn-pd-router.config';
import {getUrlFromSelectedContent} from './sn-pd-router.contentParser';

export function getSelectedContentFromUrl(prevSelectedContent, path){
    
	let {params: {path: {type, options}, search}} = getParametersFromPath('/:type/:options*', path);
    
	options = options ? options.split('/') : [];
	search = search ? search.split('/') : [];
	if(!type) {
		redirectInvalidUrl(prevSelectedContent);
		return null;
	}
	if(queryParamsEnabled())
		options = [...options, ...search];
	const content = getContent(type, options);
	if(!content){
		redirectInvalidUrl(prevSelectedContent);
		return null;
	}
	
	const subContent = getSubPortion(options);
	
	if(!subContent)
		return content;
	
	return {...subContent, parent: {...content}};   
}

/**
 * Given selectedContent, return a tab payload for an invalid URL
 * @param selectedContent
 * @returns {*}
 * @private
 */
function redirectInvalidUrl(selectedContent){
	// Redirect to selected tab
	const url = getUrlFromSelectedContent(selectedContent);
	redirectUrl(url);
}

/**
 * For a given array of URL slugs, extract args that can be used to create a sub tab or drilldown
 * @param options
 * @returns {*}
 */
function getSubPortion(options){
	const subUrlIdx = options.findIndex(t => t === SUB_URL);
	if(subUrlIdx < 0)
		return false;

	return getContent(options[subUrlIdx + 1], options.slice(subUrlIdx + 2));
}

/**
 * For a given type, generates content using options and configured url fields
 * @param type
 * @param options
 * @returns {*}
 * @private
 */
function getContent(type, options){
	let content = {};
	const typeFieldsMap = getTypeFieldsMap();
	let _type = type, _options = options;
	
	if(!has(typeFieldsMap,type)){
		_type = getDefaultType();
		_options = [type, ...options];
	}
	
	content.type = _type;
	const fields = typeFieldsMap[_type];
	for(let i=0; (i< _options.length && i < fields.length) ; i++){
		if(_options[i] === SUB_URL)
			break;
		if(!queryParamsEnabled() && _options[i] === PARAMS_DIVIDER)
			break;
		if(queryParamsEnabled() && _options[i].indexOf(QUERY_PARAMS_DEVIDER) > -1){
			const parts = _options[i].split(QUERY_PARAMS_DEVIDER);
			if(!isEmpty(parts[0])){
				content[fields[i]] = decodeURIComponent(parts[0]);
			}
			break;
		}
		content[fields[i]] = decodeURIComponent(_options[i]);
	}
	const params = getParamsFromUri(_options);
	return {...content, ...(params && {params})};
}
