import {get} from 'lodash';
import {
	STATUS_LABEL,
	STATUS_DRAFT,
	STATUS_PUBLISHED,
	APPLICATION_SCOPE_LABEL,
	STATUS_PUBLISHED_LABEL,
	STATUS_DRAFT_LABEL,
	DEACTIVATE_LABEL,
	ACTIVATE_LABEL,
	ALL_PLAYBOOKS_BUTTON_LABEL,
	PLAYBOOK_PROPERTIES_LABEL,
	NAVIGATION_PAGE_LOAD,
	HEADER_MODIFIED_INDICATOR,
	UPDATED,
	ACTIVATE_BUTTON_TOOLTIP
} from '../../constants/shared';
import * as ActionTypes from '../../constants/action-types';
import {GlideFormModal} from '../../constants/modal-constants';
import {getTriggerInstanceRecordUrl} from '../../util/iframe-utils';
import {formatScopeDisplayValue} from '../../util/transforms';
import {isObjectWithValues} from '../../util/general-utils';

export const view = (state, {dispatch}) => {
	const playbook = get(state, 'properties.playbook', {});
	const scope = get(playbook, 'scope.displayValue', '');
	const updatedTimestamp = get(playbook, 'updatedDisplayValue', '');
	const playbookSysId = get(playbook, 'sys_id','');
	const snapshotCreated = playbook.snapshot && playbook.snapshot.created;
	const isDraft = playbook.status === STATUS_DRAFT;
	const isPublished = playbook.status === STATUS_PUBLISHED;
	// isModified is a special case of isPublished: published but has modifications
	const isModified = isPublished && playbook.updated !== snapshotCreated;
	const thereIsNoPlaybook = !isObjectWithValues(playbook);
	const label = isPublished ? STATUS_PUBLISHED_LABEL : STATUS_DRAFT_LABEL;
	const highlight = isDraft ? 'low' : 'positive';
	const activateButtonIsDisabled = thereIsNoPlaybook || (isPublished && !isModified);
	const PROPERTIES_BUTTON_LABEL = PLAYBOOK_PROPERTIES_LABEL;
	const triggerInstanceSysId = get(state, 'properties.triggerInstanceSysId', '');

	const handlePlaybookDefinition = (event) => {
		event.preventDefault();
		event.stopPropagation();

		const onSubmit = () => {
			dispatch(ActionTypes.CLOSE_MODAL);
			dispatch(ActionTypes.FETCH_PLAYBOOK);
		};

		if (playbookSysId)
			dispatch(ActionTypes.OPEN_CONFIRM_MODAL_REQUESTED,
				{...GlideFormModal(PLAYBOOK_PROPERTIES_LABEL, getTriggerInstanceRecordUrl(triggerInstanceSysId), {iframe: {renderFormHeaderBar: false, onSubmit}})});
	};

	const handleShowAllPlaybooks = (event) => {
		event.stopPropagation();
		dispatch(ActionTypes.SHOW_ALL_PLAYBOOKS_CLICKED);
		dispatch(ActionTypes.ITEM_SELECTED,  NAVIGATION_PAGE_LOAD.home);
	};

	const activatePlaybook = () => {
		if (!activateButtonIsDisabled)
			dispatch(ActionTypes.ACTIVATE_PLAYBOOK_REQUESTED, {sysId: playbookSysId});
	};

	const deactivatePlaybook = () => {
		if (isPublished)
			dispatch(ActionTypes.DEACTIVATE_PLAYBOOK_REQUESTED, {sysId: playbookSysId});
	};

	return (
		<div id='sn-pd-header'>
			<div className='header-actions'>
				<div className='header-actions-left'>
					<now-button
						icon='chevron-left-fill'
						label={ALL_PLAYBOOKS_BUTTON_LABEL}
						size='md'
						variant='secondary'
						on-click={handleShowAllPlaybooks}
					/>

					<now-button
						label={PROPERTIES_BUTTON_LABEL}
						size='md'
						variant='secondary'
						on-click={handlePlaybookDefinition}
					/>
				</div>

				<div className='header-actions-right'>
					<div className='header-element'>
						<now-label-value-inline label={STATUS_LABEL} />
						{
							thereIsNoPlaybook ?
								<now-highlighted-value label='-' status='none' /> :
								<now-highlighted-value label={label} show-icon status={highlight} />
						}
						{isModified ? <div id='header-modified-indicator'>{HEADER_MODIFIED_INDICATOR}</div> : ''}
						{updatedTimestamp ?
							<now-label-value-inline label={UPDATED.charAt(0).toUpperCase() + UPDATED.slice(1) + ': '}
								value={updatedTimestamp} /> : ''}
					</div>

					<div className='header-element'>
						{
							scope ?
								<now-label-value-inline
									label={`${APPLICATION_SCOPE_LABEL}:`}
									value={formatScopeDisplayValue(scope)}
								/> : ''
						}
					</div>

					<now-button
						disabled={isDraft || thereIsNoPlaybook}
						label={DEACTIVATE_LABEL}
						size='md'
						variant='secondary'
						on-click={deactivatePlaybook}
					/>

					<now-button
						tooltip-content={ACTIVATE_BUTTON_TOOLTIP}
						disabled={activateButtonIsDisabled}
						icon='upload-outline'
						label={ACTIVATE_LABEL}
						size='md'
						variant='primary'
						on-click={activatePlaybook}
					/>
				</div>
			</div>
		</div>
	);
};
