import {controlElementFactory} from '@devsnc/sn-glide-form-controls';
import {CONDITION_TO_RUN_ELEMENT_NAME} from '../condition-to-run/condition-to-run-constants';

export function getFormControl(componentId, type, field, onValueChange, onStagedValueChange) {
	if (type === 'conditions')
		return {
			Control: CONDITION_TO_RUN_ELEMENT_NAME,
			props: {
				field
			}
		};

	return controlElementFactory.getFormControl(
		componentId,
		type,
		field,
		onValueChange,
		onStagedValueChange,
	);
}
