import {map, max, compact, isEmpty, min, isNil, get} from 'lodash';
import {
	CLICK,
	COMPONENT_ID,
	ENTER_KEY,
	IMMEDIATE_START_RULE,
	PROCESS_STATE_LABEL,
	STATE_LABEL, STRING_TYPE
} from '../constants/shared';
import {handleErrorsInPayload} from '../actions/error-actions';
import {getDataFromPayload} from './selectors';
import {GRAPHQL_ERROR_OCCURRED} from '../constants/action-types';
import * as TypeConsts from '../constants/glide-types';

export const isArrayWithValues = (item) => {
	return !!(item && Array.isArray(item) && item.length);
};

export const isObjectWithValues = (item) => {
	return !!(item && typeof item === 'object' && Object.keys(item).length > 0 && !Array.isArray(item));
};

export const isClickOrEnter = (event) => {
	return (event.type === CLICK || event.keyCode === ENTER_KEY);
};

export const getMaxValueOfObjectProp = (object, prop) => {
	const propArr = compact(map(object, prop));
	if (isEmpty(propArr))
		return 0;

	return parseInt(max(propArr));
};

export const getMinValueOfObjectProp = (object, prop) => {
	const propArr = compact(map(object, prop));
	if (isEmpty(propArr))
		return 0;

	return parseInt(min(propArr));
};

export const isDependent = (startRule) => {
	return startRule !== IMMEDIATE_START_RULE;
};

/**
 * Similar to document.querySelector, but searches inside shadow roots as well
 *
 * @param {String} selector - a valid CSS selector
 * @param {Node} [rootEl=window.document.body] - Root element to start traversing down from
 * @return {HTMLElement[]} - an array of nodes
 * */
export const querySelectorDeep = (selector, rootEl) => {
	rootEl = rootEl || window.document.body;
	let matches = [];
	const childNodes = rootEl.childNodes;
	for (let i = 0; i < childNodes.length; i++) {
		matches = matches.concat(querySelectorDeep(selector, childNodes[i]));
		if (childNodes[i].matches && childNodes[i].matches(selector))
			matches.push(childNodes[i]);
	}

	if (rootEl.shadowRoot)
		matches = matches.concat(querySelectorDeep(selector, rootEl.shadowRoot));

	return matches;
};

/**
 * Check if data response is valid object or a messed up data that cannot be used. 
 * Data is return from GraphQL can have null value inside of GraphQL return key object (e.g data: {ProcessAutomation_Mutation: null})
 * This method return true if there is data inside of GraphQL return object.
 * 
 * @param {*} data 
 */
export const isValidData = (data) => {
	if (!data)
		return false;

	// {payload: {data: {}}} is a valid return value for trigger query
	if (typeof data === 'object' && Object.getOwnPropertyNames(data).length === 0)
		return true;

	for (const key in data) {
		const current = data[key];
		// GraphQL response is not a nested object, return false if current is null or undefined
		// eg: data = {GlideRecord_Query: null}
		if (!(current instanceof Object) && isNil(current)) 
			return false;

		// Nested graphQL response object when user goes to designer view from list view:
		// {data: {GlideRecord_Query: { activityStartRules: {_results: []}, laneStartRules: {_results: []}, playbook: {_results: []} }}
		const playbookData = get(current, 'playbook._results');
		// Only need to check for empty result in playbook object
		if (Array.isArray(playbookData) && !isArrayWithValues(playbookData))
			return false;
	}

	return true;
};


export const handleInvalidDataInPayload = (payload, dispatch) => {
	const data = getDataFromPayload(payload);
	const isValid = isValidData(data);
	if (!isValid)
		dispatch(GRAPHQL_ERROR_OCCURRED);

	return isValid;
};


export const responseHasErrorsOrNoData = (payload, dispatch) => {
	const errors = handleErrorsInPayload(payload, dispatch);
	const isValidData = handleInvalidDataInPayload(payload, dispatch);

	if (errors.length || !isValidData)
		return true;
	
	return false;
};

/**
 * This utility function is being provided to work around the fact that we don't want to make any metadata
 * changes for defining selectable properties on components. As a result on the first component bootstrap, we will
 * manually get the component id from the component the implementor cares about and create the selectable property string
 * and apply it to the component.
 *
 * Usage:
 * getSelectableProp('sn-portal-root', 'selectedContent');
 *
 * Returns:
 * '@[component-id of layout]/tabsData'
 *
 * @param componentName
 * @param propName
 * @returns {*}
 */
export const getSelectableProp = (componentName, propName) => {
	let componentIds = {};
	let componentId = componentIds[componentName] || null;

	if (!componentId) {
		const element = document.getElementsByTagName(componentName);
		if (!element.length)
			return {};

		componentId = element[0].getAttribute(COMPONENT_ID);
		componentIds[componentName] = componentId;
	}

	return `@${componentId}/${propName}`;
};

export const guid = () => {
	const x = 2147483648;

	return (
		Math.floor(Math.random() * x).toString(36) +
		Math.abs(Math.floor(Math.random() * x) ^ Date.now()).toString(36)
	);
};

// is a given type a reference
export function isReferenceType(type) {
	if (!type)
		return false;

	return TypeConsts.REFERENCE_TYPES.includes(type);
}

export function getStateDotWalkObject(isProcessState) {
	return {
		displayValue: isProcessState ? PROCESS_STATE_LABEL : STATE_LABEL,
		isReference: false,
		type: STRING_TYPE,
		disabled: false
	};
}

