import * as constants from '../constants/constants';
import {kebabCase, isEmpty, has} from 'lodash';
import {getTypeFieldsMap, getDefaultType, queryParamsEnabled } from './sn-pd-router.config';

/**
 * Given a selected content object, returns a URL for root and leaf content definitions
 * @param selectedContent
 * @returns {*}
 */
export function getUrlFromSelectedContent(selectedContent){
	if(!selectedContent)
		return null;

	const leafUrl = getUrlFromContentDefinition(selectedContent);

	let rootContent = selectedContent.parent;
	while(rootContent && rootContent.parent){
		rootContent = rootContent.parent;
	}

	if(!rootContent)
		return leafUrl;

	const rootUrl = getUrlFromContentDefinition(rootContent);

	if(!rootUrl)
		return leafUrl;

	return `${rootUrl}/${constants.SUB_URL}${leafUrl}`;
}
	

function getUrlFromContentDefinition(contentDef){
	if(!contentDef)
		return '';
	
	const typeFieldsMap = getTypeFieldsMap();
	
	const defaultType = getDefaultType();
	
	const urlType = has(typeFieldsMap, contentDef.type) ? contentDef.type : defaultType;
	
	let url = urlType === defaultType ? '' : `/${urlType}`;
	
	const fields = typeFieldsMap[urlType];
	
	for(let i=0; i<fields.length; i++){
		let urlFragment = contentDef[fields[i]];
		if(urlFragment)
			url += `/${encodeURIComponent(urlFragment)}`;
		else
			return '';
	}
	return url + getUriFromParams(contentDef.params);
}


/**
 * Construct a URL from a params object
 * @param tab
 * @returns {string}
 */
function getUriFromParams(params){
	if(isEmpty(params))
		return '';
	
	if(queryParamsEnabled()){
		return '?' + Object.keys(params).reduce((result, key, i)=>{
			const amp = i === 0 ? '' : '&';
			return `${result}${amp}${encodeURIComponent(kebabCase(key))}=${encodeURIComponent(params[key])}`;
		}, '');
	}

	return '/params' + Object.keys(params).reduce((result, key)=>{
		return `${result}/${encodeURIComponent(kebabCase(key))}/${encodeURIComponent(params[key])}`;
	}, '');
}
