const activityMutationReturnBody = `
      sys_id
      active
      description
      label
      name
      order
      start_rule: start_rule_type
      start_rule_display
      updated: sys_updated_on
      condition_to_run
      lane_id
      error
      inputs {
        variables {
          variableName
          variableValue
          variableMapping
        }
      }
`;

export const playbookActivityMutation = `
mutation($sysId: String!, $label: String, $description: String, $active: String, $condition_to_run: String,
$start_rule: String, $order: Int, $lane: String, $inputs: [GlideProcessAutomation2_VarInputType]) {
  GlideProcessAutomation2_Mutation {
    updateActivity(sys_id: $sysId, label: $label, description: $description, name: $label, active: $active,
    condition_to_run: $condition_to_run, start_rule: $start_rule, order: $order, lane: $lane,
    inputs: $inputs)
    {
        ${activityMutationReturnBody}
    }
  }
}
`;

export const playbookActivityRearrangeMutation = `
mutation($activities: [GlideProcessAutomation2_ActivityInputType]) {
  GlideProcessAutomation2_Mutation {
    updateActivities(activities: $activities)
    {
        ${activityMutationReturnBody}
    }
  }
}`;

export const playbookActivityDeleteMutation = `
mutation($sysId: String!) {
  GlideRecord_Mutation {
    delete_sys_pd_activity(sys_id: $sysId) {
      modified_count
    }
  }
}`;
