/* eslint-disable indent */
import '@servicenow/now-modal';
import '@servicenow/now-loader';
import '../sn-pd-layout';
import '@servicenow/now-visual-board';
import '../board/lane-header';
import '../board/lane-footer';
import '../board/card';
import '../board/lane-header-empty';
import '../configuration-components/configuration-parent';
import '../sn-pd-modal';
import '../header';
import 'sn-pd-picker';
import '../alert';
import '../start-process';
import {BOARD_CONFIG, CREATE_ACTIVITY_DEF_ROLES} from '../../constants/shared';
import '../board/card/card-drag-placeholders';
import get from 'lodash/get';
import {getUserRoles} from '../../util/selectors';
import {isArrayWithValues} from '../../util/general-utils';

export const designer = (state) => {
	const showPicker = get(state, 'activityPicker.show', false);
	const {
		playbook,
		boardData,
		modal,
		alerts,
		configurationState,
		showStartProcessElements,
		userData,
		isLoading,
		configurationDotWalkData
	} = state.properties;
	const userRoles = getUserRoles(userData);
	const userHasCreateActivityDefAccess = CREATE_ACTIVITY_DEF_ROLES.some(r => userRoles.includes(r));

	const renderPicker = () => {
		return (
			<div className='picker-holder'>
				<sn-pd-picker
					component-id='picker'
					variant='activity-picker'
					showPicker={showPicker}
					showCreateItem={userHasCreateActivityDefAccess}
				/>
			</div>
		);
	};

	const renderSpinner = () => {
		return <div className='loader'><now-loader size='lg'></now-loader></div>;
	};

	const renderContent = () => {
		return (
			showStartProcessElements ?
			<div id='start-process-element-container'>
				<sn-pd-start-process />
			</div> :
			<div id='board-container'>
				<now-visual-board
					component-id='board'
					{...boardData}
					{...BOARD_CONFIG}
				/>
			</div>
		);
	};

	return (
		<div id='playbook-designer'>
			{
				isArrayWithValues(alerts) ?
					<sn-pd-alert-list
						alerts={alerts}
						component-id='alert-list'
					/> : ''
			}
			<div id='playbook-container'>
				<sn-pd-modal {...modal} />

				{renderPicker()}

				<sn-pd-layout>
					<div className='pd-header' slot='pd-header'>
						<sn-pd-header component-id='playbook-header' playbook={playbook} />
					</div>

					<div slot='pd-left-panel'>
						{/*placeholder*/}
					</div>

					<div slot='pd-main-content'>
						{isLoading ? renderSpinner() : renderContent()}
					</div>

					<div slot='pd-right-panel'>
						<sn-pd-config-parent configurationState={configurationState} dotWalkData={configurationDotWalkData} />
					</div>
				</sn-pd-layout>
			</div>
		</div>
	);
};
