import '@servicenow/now-select';
import '@servicenow/now-button';
import '@servicenow/now-loader';
import {transformFieldMetaForNowSelect} from './template-value-util';
import {TEMPLATE_FIELD_ADD_ROW} from '../../constants/action-types';
import './template-row';
import {t} from 'sn-translate';

export const view = (state, {dispatch}) => {
	const {fieldMeta, decodedValue, isLoading} = state;
	const {label, dotWalkData, openInModal} = state.properties;
	const items = transformFieldMetaForNowSelect(fieldMeta);

	const addRow = () => dispatch(TEMPLATE_FIELD_ADD_ROW);

	// Render row for each field
	const renderRows = decodedValue => {
		return decodedValue.map((field, idx) => {
			return <sn-pd-template-row rowIndex={idx} dotWalkData={dotWalkData} keyItems={items} field={field} openInModal={openInModal} />;
		});
	};

	const renderLoader = () => {
		return <now-loader />;
	};

	const renderButtons = () => {
		return (
			<div className='template-value-footer'>
				<now-button
					label={t('Add Field')}
					icon=''
					tooltipContent={t('Add a new field')}
					variant='primary'
					size='md'
					configAria={{}}
					on-click={addRow}
				></now-button>
			</div>
		);
	};

	const renderLabel = () => {
		return <div>{label}</div>;
	};

	return (
		<div className='template-value-field'>
			{renderLabel(label)}
			{isLoading ? renderLoader() : null}
			{isLoading ? null : renderRows(decodedValue)}
			{isLoading ? null : renderButtons()}
		</div>
	);
};
