import {get} from 'lodash';
import {NOT_DROPPABLE} from '../constants/shared';
import {handleActivityRearrange} from './rearrange-actions';
import {getCardsFromProperties} from '../util/selectors';

export const handleCardDrop = ({action, dispatch, properties}) => {
	const {
		payload: {
			id: itemId,
			toLane: laneId,
			toPosition: dropIndex
		}
	} = action;

	const cards = getCardsFromProperties(properties);
	const card = get(properties, `playbook.cards[${itemId}]`);

	if (card) {
		if (laneId === NOT_DROPPABLE)
			return cards;

		handleActivityRearrange(card, {laneId, itemId, dropIndex}, dispatch, properties);
	}
};

