import {createCustomElement} from '@servicenow/ui-core';
import {configurationView as view} from './config-panel-view';
import {get, truncate, isNil, set} from 'lodash';
import {getEditLink} from '../config-utils';
import * as AppConst from '../../../constants/shared';
import {getConfigurationStateFromProperties} from '../../../util/selectors';
import {getSelectableProp} from '../../../util/general-utils';
import {SN_CANVAS_SESSION} from '../../canvas/constants/constants';
import styles from './config-panel.scss';
import * as PanelConsts from '../config-constants';
import {isFieldValid} from '../../dynamic-form/dynamic-form-helpers';
import {t} from 'sn-translate';
import {getFormFields} from '../config-utils';

createCustomElement('sn-pd-config-panel', {
	view,
	transformState(state) {
		const {properties: {configurationState}} = state;
		if (configurationState.panelOpen === undefined)
			return {...set(state, 'properties.configurationState.panelOpen', false)};

		return state;
	},
	properties: {
		sysId: {
			computed({properties: {configurationState}}) {
				return get(configurationState, 'config.sys_id');
			}
		},
		configurationState: {},
		dotWalkData: {},
		formFields: {
			computed({properties: {configurationState, userData}}) {
				const {fields, inputs} = getFormFields(configurationState, PanelConsts.CONFIG_PANEL_VIEW, userData);

				return [...fields, ...inputs];
			}
		},
		invalidFields: {
			computed({properties: {formFields}}) {
				const invalid = (field) => !isFieldValid(field);
				return formFields.some(invalid);
			}
		},
		userData: {
			get default() {return getSelectableProp(SN_CANVAS_SESSION, AppConst.SESSION_USER);}
		},
		triggerInstanceSysId: {
			get default() {return getSelectableProp(AppConst.PROVIDER, AppConst.TRIGGER_INSTANCE_SYS_ID);}
		},
		textualMessage: {
			computed({properties: {configurationState}}) {
				return get(configurationState, 'textualMessage', {});
			}
		},
		heading: {
			computed({properties}) {
				const configurationState = getConfigurationStateFromProperties(properties);
				const label = get(configurationState, 'config.fields[0].value', '');

				if (label)
					return truncate(label.toUpperCase(), {
						'length': AppConst.PANEL_HEADING_LENGTH_LIMIT,
						'separator': '...'
					});

				return '';
			}
		},
		editLinkUrl: {
			computed({properties}) {
				if (!isNil(get(properties, 'configurationState.type')))
					return getEditLink(properties);

				return null;
			},
		},
		ariaLiveContent: {
			computed({properties}) {
				const configurationState = get(properties, 'configurationState', {});
				const prefix = 'panel content for';
				const suffix = 'loaded';
				const label = get(configurationState, 'config.fields[0].value', '');
				return t('{0} {1} {2} {3}', prefix, configurationState.type, label, suffix);
			}
		}
	},
	styles
});
