import {
	USER_PREF_UPDATE,
	USER_PREF_UPDATE_FAILED,
	USER_PREF_UPDATE_STARTED,
	USER_PREF_UPDATE_SUCCEEDED,
	USER_PREF_UPDATE_ERROR
} from '../constants/constants';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import get from 'lodash/get';

const userPrefUpdateQuery = `
	mutation($name: String!, $value: String!) {
	  GlideDomain_Mutation {
		user {
		  setPreference(name: $name, value: $value) {
			name
			value
		  }
		}
	  }
	}
`;

const userPrefUpdate = createGraphQLEffect(userPrefUpdateQuery, {
	variableList: ['name', 'value'],
	startActionType: USER_PREF_UPDATE_STARTED,
	successActionType: USER_PREF_UPDATE_SUCCEEDED,
	errorActionType: USER_PREF_UPDATE_FAILED
});

const userPrefUpdateSucceededEffect = ({ action: { payload }, properties : {userPrefs}, dispatch, updateProperties }) => {

	const { errors = [] } = payload;

	if (errors.length) {
		console.error('GlideDomain_Mutation.user.setPreference returned errors: ', errors);
		dispatch(USER_PREF_UPDATE_ERROR, errors);
		return;
	}
	const userPref = get(payload, 'data.GlideDomain_Mutation.user.setPreference');

	let userPreferencesNew = [];

	if(userPrefs)
		userPreferencesNew = userPrefs.slice(0);

	if(userPref) {
		const {name, value} = userPref;
		const prefIdx = userPrefs.findIndex(p => p.name === name);
		if(prefIdx >= 0)
			userPreferencesNew[prefIdx] = {...userPrefs[prefIdx], value};
		else
			userPreferencesNew.push({name, value});
	}

	updateProperties({userPrefs : userPreferencesNew, preventDefault : true});
};

export const actionHandlers = {
	[USER_PREF_UPDATE_SUCCEEDED]: {
		effect: userPrefUpdateSucceededEffect,
		stopPropagation: true
	},
	[USER_PREF_UPDATE]: {
		...userPrefUpdate,
		stopPropagation: true
	}
};
