import '@servicenow/now-card';
import '@servicenow/now-label-value';
import '../../textual-message';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import {ACTIVITY, START_LABEL, NOW_BUTTON_ICONIC, NOW_DROPDOWN_PANEL, CARD_ORDER_USER_PREF} from '../../../constants/shared';
import {buildActivityDropdownActions, ACTIVITY_DROPDOWN_LABEL} from './card-dropdown-config';
import {isArrayWithValues, isClickOrEnter} from '../../../util/general-utils';
import {isDropdownInPath} from '../../../util/accessibility-utils';
import {POPULATE_CONFIGURATION_PANEL} from '../../../constants/action-types';
import {getUserRoles} from '../../../util/selectors';
import {createRef} from '@servicenow/ui-renderer-snabbdom';


const view = (state, {dispatch}) => {
	const {properties: {data: card, userData, userPrefs}} = state;
	const orderPreference = isArrayWithValues(userPrefs) && userPrefs.filter(pref => pref.name === CARD_ORDER_USER_PREF);
	const shouldShowOrder = orderPreference && orderPreference[0] && orderPreference[0].value;
	const cardElement = createRef();

	if (!card)
		return null;

	const {
		label,
		description,
		order,
		start_rule,
		textualMessages,
		activity_definition
	} = card;

	const ariaDesc = `${label} ${isEmpty(description) ? '' : description}`;
	const startRule = start_rule && start_rule.displayValue;

	const activityType = get(card, 'activity_definition.activity_type', '');
	const activityTypeName = get(activityType, 'name');
	const activityTypeIcon = get(activityType, 'icon');
	const activityTypeColor = get(activityType, 'color');

	const userRoles = getUserRoles(userData);
	let badge = get(activity_definition, 'badge');
	badge = (badge && badge + '.iix') || null;

	const showConfigOnClickOrEnter = (event) => {
		const isNotMenuButtonOrDropdown = !isDropdownInPath(event, NOW_BUTTON_ICONIC) && !isDropdownInPath(event, NOW_DROPDOWN_PANEL);
		if (isClickOrEnter(event) && isNotMenuButtonOrDropdown)
			dispatch(POPULATE_CONFIGURATION_PANEL, {configType: ACTIVITY, data: card, triggerElement: cardElement.current});
	};

	const getNowCardClasses = () => {
		const baseClass = 'pd-card';
		if (isArrayWithValues(textualMessages)) {
			const cardMessage = textualMessages[0];
			return `${baseClass} ${cardMessage.type.toLowerCase()}`;
		}
		return baseClass;
	};

	const showTextualMessage = () => {
		// For now, we are only showing the first message / error a card receives
		if (isArrayWithValues(textualMessages)) {
			const cardMessage = textualMessages[0];
			return (
				<div className='textual-message-container'>
					<sn-pd-textual-message message={cardMessage.message} level={cardMessage.type}/>
				</div>
			);
		}

		return (
			<div className='textual-message-container'></div>
		);
	};

	const convertHexToRgba = (hex, opacity = 1) => {
		if (!hex)
			return 'transparent';

		const [r, g, b] = hex.match(/\w\w/g).map(x => parseInt(x, 16));
		return `rgba(${r},${g},${b},${opacity})`;
	};

	const activityTypeColorSetting =  activityTypeColor || '#ffffff';
	const activityTypeTagStyle = {background: convertHexToRgba(activityTypeColorSetting, .3)};
	const sidebarStyle = {'background-color': activityTypeColorSetting};
	const orderWidth = card.orderWidth ? {width: card.orderWidth} : {};

	return (
		<div className='pd-card-wrapper'>
			{shouldShowOrder === 'true' ? <div className='card-order' style={orderWidth}>{order}</div> : ''}
			<div className='pd-card-sidebar' style={sidebarStyle}></div>
			<now-card
				ref={cardElement}
				className={getNowCardClasses()}
				interaction='click'
				size='lg'
				configAria={{'aria-label': ariaDesc}}
				on-click={showConfigOnClickOrEnter}
				on-keydown={showConfigOnClickOrEnter}
			>
				<div className='pd-card-container'>
					<now-card-header
						className='header'
						heading={{label: label, size: 'sm', variant: 'secondary', lines: 1, level: 4}}
						dropdowns={buildActivityDropdownActions(card, userRoles)}
						configAria={{'aria-label': ACTIVITY_DROPDOWN_LABEL}}
					>
						{badge ? <div slot='identifier' ><img className='activity-badge' src={badge}/> </div> : ''}
						<slot name='header-identifier' />
					</now-card-header>

					<div className='content-container'>
						<slot name='content'>
							<div className='snippet'>{description}</div>
							{ showTextualMessage() }
						</slot>
					</div>

					<slot name='footer' className='footer'>
						<now-card-footer split='equal'>
							<span slot='start'>
								<now-label-value-inline label={START_LABEL} value={startRule} />
							</span>
							<span slot='end'>
								<div>
									<span className='type-tag' style={activityTypeTagStyle}>
										{
											activityTypeIcon ?
												<now-icon size='sm' icon={activityTypeIcon}></now-icon> :
												<div className='icon-placeholder'></div>
										}
										{activityTypeName}
									</span>
								</div>
							</span>
						</now-card-footer>
					</slot>
				</div>
			</now-card>
		</div>
	);
};

export {view};
