import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {view} from './template-row-view';
import styles from './_template-row.scss';
import * as ActionTypes from '../../../constants/action-types';
import * as Handlers from './template-row-action-handlers';

createCustomElement('sn-pd-template-row', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		field: {},			// rendered field meta information
		keyItems: [],		// items that go into the dropdown of the key field
		rowIndex: {
			default: null   // used to edit or delete existing decoded value data
		},
		dotWalkData: {},
		openInModal: false
	},
	actionHandlers: {
		[ActionTypes.NOW_SELECT_SELECTED_ITEM_SET]: Handlers.handleKeySelected,
		[ActionTypes.PILL_VALUE_SELECTED]: Handlers.handleValueSelected
	},
	styles
});
