import { concat, flatten, values, fromPairs } from 'lodash';
import * as SharedConsts from '../constants/shared';
import {getStartRuleNameById} from '../components/configuration-components/config-utils';

const START = '#START';

/**
 * Helper methods for lookup of dependencies for the playbook
 *
 * @param {object} playbook - the playbook object itself
 * @param {object} dependencies  - dependencies computed by getPlaybookDependencyRelationships
 */
export const playbookDependencyLookup = (playbook, dependencies) => {

	return {
		/**
         * Returns a list of lane ids that will execute
         * before the input lane id. If there are no lane
         * predecessors returns an empty list
         *
         * @param {string} laneId
         * @returns array containing previous lane ids (empty array if there are no previous lanes)
         */
		getLanePredecessors(laneId) {

			return filterStart(
				dependencies
					.laneDeps
					.predecessors[laneId]);
		},

		/**
         * @param {string} activityId
         * @returns array containing any activity ids that will run before the input
         */
		getActivityPredecessors(activityId) {

			//get activities lane
			const activitiesLaneId = playbook.cards[activityId].lane_id;

			//all dependency relationships for the lane
			const laneDependencies = dependencies.cardDepsByLane[activitiesLaneId];

			//all prior activities in the specific lane
			const lanesPreceedingActivities = filterStart(laneDependencies.predecessors[activityId]);

			const previousLanes = this.getLanePredecessors(activitiesLaneId);

			//for each prior lane collect all activities in that lane
			const previousLanesActivities = flatten(previousLanes.map(laneId => playbook.lanes[laneId].cards));

			return concat(lanesPreceedingActivities, previousLanesActivities);
		},

		getLaneSuccessors(laneId) {
			return filterStart(dependencies.laneDeps.successors[laneId]);
		},

		laneHasReferencingStartRules(laneId) {
			const startRule = dependencies.laneDeps.with_previous[laneId] || dependencies.laneDeps.after_previous[laneId];
			return startRule !== undefined;
		}
	};
};

function filterStart(items = []) {
	return items.filter(id => START !== id);
}

/**
 * Calcuates and returns an object containing
 * dependency relationships between lanes and
 * activities.
 *
 * @param {object} - input playbook
 * @returns {object} - object containing relationships
 */
export const getPlaybookDependencyRelationships = (playbook, laneStartRules, activityStartRules) => {

	const laneDeps = computeDependencies(playbook.lanes, laneStartRules);

	const cardDepsByLane = fromPairs(
		values(playbook.lanes).map(lane => {
			if (lane.cards) {
				const lanesCards = fromPairs(lane.cards.map(sys_id => [sys_id, playbook.cards[sys_id]]));
				const laneCardDeps = computeDependencies(lanesCards, activityStartRules);
				return [lane.sys_id, laneCardDeps];
			} else
				return [lane.sys_id, {}];
		})
	);

	return { laneDeps, cardDepsByLane };

};


/**
 *
 * This calculates the sucessor/predecessor relationships
 * between all items in the input.
 *
 * @param {} itemsById
 */
function computeDependencies(itemsById, startRules) {

	//the map is to clone the items so we don't mutate
	//state passed in
	const items = values(itemsById).map(a => ({...a}));

	//sort lanes to process in order
	items.sort(({ order: orderA }, { order: orderB }) => orderA - orderB);

	let successors = { [START]: [] };
	let predecessors = { [START]: [] };

	//capture these relationships for faster lookup
	let with_previous = {};
	let after_previous = {};

	items.forEach((item, index) => {
		successors[item.sys_id] = [];
		predecessors[item.sys_id] = [];

		//this normalizes the order within a lane since it is
		//possible to have arbitrary order indexes and we want
		//to just make them relative to their slot in the array
		//while we look for previous items
		item.normalizedOrder = index;
	});


	/**
	 * For each item keep track of its predecessors and sucessors
	 */
	items.forEach(item => {
		const { start_rule, normalizedOrder: index, sys_id } = item;

		if (start_rule === undefined)
			return;

		const startRuleId = start_rule && start_rule.value ? start_rule.value : start_rule;
		const startRule = getStartRuleNameById(startRules, startRuleId);

		//The additional index === zero check is to deal with
		//bad data.  We don't currently validate start_rules so
		//someone can make the first activity in a lane be
		//after or with previous as it's start rule

		if (startRule === SharedConsts.IMMEDIATE_START_RULE || (index === 0)) {
			//Add entry to START key for any immediate lanes
			successors[START].push(sys_id);
			predecessors[sys_id].push(START);
		} else if (startRule === SharedConsts.AFTER_PREVIOUS_START_RULE) {
			const prevIndex = index - 1;

			const predId = items[prevIndex].sys_id;
			successors[predId].push(sys_id);
			predecessors[sys_id].push(predId);
			after_previous[predId] = sys_id;
		} else if (startRule == SharedConsts.WITH_PREVIOUS_START_RULE) {
			const prevIndex = index - 1;

			//  If we start WITH previous we will copy the
			//  previous lanes predecessors and place an entry
			//  to the successor list of any of the predecessors of
			//  the previous lane
			const predId = items[prevIndex].sys_id;
			predecessors[sys_id] = Array.from(predecessors[predId]);
			predecessors[predId].forEach(id => successors[id].push(sys_id));
			with_previous[predId] = sys_id;
		} else
			throw Error('invalid start_rule: ' + startRule);

	});

	return { successors, predecessors, with_previous, after_previous };
}
