import '@servicenow/now-button';
import {ADD_A_STEP} from './lane-footer-constants';
import '@servicenow/now-icon';
import {ADD_ITEM_TO_LANE_CLICKED} from '../../../constants/action-types';
import {ACTIVITY} from '../../../constants/shared';
import {createRef} from '@servicenow/ui-renderer-snabbdom';
import {t} from 'sn-translate';

const view = (state, {dispatch}) => {
	const {properties: {data: {sys_id, order}}} = state;

	const footerButtonElement = createRef();
	const ariaLabel = t('Click to add an activity to lane {0}', order);

	const handleOnClick = () => {
		dispatch(
			ADD_ITEM_TO_LANE_CLICKED,
			{variant: ACTIVITY, laneId: sys_id, returnFocusElement: footerButtonElement.current, show: true}
		);
	};

	const onKeyDown = (event) => {
		if (event.keyCode === 13)
			handleOnClick();

		event.stopPropagation();
	};

	return (
		<footer className='lane-footer'>
			<now-button-bare
				ref={footerButtonElement}
				on-click={handleOnClick}
				on-keydown={onKeyDown}
				configAria={{'aria-label': ariaLabel}}
				icon-start='plus-outline'
				label={ADD_A_STEP}
				size='md'
				variant='primary'
			/>
		</footer>
	);
};

export {view};
