import {camelCase, intersection, get} from 'lodash';
import {PAGE_TYPE, ACTIVATED_PROP} from '../constants/constants';

export function computeShowFromProps(properties) {
	const {selectedContent, pickerCondition} = properties;
	if(!pickerCondition || !selectedContent || Object.keys(selectedContent).length === 0)
		return false;

	const pickerState = transformSelectedContent(selectedContent);
	if (!pickerState)
		return false;

	const conditions = parsePickerCondition(pickerCondition);
	return validateConditions(properties, pickerState, conditions);
}

export function transformSelectedContent(selectedContent) {
	const {type, page_id: pageId} = selectedContent;

	if (type !== PAGE_TYPE || !pageId)
		return;

	return {
		pageId
	};
}

export function parsePickerCondition(condition) {
	const parts = condition.split('^');
	const conditions = {};

	parts.forEach(part => {
		const isRoles = part.startsWith('roles');
		let token = isRoles ? 'IN' : '=';
		const pieces = part.split(token);
		const result = isRoles ? pieces[1].split(',') : pieces[1];

		conditions[camelCase(pieces[0])] = result;
	});

	return conditions;
}

export function validateConditions(props, pickerState, conditions) {
	let shouldRender = true;

	const keys = Object.keys(conditions);
	if (keys.length > 0) {
		keys.forEach(key => {
			if (key === 'roles') {
				const currentRoles = get(props, 'behaviors.agentRoles', null);
				const result = intersection(currentRoles, conditions[key]);
				if (!result || result.length <= 0)
					shouldRender = false;
			} else {
				if (conditions[key] !== pickerState[key])
					shouldRender = false;
			}
		});
	}

	return shouldRender;
}

/**
 * Some elements may be coming in with the deferrred property.
 * This is utilized to manually remove the deferred propery when ready.
 *
 * @param node
 */
export function activateDeferredElement(node) {
	if (!node)
		return;

	const rootNode = node.getRootNode();
	if (!rootNode)
		return;

	const assignedElement = getAssignedElement(rootNode);
	if (assignedElement && assignedElement.hasAttribute('defer')) {
		window.requestAnimationFrame(() => assignedElement.removeAttribute('defer'));
	}
}

/**
 * Get the node within the default slot.
 *
 * @param rootNode
 * @returns {*}
 */
export function getAssignedElement(rootNode) {
	const slots = [...rootNode.querySelectorAll('slot')];
	const foundSlot = slots.length > 0 ? slots[0] : null;

	if (foundSlot)
		return foundSlot.assignedNodes().filter(node => node.nodeType === 1)[0];
}

export function setActive(ref, isActive) {
	if (!ref)
		return;

	const assignedElement = getAssignedElement(ref);
	if (!assignedElement)
		return;

	assignedElement[ACTIVATED_PROP] = isActive;
}

/**
 * On load of the picker, set up initial data.
 *
 * - Set a prop "activated" on the slotted child to match the show value.
 * - Also, set any params spread as props on that child element.
 *
 * @param elm
 * @param state
 */
export const handleElementLoad = (elm, state) => {
	if (!elm)
		return;

	const {show} = state.properties;
	const params = get(state.properties, 'selectedContent.params', null);

	if(show)
		setSlottedParams(elm, params);

	setActive(elm, show);
};

/**
 * Find the slotted element and set properties directly on it for params.
 *
 * Elements in Seismic 2 have the ability to setProperties through an API point. This wasn't the case for Seismic 1 where
 * we directly added them onto the element.
 * @param ref
 * @param params
 */
const PREV_PARAMS_CACHE = {}; // Map of component-id and its previous params
export function setSlottedParams(ref, params) {
	if (!ref)
		return;
		
	const assignedElement = getAssignedElement(ref);
	if (!assignedElement)
		return;
	
	const componentId = assignedElement.getAttribute('component-id');
	const cachedParams = PREV_PARAMS_CACHE[componentId];
	
	if(cachedParams)
		Object.keys(cachedParams).forEach(prevKey => assignedElement[prevKey] = undefined);
	
	if(params)
		Object.keys(params).forEach(key => assignedElement[key] = params[key]);
	
	PREV_PARAMS_CACHE[componentId] = params;
}
