import {SELECTED_CONTENT_CHANGED, URL_CONTENT_LOADED, ITEM_SELECTED, CONTENT_UPDATED, USER_PREF_CHANGED, NAVIGATION_REQUESTED} from '../constants/constants';
import isEmpty from 'lodash/isEmpty';
import {transformItemSelectedToContent} from './util';

export default {
	[ITEM_SELECTED]: handleItemSelected,
	[SELECTED_CONTENT_CHANGED]: handleSelectedContentChanged,
	[CONTENT_UPDATED]: handleContentUpdated,
	[URL_CONTENT_LOADED] : handleSelectedContentChanged,
	[USER_PREF_CHANGED] : handleUserPreferencesChanged,
	[NAVIGATION_REQUESTED] : handleRequestedNavigation
};

function handleSelectedContentChanged({action, updateProperties}) {
	updateProperties({selectedContent: action.payload});
}

function handleItemSelected({action, updateProperties}) {
	const selectedContent = transformItemSelectedToContent(action.payload);
	if (selectedContent)
		updateProperties({selectedContent});
}

function handleContentUpdated({state, action: {payload}, updateProperties}){
	let { params = {}, redirect = false, ...restPayload } = payload;
	
	const {selectedContent} = state;
	
	const {params: currentParams = {}, ...currentRestSelectedContent} = selectedContent;
	
	if(redirect && !isEmpty(params) && isEmpty(currentParams))
		redirect = true;
	
	updateProperties({selectedContent: {...currentRestSelectedContent, ...restPayload, params: {...currentParams, ...params}, redirect}});
}

function handleUserPreferencesChanged({action, updateProperties}){
	updateProperties({changedUserPref: action.payload});
}

function handleRequestedNavigation({action, updateProperties}){
	updateProperties({urlContent : {...action.payload}}); 
}
