import {get, uniqBy} from 'lodash';
import * as ActionTypes from '../constants/action-types';
import * as Shared from '../constants/shared';
import * as Errors from '../util/error-messages';
import {getErrorsFromPayload} from '../util/selectors';
import {isArrayWithValues} from '../util/general-utils';

export const handleGraphQLErrors = ({dispatch}) => {
	dispatch(ActionTypes.SHOW_ALERTS, {alerts: [{message: Errors.GENERIC_GRAPHQL_ERROR}]});
};

export const handleNetworkError = ({dispatch}) => {
	dispatch(ActionTypes.SHOW_ALERTS, {alerts: [{message: Errors.GENERIC_NETWORK_ERROR}]});
};

export const handleDeclarativeActionAlert = ({action: {payload}, dispatch}) => {
	const notifications = get(payload, 'notifications', []);
	const errors = notifications.some((notification) => notification.type === 'error');

	if (errors)
		dispatch(ActionTypes.SHOW_ALERTS, {
			alerts: [{
				message: Errors.DECLARATIVE_ACTION_ERROR,
				textLinkProps: Shared.APPLICATION_ADMINISTRATION_TEXT_LINK_PROPS
			}]
		});
};

export const handlePlaybookValidationError = ({action: {payload}, properties: {playbook}, dispatch, updateProperties}) => {
	// Server errors are returned in an array (even if only a single error occurred)
	const errors = getErrorsFromPayload(payload);
	const errorsWithoutActivityRelatedErrors = Errors.getCustomErrors(filterOutActivityRelatedErrors(errors));
	const activityRelatedErrors = Errors.getCustomErrors(getActivityRelatedErrors(errors));
	const uniqueActivityRelatedErrors = uniqBy(activityRelatedErrors, 'message');
	const alerts = [...errorsWithoutActivityRelatedErrors, ...uniqueActivityRelatedErrors];

	// If activity related errors, get messages to show in cards and panel
	if (isArrayWithValues(activityRelatedErrors)) {
		const textualMessages = activityRelatedErrors.map(error => get(error, 'textualMessage', {}));
		updateProperties({playbook: {...playbook, textualMessages}});
	}

	// All errors will be displayed using alert-list component
	dispatch(ActionTypes.SHOW_ALERTS, {alerts});
};

export const handleErrorsInPayload = (payload, dispatch) => {
	const errors = getErrorsFromPayload(payload);
	if (errors.length)
		dispatch(ActionTypes.GRAPHQL_ERROR_OCCURRED);
	return errors;
};

export function getActivityRelatedErrors(errors) {
	return errors.filter(error => {
		const tableReference = get(error, 'reference.tableName', '');
		return tableReference === Shared.PLAYBOOK_ACTIVITY_TABLE;
	});
}

export function filterOutActivityRelatedErrors(errors) {
	return errors.filter(error => {
		const tableReference = get(error, 'reference.tableName', '');
		return tableReference !== Shared.PLAYBOOK_ACTIVITY_TABLE;
	});
}
