import {createCustomElement} from '@servicenow/ui-core';
import * as Consts from '../config-constants';
import * as ConfigActions from './config-parent-actions';
import * as ActionTypes from '../../../constants/action-types';
import {view} from './config-parent-view';

createCustomElement('sn-pd-config-parent', {
	view,
	properties: {
		configurationState: {},
		dotWalkData: {}
	},
	actionHandlers: {
		[Consts.PANEL_FORM_FIELD_UPDATED]: ConfigActions.updateFormField,
		[Consts.SAVE_CONFIGURATION_DATA_CLICKED]: ConfigActions.prepPayloadAndDispatchSave,
		// modify condition to run modal
		[ActionTypes.CONDITION_BUILDER_QUERY_CHANGED]: ConfigActions.stageConditionToRunQueryChange,
		[ActionTypes.MODIFY_CONDITION_TO_RUN]: ConfigActions.modifyConditionToRun,
		[ActionTypes.NOW_MODAL_OPENED_SET]: {
			effect: ConfigActions.clearConditionToRunStagedChange,
			stopPropagation: true
		},
		[ActionTypes.CLOSE_CONDITION_MODAL]: ConfigActions.clearConditionToRunStagedChange,
		// config modal
		[ActionTypes.SHOW_CONFIG_MODAL]: ConfigActions.showConfigurationModal,
		[ActionTypes.DISMISS_CONFIG_MODAL]: ConfigActions.hideConfigurationModal
	}
});
