export const playbookMutation = (variables) => {
	return {
		variables,
		query: `mutation($sysId: String!, $label: String, $description: String) {
			  GlideRecord_Mutation {
				update_sys_pd_process_definition(sys_id: $sysId, label: $label, description: $description) {
				  sys_id { value }
				  label { value }
				  description { value }
				  updated: sys_updated_on { value }
				}
			  }
			}`
	};
};

export const activatePlaybookMutation = `
mutation($sysId: String!) {
  GlideProcessAutomation_Mutation {
    activate(sysId: $sysId) {
      processDefinition {
        status
        snapshot { sys_id created }
        updated
        updatedDisplayValue
      }
      errors {
      	errorType
        message
        reference {
        	tableName
        	sysId
        }
      }
    }
  }
}`;

export const deactivatePlaybookMutation = `
mutation ($sysId: String!) {
  GlideProcessAutomation_Mutation {
    deactivate(sysId: $sysId) {
      status
      updated
      updatedDisplayValue
    }
  }
}`;

export const deletePlaybookMutation = `
mutation($sysId: String!) {
  GlideRecord_Mutation {
    delete_sys_pd_process_definition(sys_id: $sysId) {
      modified_count
    }
  }
}`;
