import {getBasePath, getLocation} from './sn-pd-router.util';
import {getSelectedContentFromUrl} from './sn-pd-router.urlParser';
import {URL_CONTENT_LOADED} from '../constants/constants';

const {origin} = getLocation();
const _baseUrl = origin+getBasePath();

const getRootNode = () => document.body.children[0];

export const resolveHref = href => new URL(href, document.location).href;

export const shouldNavigateWithInApp = (href, target='_self') => {
	if(target!='' && target!='_self')
		return false;
	if(href && href.indexOf(_baseUrl) == 0 )
		return true;
	return false;
};

const isTagInPath = (path, tagName) => {
	if(!path)
		return false;
	for(let i = 0; i < path.length; i++){
		if(path[i].nodeName == tagName)
			return path[i];
	}
	return false;
};

let anchorLinkInterceptor;

export const attachAnchorLinkInterceptor = dispatch => {
	
	anchorLinkInterceptor = e => {
		const path = e.composedPath ? e.composedPath() : e.path;
		const target = isTagInPath(path, 'A');
		if(!target)
			return;
		const resolvedHref = resolveHref(target.href); 
		if (target && shouldNavigateWithInApp(resolvedHref)) {
			e.stopPropagation();
			e.preventDefault();
			const selectedContent = getSelectedContentFromUrl(null, resolvedHref.substring(origin.length));
			if(selectedContent)
				dispatch(URL_CONTENT_LOADED, selectedContent);
		}
	};
	
	
	const _attachAnchorLinkInterceptor = () => {
		const rootNode = getRootNode();
		if(!rootNode)
			return;
		rootNode.addEventListener('click', anchorLinkInterceptor, false);
	};
	
	document ? _attachAnchorLinkInterceptor() : window.addEventListener('load', _attachAnchorLinkInterceptor);

};

export const detachAnchorLinkInterceptor = () => {
	const rootNode = getRootNode();
	if(!rootNode || !anchorLinkInterceptor)
		return;
	rootNode.removeEventListener('click', anchorLinkInterceptor, false);
};
