import {get, capitalize, find} from 'lodash';
import * as AppConst from '../../constants/shared';
import {getConfigurationStateFromProperties, getConditionFieldsEnabled, getDisplayPrefsEnabled} from '../../util/selectors';
import {getTriggerInstanceRecordUrl} from '../../util/iframe-utils';
import * as Consts from './config-constants';
import {isArrayWithValues} from '../../util/general-utils';
import {t} from 'sn-translate';

export function getAdvancedConfigLabel(type) {
	return t('{0} {1}', capitalize('Configure'), type);
}

export function getConfigureModalHeader(type) {
	switch (type) {
	case AppConst.PLAYBOOK: return AppConst.CONFIGURE_YOUR_PROCESS;
	case AppConst.LANE: return AppConst.CONFIGURE_YOUR_LANE;
	case AppConst.ACTIVITY: return AppConst.CONFIGURE_YOUR_ACTIVITY;
	default: return '';
	}
}

export function getEditLink(properties) {
	const configurationState = getConfigurationStateFromProperties(properties);
	const type = get(configurationState, 'type', AppConst.PLAYBOOK);
	const sysId = get(configurationState, 'config.sys_id', '');
	const forceDefaultViewParams = '&sysparm_view=&sysparm_view_forced=true';
	const triggerInstanceSysId = get(properties, 'triggerInstanceSysId', '');

	if (!sysId && type !== AppConst.PLAYBOOK)
		return null;

	switch (type) {
	case AppConst.LANE:
		return `${AppConst.PLAYBOOK_LANE_TABLE_URL}${sysId}${forceDefaultViewParams}`;
	case AppConst.ACTIVITY:
		return `${AppConst.PLAYBOOK_ACTIVITY_TABLE_URL}${sysId}${forceDefaultViewParams}`;
	default:
		return getTriggerInstanceRecordUrl(triggerInstanceSysId);
	}
}

export function prepConfigForSaving(config) {
	const fields = get(config, 'fields', []);

	return fields.reduce((config, field) => {
		config[field.name] = field.value;
		return config;
	}, {});
}

export function getConfigurationData(type, data, properties) {
	if (type === AppConst.PLAYBOOK)
		return getBasicConfig(type, properties.playbook);
	else
		return getConfigForLaneOrActivity(type, data, properties);
}

export function getBaseFields(type, data) {
	return [
		getLabelField(type, data),
		getDescriptionField(type, data)
	];
}

export function getFieldsForLaneOrActivity(type, data, properties) {
	return [
		...getBaseFields(type, data),
		getConditionToRunField(data),
		getStartRuleField(type, data, properties)
	];
}

export function getBasicConfig(type, data) {
	return { fields: [ ...getBaseFields(type, data) ] };
}

export function getConfigForLaneOrActivity(type, data, properties) {
	const baseConfig = {
		sys_id: get(data, 'sys_id', ''),
		fields: [ ...getFieldsForLaneOrActivity(type, data, properties) ]
	};

	if (type === AppConst.ACTIVITY)
		return {
			...baseConfig,
			inputs: get(data, 'inputs', [])
		};

	return baseConfig;
}

export function getLabelField(type, data) {
	return {
		displayValue: get(data, 'label', ''),
		label: AppConst.LABEL,
		name: 'label',
		internalType: 'string',
		value: get(data, 'label', ''),
		mandatory: true,
		autofocus: true,
		placeholder: AppConst.CONFIG_PANEL_INPUT_PLACEHOLDER[type],
	};
}

export function getDescriptionField(type, data) {
	return {
		displayValue: get(data, 'description', ''),
		label: AppConst.DESCRIPTION,
		name: 'description',
		internalType: 'textarea',
		value: get(data, 'description', ''),
	};
}

export function getStartRuleField(type, data, properties) {
	return {
		displayValue: get(data, 'start_rule.displayValue', ''),
		label: AppConst.START_RULE,
		name: 'start_rule',
		internalType: 'choice',
		mandatory: true,
		value: get(data, 'start_rule.value', ''),
		choices: getStartRules(type, properties),
	};
}

export function getStartRules(type, properties) {
	if (type === AppConst.LANE)
		return properties.laneStartRules;

	if (type === AppConst.ACTIVITY)
		return properties.activityStartRules;
}

export function getStartRuleIdByName(startRules, startRuleName) {
	const startRule = find(startRules, ['name', startRuleName]);
	return startRule ? startRule.value : startRuleName;
}

export function getStartRuleNameById(startRules, startRuleId) {
	const startRule = find(startRules, ['value', startRuleId]);
	return startRule ? startRule.name : startRuleId;
}

export function createStartRuleByName(startRules, startRuleName) {
	var displayValue = AppConst.IMMEDIATE_DISPLAY_VALUE;
	if (startRuleName === AppConst.AFTER_PREVIOUS_START_RULE)
		displayValue = AppConst.AFTER_PREVIOUS_DISPLAY_VALUE;
	if (startRuleName === AppConst.WITH_PREVIOUS_START_RULE)
		displayValue = AppConst.WITH_PREVIOUS_DISPLAY_VALUE;

	return {
		value: getStartRuleIdByName(startRules, startRuleName),
		displayValue
	};
}

export function getConditionToRunField(data) {
	return {
		label: AppConst.CONDITION_TO_RUN,
		name: 'condition_to_run',
		internalType: 'conditions',
		value: get(data, 'condition_to_run', ''),
		sysId: get(data, 'sys_id', ''),
	};
}

export function filterInputsByDisplayPreference(inputs, view, inputDisplayPrefs) {
	// advanced view displays everything
	// if no preferences are set, everything should default to the standard view.
	// standard view will show everything. config panel will show nothing

	const isAdvancedView = view === Consts.ADVANCED_MODAL_VIEW;
	const isStandardViewWithNoPrefs = view === Consts.STANDARD_MODAL_VIEW && !inputDisplayPrefs;
	const isPanelViewWithNoPrefs = view === Consts.CONFIG_PANEL_VIEW && !inputDisplayPrefs;

	if (isAdvancedView || isStandardViewWithNoPrefs)
		return inputs;

	if (isPanelViewWithNoPrefs || !isArrayWithValues(inputs) || !view)
		return [];

	const preferenceArray = inputDisplayPrefs && inputDisplayPrefs.split(';') || [];
	let preferenceObject = {};
	preferenceArray.forEach(function(pref) {
		const prefKeyValue = pref.split('=');
		preferenceObject[prefKeyValue[0]] = prefKeyValue[1];
	});

	let filteredInputs = [];
	inputs.forEach(input => {
		const hasMatchingView = preferenceObject[input.name] === view;
		const isStandardViewAndShouldIncludePanelInputs = view === Consts.STANDARD_MODAL_VIEW &&
			preferenceObject[input.name] === Consts.CONFIG_PANEL_VIEW;

		if (hasMatchingView || isStandardViewAndShouldIncludePanelInputs)
			filteredInputs.push(input);
	});

	return filteredInputs;
}

export function filterFieldsBySysProps(fields, userData) {
	const conditionFieldsEnabled = getConditionFieldsEnabled(userData);
	return fields.filter(field => field.name !== 'condition_to_run' || conditionFieldsEnabled);
}

export function getFormFields(configurationState, selectedView, userData) {
	const fields = filterFieldsBySysProps(get(configurationState, 'config.fields', []), userData);
	const displayPrefsEnabled = getDisplayPrefsEnabled(userData);

	if (selectedView === Consts.CONFIG_PANEL_VIEW && !displayPrefsEnabled)
		return {fields, inputs: []};

	const unfilteredInputs = get(configurationState, 'config.inputs', []);
	const inputDisplayPrefs = get(configurationState, 'inputDisplayPrefs');

	const inputs = filterInputsByDisplayPreference(unfilteredInputs, selectedView, inputDisplayPrefs);

	return {fields, inputs};
}
