import {SELECTED_CONTENT_INIT_PROP, SELECTED_CONTENT} from '../constants/constants';
import {getSelectableRootProps} from '../utils/selectable-prop-utils';

export const selectedContentInitProp = {
	name: SELECTED_CONTENT_INIT_PROP,
	properties: {
		selectedContent: {
			get default() {
				return getSelectableRootProps(SELECTED_CONTENT);
			}
		}
	}
};