import {t} from 'sn-translate';
import * as Shared from '../constants/shared';
import get from 'lodash/get';
import {isArrayWithValues, isObjectWithValues} from './general-utils';

/**
 * The main purpose of this file is to map errorTypes from the server
 * to custom errors for display in the alert-list, card, and config panel components.
 *
 * message: displayable message used by the alert component
 * textLinkProps: provides link properties to the alert-list component
 * textualMessage.message: displayable message used by the card and config panel components
 * textualMessage.type: used as a css class in the card component
 * textualMessage.id: used to map messages to cards
 */

//Error types
export const ACTIVITY_MISSING_REQUIRED_INPUT = 'ACTIVITY_MISSING_REQUIRED_INPUT';
export const ACTIVITY_HAS_INVALID_ACTIVITY_DEFINITION = 'ACTIVITY_HAS_INVALID_ACTIVITY_DEFINITION';
export const ACTIVITY_DEFINITION_DOESNT_MATCH_SCOPE = 'ACTIVITY_DEFINITION_DOESNT_MATCH_SCOPE';
export const ACTIVITY_HAS_INVALID_AUTOMATION_PLAN = 'ACTIVITY_HAS_INVALID_AUTOMATION_PLAN';
export const AUTOMATION_PLAN_DOESNT_MATCH_SCOPE = 'AUTOMATION_PLAN_DOESNT_MATCH_SCOPE';
export const ACTIVITY_MISSING_REQUIRED_FIELD = 'MISSING_REQUIRED_FIELD';
export const ACTIVITY_INPUT_HAS_INVALID_REFERENCE = 'ACTIVITY_INPUT_HAS_INVALID_REFERENCE';
export const TRIGGER_INSTANCE_NOT_FOUND = 'TRIGGER_INSTANCE_NOT_FOUND';
export const TRIGGER_TABLE_NOT_SPECIFIED = 'TRIGGER_TABLE_NOT_SPECIFIED';
export const TRIGGER_TABLE_NOT_LICENSED = 'TRIGGER_TABLE_NOT_LICENSED';
export const SYSTEM_ERROR = 'SYSTEM_ERROR';
export const UNKNOWN_ERROR = 'UNKNOWN_ERROR';

// Error messages
export const GENERIC_GRAPHQL_ERROR = t('A server error occurred. See the server logs or contact your System ' +
	'Administrator if this problem continues.');
export const GENERIC_NETWORK_ERROR = t('A network error occurred. Please refresh the page or contact your System ' +
	'Administrator if this problem continues.');
export const DECLARATIVE_ACTION_ERROR = t(`We couldn't delete your selected process. To delete this process, you'll 
need access to the application scope where the process is defined.`);

// Activity is missing a required input
export const ACTIVITY_MISSING_REQUIRED_INPUT_CARD_MESSAGE = t('Activity inputs are missing');
export const ACTIVITY_MISSING_REQUIRED_INPUT_ALERT = t('One or more of your activities wasn\'t configured correctly. ' +
	'Please select "Configure activity" on the activity properties panel to add missing activity inputs.');

export const activityMissingRequiredInput = (message) => {
	const id = get(message, 'reference.sysId', '');
	return {
		message: ACTIVITY_MISSING_REQUIRED_INPUT_ALERT,
		textLinkProps: Shared.CREATE_ACTIVITY_DEFINITION_TEXT_LINK_PROPS,
		textualMessage: {
			message: ACTIVITY_MISSING_REQUIRED_INPUT_CARD_MESSAGE,
			type: Shared.ERROR,
			id
		},
	};
};

