import {createCustomElement} from '@servicenow/ui-core';
import {view} from './config-modal-view';
import styles from './config-modal.scss';
import * as Consts from '../config-constants';
import {getFormFields} from '../config-utils';
import {get} from 'lodash';
import * as AppConst from '../../../constants/shared';
import {getSelectableProp} from '../../../util/general-utils';
import {isFieldValid} from '../../dynamic-form/dynamic-form-helpers';
import {SN_CANVAS_SESSION} from '../../canvas/constants/constants';

createCustomElement('sn-pd-config-modal', {
	view,
	initialState: {
		selectedView: Consts.STANDARD_MODAL_VIEW,
		selectedTab: Consts.GENERAL_TAB
	},
	properties: {
		configurationState: {},
		dotWalkData: {},
		formFields: {
			computed({selectedView, properties: {configurationState, userData}}) {
				return getFormFields(configurationState, selectedView, userData);
			}
		},
		playbook: {
			get default() {
				return getSelectableProp(AppConst.PROVIDER, AppConst.PLAYBOOK_PROPERTY_NAME);
			}
		},
		invalidFields: {
			computed({properties: {formFields}}) {
				const {fields, inputs} = formFields;
				const invalid = (field) => !isFieldValid(field);
				return fields.some(invalid) && inputs.some(invalid);
			}
		},
		userData: {
			get default() {return getSelectableProp(SN_CANVAS_SESSION, AppConst.SESSION_USER);}
		},
	},
	actionHandlers: {
		[Consts.MODAL_TAB_SELECTED]: ({action: {payload}, updateState}) => {
			updateState({selectedTab: get(payload, 'value', Consts.GENERAL_TAB)});
		},
	},
	styles: [styles]
});
