import {REQUEST_FIELD_META} from '../../constants/action-types';
import {get} from 'lodash';
import {addRow, decodeTemplateValue, encodeTemplateValue, removeRow} from './template-value-util';

/**
 * On bootstrap request field meta information for the table passed in as props
 *
 * @param action
 * @param properties
 * @param dispatch
 */
export function handleComponentBootstrapped({properties, dispatch}) {
	const table = get(properties, 'table', null);

	if (table)
		dispatch(REQUEST_FIELD_META, {table});
}

/**
 * After receiving fields successfully from the database, decode the initial value if there is one and set all state.
 *
 * @param encodedValue
 * @param payload
 * @param updateState
 */
export function handleRequestFieldMetaSucceeded({properties: {encodedValue}, action: {payload}, updateState}) {
	const fieldMeta = get(payload, 'result.columns', []);
	let decodedValue = encodedValue ? decodeTemplateValue(encodedValue, fieldMeta) : [];

	if (decodedValue.length === 0)
		decodedValue = addRow(decodedValue);

	updateState({fieldMeta, decodedValue, isLoading: false});
}

/**
 * Handles a selection of a key field and constructs the field selection object which is later passed
 * to a pill container.
 *
 * @param payload
 * @param updateState
 */
export function handleRowKeySelected({state: {decodedValue, fieldMeta}, action: {payload: {idx, value, label}}, updateState, updateProperties}) {
	const newDecodedValue = [...decodedValue];
	newDecodedValue[idx] = {...newDecodedValue[idx], name: value, label, ...fieldMeta[value]};

	updateState({decodedValue: newDecodedValue});
	updateProperties({encodedValue: encodeTemplateValue(newDecodedValue)});
}

/**
 * Whenever a row has a value selected, (right side field), update the decodedValue data structure with the new value
 * @param decodedValue
 * @param idx
 * @param value
 * @param updateState
 */
export function handleRowValueSelected({state: {decodedValue}, action: {payload: {idx, value, displayValue}}, updateState, updateProperties}) {
	const newDecodedValue = [...decodedValue];
	newDecodedValue[idx] = {...newDecodedValue[idx], value, displayValue};

	updateState({decodedValue: newDecodedValue});
	updateProperties({encodedValue: encodeTemplateValue(newDecodedValue)});
}

// Add a new empty row to render
export function handleAddRow({state: {decodedValue}, updateState}) {
	updateState({decodedValue: addRow(decodedValue)});
}

// Remove the chosen row from the list of rows
export function handleRemoveRow({state: {decodedValue}, updateState, updateProperties, action: {payload}}) {
	const {idx} = payload;
	const rows = removeRow(decodedValue, idx);

	updateState({decodedValue: rows});
	updateProperties({encodedValue: encodeTemplateValue(rows)});
}

// When the encoded value changes, update the configuration panel to accept the value change.
export function handlePropertyChanged({properties: {onChangeActionType, field}, action: {payload}, dispatch, updateState}) {
	if (hasEncodedValueChanged(payload) && onChangeActionType) {
		const {value, isGlideVar} = payload;

		dispatch(onChangeActionType, {...field, value, displayValue: value, isGlideVar});
	}

	// in the config panel, the table is passed in after the component has loaded.
	if (hasTableChanged(payload)) {
		dispatch(REQUEST_FIELD_META, {table: payload.value});
		updateState({isLoading: true});
	}

}

function hasTableChanged({name, value, previousValue}) {
	return name === 'table' && value !== previousValue;
}

function hasEncodedValueChanged({name, value, previousValue}) {
	return name === 'encodedValue' && value !== previousValue;
}

