import {get} from 'lodash';
import '@servicenow/now-alert';
import * as SharedConsts from '../../../constants/shared';
import * as NewProcessConsts from './creation-constants';
import {getKeyValuePairs} from '../../../util/transforms';
import {findCurrentScopeDisplayValue} from './util';

export const createProcessDetailsForm = (state) => {
	const {newPlaybookForm, filteredUserScopes, currentScope} = state;
	const {label, description} = getKeyValuePairs(newPlaybookForm);
	const defaultApplicationDisplayValue = findCurrentScopeDisplayValue(filteredUserScopes, currentScope);
	const applicationDisplayValue = get(newPlaybookForm, 'application.displayValue', defaultApplicationDisplayValue);

	return (
		<span>
			<div className='new-pd-form-input'>
				<sn-record-input
					type='text'
					name='label'
					label={SharedConsts.LABEL}
					placeholder={NewProcessConsts.PLACEHOLDER_NAME}
					required='true'
					value={label}
					autofocus='true'
					aria-label={NewProcessConsts.LABEL_ARIA_LABEL}
				/>
			</div>

			<div className='new-pd-form-input'>
				<sn-record-input
					type='textarea'
					name='description'
					label={SharedConsts.DESCRIPTION}
					value={description}
					aria-label={NewProcessConsts.DESCRIPTION_ARIA_LABEL}
				/>
			</div>

			<div className='new-pd-form-input'>
				<now-record-typeahead
					options={filteredUserScopes}
					name='application'
					required={true}
					label={SharedConsts.APPLICATION}
					value={applicationDisplayValue}
					placeholder={NewProcessConsts.PLACEHOLDER_APPLICATIONS}
					aria-label={NewProcessConsts.APPLICATION_ARIA_LABEL}
					search='contains'
				/>
			</div>
		</span>
	);
};
