import '../lane-header';
import '@servicenow/now-button';
import {ADD_LANE, EMPTY_LANE_VARIANT} from '../../../constants/shared';
import {isClickOrEnter} from '../../../util/general-utils';
import {CREATE_LANE_CLICKED} from '../../../constants/action-types';

const view = (state, {dispatch}) => {
	const data = {label: ''};

	const onHeaderClick = (event) => {
		if (isClickOrEnter(event))
			dispatch(CREATE_LANE_CLICKED);
	};

	return (
		<sn-pd-lane-header
			data={data}
			nameOnly
			hideOptions
			variant={EMPTY_LANE_VARIANT}
			aria-description={ADD_LANE}
			on-click={onHeaderClick}
		>
			<div slot='before-name-icon'>
				<now-button
					label={ADD_LANE}
					icon='plus-fill'
					size='md'
					tooltip-content={ADD_LANE}
					variant='primary'
				/>
			</div>
		</sn-pd-lane-header>
	);
};

export {view};
