import '@servicenow/now-heading';
import '@servicenow/now-button';
import '@servicenow/now-dropdown';
import * as HeaderConsts from './lane-header-constants';
import get from 'lodash/get';
import {isClickOrEnter} from '../../../util/general-utils';
import {isDropdownInPath} from '../../../util/accessibility-utils';
import {LANE, NOW_DROPDOWN, UNDEFINED_LANE_LABEL, EMPTY_LANE_VARIANT} from '../../../constants/shared';
import {POPULATE_CONFIGURATION_PANEL, UPDATE_CONFIG_TRIGGER_ELEMENT_FOR_NEW_LANE} from '../../../constants/action-types';
import {createRef} from '@servicenow/ui-renderer-snabbdom';

const laneHeaderView = (state, {dispatch}) => {
	const {
		data,
		variant,
		nameOnly,
		hideOptions
	} = state.properties;

	const laneHeaderElement = createRef();

	const isFirstLane = get(data, 'isFirstLane', false);
	let laneLabel = get(data, 'label' );
	laneLabel = variant !== EMPTY_LANE_VARIANT && !laneLabel ? UNDEFINED_LANE_LABEL : laneLabel;
	const laneDesc = get(data, 'description');
	const laneOrder = get(data, 'order');
	const ariaDesc = `${laneLabel} ${laneDesc}`;
	const isIndependent = get(data, 'isIndependent');
	const titleOnlyClass = nameOnly ? 'name-only' : '';
	const variantClass = variant.includes('outline') ? variant.replace('-', ' ') : variant;
	const isDependedOn = get(data, 'isDependedOn');
	const isDependedOnClass = isDependedOn ? ' depended-on' : ' not-depended-on';
	// first lane should always have the 'independent' class
	const isDependentClass = isIndependent || isFirstLane ? ' independent' : ' dependent';
	const isLastLaneClass = get(data, 'isLastLane') ? ' last' : '';
	let variantOverrideClass = isIndependent && !isDependedOn  ? 'box ' : variantClass;
	variantOverrideClass = variant === HeaderConsts.EMPTY_LANE ? variant : variantOverrideClass;

	const renderSubtext = () => {
		if (!nameOnly)
			return (
				<div className='description-sm' title={laneDesc} aria-label={laneDesc}>
					{laneDesc}
				</div>
			);
	};

	const renderOptions = () => {
		const editActionPayload = {type: HeaderConsts.LANE_ACTION_EDIT, payload: data};
		const deleteActionPayload = {type: HeaderConsts.LANE_ACTION_DELETE, payload: data};
		const insertLaneBefore = {
			type: HeaderConsts.LANE_ACTION_INSERT,
			payload: {laneOrder, insertLocation: HeaderConsts.INSERT_BEFORE}
		};
		const insertLaneAfter = {
			type: HeaderConsts.LANE_ACTION_INSERT,
			payload: {laneOrder, insertLocation: HeaderConsts.INSERT_AFTER}
		};

		const items = [
			{'id': 1, 'label': HeaderConsts.EDIT_LANE_CONFIGURATION, 'action': editActionPayload},
			{'id': 2, 'label': HeaderConsts.INSERT_LANE_BEFORE, 'action': insertLaneBefore},
			{'id': 3, 'label': HeaderConsts.INSERT_LANE_AFTER, 'action': insertLaneAfter},
			{'id': 4, 'label': HeaderConsts.DELETE_LANE, 'action': deleteActionPayload}
		];

		if (!hideOptions) {
			return (
				<div className='dropdown-container'>
					<now-dropdown
						tooltip-content={HeaderConsts.LANE_ACTIONS}
						bare
						hide-caret
						hide-label
						icon='ellipsis-v-fill'
						items={items}
						select='none'
						size='sm'
						variant='secondary'
					/>
				</div>
			);
		}
	};

	const showConfigOnClickOrEnter = (event) => {
		if (isClickOrEnter(event) && !isDropdownInPath(event, NOW_DROPDOWN))
			dispatch (POPULATE_CONFIGURATION_PANEL, {configType: LANE, data, triggerElement: laneHeaderElement.current});
	};

	const checkIfLaneIsNew = (node) => {
		if (data.isNew)
			dispatch(UPDATE_CONFIG_TRIGGER_ELEMENT_FOR_NEW_LANE, {triggerElement: node.elm});
	};

	const otherClasses = variantOverrideClass + isDependedOnClass + isDependentClass + isLastLaneClass;
	// prevent tab to lane header background if rendering add new lane button
	const headerBackgroundTabindex = otherClasses.includes(HeaderConsts.EMPTY_LANE) ? '-1' : '0';

	return (
		<div
			ref={laneHeaderElement}
			hook-insert={vnode => checkIfLaneIsNew(vnode)}
			className='lane-header-background'
			tabindex={headerBackgroundTabindex}
			aria-description={ariaDesc}
			on-click={showConfigOnClickOrEnter}
			on-keydown={showConfigOnClickOrEnter}
		>
			<div className={'lane-header ' +  otherClasses}>
				<div className={'vtb-header-' + variantOverrideClass}>
					<div className='hover-container'>
						<div className={'lane-header-text ' + titleOnlyClass}>
							<div className='before-name-icon'>
								<slot name='before-name-icon' />
							</div>

							<div className='heading-cont' title={laneLabel} aria-label={laneLabel}>
								{laneLabel.toUpperCase()}
							</div>

							{renderSubtext()}
						</div>

						{renderOptions()}
					</div>
				</div>
			</div>
		</div>
	);
};

export {laneHeaderView};
