import {getBoardData, transformPlaybookData, getConfigurationDefaults, transformCard} from '../util/transforms';
import {createFlowDesignerUrl, openInNewWindow} from '../util/url-utils';
import * as ActionTypes from '../constants/action-types';
import {get, isEmpty, isString} from 'lodash';
import {isObjectWithValues, responseHasErrorsOrNoData, isArrayWithValues} from '../util/general-utils';
import {getCardOrderFromAdjacentLane} from './rearrange-actions';
import * as GraphQLHelpers from '../datasource/graphql/helpers.graphql';
import * as Shared from '../constants/shared';
import {getActivityPillDependencies} from '../util/pill-utils';
import * as Selectors from '../util/selectors';
import {getPlaybookDependencyRelationships} from '../util/dependencies';
import {GlideFormModal} from '../constants/modal-constants';
import * as GraphQL from '../datasource/graphql';
import {getStartRules, getStartRuleIdByName} from '../components/configuration-components/config-utils';

export const handlePlaybookUrlChange = ({properties, dispatch, action, updateProperties}) => {
	const language = get(properties, 'userData.user.language', 'en');
	const sysId = get(action, 'payload.params.sysId');

	if (sysId) {
		updateProperties({loadingPAD: true});
		dispatch(ActionTypes.FETCH_PLAYBOOK_REQUESTED, {sysId, language});
		dispatch(ActionTypes.SUBSCRIBE_PLAYBOOK, {sysId, subscribe: true});
	} else
		updateProperties({playbook: {}});
};

//Fill in the sysId of the current playbook if not provided in the action payload
export const handleFetchPlaybook = ({state, action, dispatch, updateProperties}) => {
	let payload = get(action, 'payload.data', {});
	payload.language = get(payload, 'language', 'en');

	if (!payload.sysId)
		payload.sysId = get(state, 'properties.playbook.sys_id', null);

	updateProperties({loadingPAD: true});
	dispatch(ActionTypes.FETCH_PLAYBOOK_REQUESTED, payload);
};

const removeStartWithCondition = (startRules) => startRules.filter(startRule => startRule.name !== Shared.WITH_CONDITION_START_RULE);

export const handleFetchPlaybookSucceeded = ({action: {payload}, updateProperties, dispatch}) => {
	if (responseHasErrorsOrNoData(payload, dispatch)) {
		updateProperties({loadingPAD: false});
		return;
	}

	const results = GraphQLHelpers.removeGlideGraphQLBoilerplate(Selectors.getDataFromPayload(payload));
	// was an array transformed into object
	const playbooksObject = results && results.playbook ? GraphQLHelpers.flattenValues(results.playbook) : {};
	const laneStartRules = GraphQLHelpers.flattenCollection(get(results, 'laneStartRules'));
	const activityStartRules = GraphQLHelpers.flattenCollection(get(results, 'activityStartRules'));
	
	if (playbooksObject && !isObjectWithValues(playbooksObject[0])) {
		updateProperties({loadingPAD: false});
		return dispatch(ActionTypes.SHOW_ALERTS, [{message: Shared.NO_PLAYBOOKS_FOUND}]);
	}
	
	const {playbook} = transformPlaybookData(playbooksObject[0]);
	const playbookSysId = get(playbook, 'sys_id', '');
	const showStartProcessElements = isEmpty(get(playbook, 'lanes', {}));
	const triggerInstanceSysId = get(playbook, 'trigger_instance.sys_pd_trigger_instance[0].sys_id');
	// object of activity sys_ids that are referenced in pills will a value of an array
	// of the activities referencing ithandleFetchPlaybook
	const activitiesDependedOn = getActivityPillDependencies(playbook.cards);
	
	const hideStartWithCondition = !Selectors.getConditionFieldsEnabled(); // Overloading this feature flag to hide UI for start-with-condition start rule
	updateProperties({
		playbook,
		laneStartRules: hideStartWithCondition ? removeStartWithCondition(laneStartRules) : laneStartRules,
		activityStartRules: hideStartWithCondition ? removeStartWithCondition(activityStartRules) : activityStartRules,
		activitiesDependedOn,
		showStartProcessElements,
		triggerInstanceSysId,
		loadingPAD: false
	});

	// Run validators on successful load of a playbook
	if (playbookSysId)
		dispatch(ActionTypes.VALIDATE_PLAYBOOK_REQUESTED, {sysId: playbookSysId});
};

export const handleSavePlaybookConfigSucceeded = ({action: {payload}, updateProperties, state, dispatch}) => {
	if (responseHasErrorsOrNoData(payload, dispatch))
		return;

	const data = Selectors.getDataFromPayload(payload);
	const playbookData = get(data, 'GlideRecord_Mutation.update_sys_pd_process_definition', '');
	const playbookActivatedData = get(data, 'GlideProcessAutomation_Mutation.activate.processDefinition', '');
	const playbookDeactivatedData = get(data, 'GlideProcessAutomation_Mutation.deactivate', '');
	const updatedPlaybook = playbookData || playbookActivatedData || playbookDeactivatedData;
	const transformedPlaybookData = GraphQLHelpers.flattenValues(updatedPlaybook);
	const playbook = get(state, 'properties.playbook', {});

	updateProperties({
		configurationState: getConfigurationDefaults(),
		playbook: {...playbook, ...transformedPlaybookData},
		textualMessages: []
	});
};

export const handleActivatePlaybookResponse = (input) => {
	const {action: {payload}, dispatch} = input;
	const errors = get(payload, 'data.GlideProcessAutomation_Mutation.activate.errors', '');

	if (isArrayWithValues(errors))
		dispatch(ActionTypes.PLAYBOOK_VALIDATION_ERROR_OCCURRED, {errors});

	handleSavePlaybookConfigSucceeded(input);
};

export const handleSavePlaybookConfigRequested = ({action, dispatch, updateProperties, properties}) => {
	const payload = get(action, 'payload', {});
	const sysId = get(properties, 'playbook.sys_id');
	const data = GraphQL.playbookMutation({...payload, sysId});
	const scope = Selectors.getScopeFromProperties(properties);
	dispatch(ActionTypes.SAVE_PLAYBOOK_CONFIGURATION, {data, sysparm_transaction_scope: scope});
	updateProperties({configurationState: getConfigurationDefaults()});
};

export const openCreateActivityDefModal = ({dispatch}) => {
	dispatch(ActionTypes.HIDE_ACTIVITY_PICKER);

	const onSubmit = () => {
		dispatch(ActionTypes.CLOSE_MODAL);
		dispatch(ActionTypes.PICKER_WAS_DISMISSED);
	};

	dispatch(
		ActionTypes.OPEN_CONFIRM_MODAL_REQUESTED,
		{...GlideFormModal(Shared.CREATE_ACTIVITY_DEF_HEADER, Shared.PLAYBOOK_ACTIVITY_DEF_TABLE_URL, {iframe: {renderFormHeaderBar: false, onSubmit}})}
	);
};

export const handlePickerItemSelected = ({state, action:{payload}, dispatch, properties}) => {
	if (isObjectWithValues(state.activityPicker)) {
		const laneId = get(state, 'activityPicker.laneId');
		const activityId = get(payload, 'sysId');
		const label = get(payload, 'label');
		const lanes = Selectors.getLanesFromProperties(properties);
		const lane = lanes && lanes[laneId];
		const lastCardId = lane && lane.cards && lane.cards[lane.cards.length - 1];
		const cards = Selectors.getCardsFromProperties(properties);
		const lastCard = cards && cards[lastCardId];
		const playbookId = get(properties, 'playbook.sys_id');

		const activityStartRules = getStartRules(Shared.ACTIVITY, properties);
		const start_rule = !lastCard ? Shared.IMMEDIATE_START_RULE : Shared.DEFAULT_START_RULE;

		dispatch(ActionTypes.POST_NEW_LANE_CARD, {
			lane: laneId,
			activity_definition: activityId,
			playbook: playbookId,
			label: label,
			start_rule: getStartRuleIdByName(activityStartRules, start_rule),
			order: lastCard ? lastCard.order + 1 : getCardOrderFromAdjacentLane(lanes, laneId, cards)
		});

		dispatch(ActionTypes.DISMISS_PICKER);
	}
};

export const updatePickerData = ({action:{payload}, updateState}) => {
	const pickerType = get(payload, 'variant');
	const toFocus = get(payload, 'returnFocusElement');
	const {laneId, show} = payload;

	if (pickerType === Shared.ACTIVITY)
		updateState({activityPicker: {laneId, toFocus, show}});
	else
		updateState({actionPicker: {laneId, toFocus, show}});
};

export const returnFocusToLaneFooterButton = (state) => {
	const activityPickerData = get(state, 'activityPicker');
	const toFocus = activityPickerData ? get(activityPickerData, 'toFocus') : get(state, 'actionPicker.toFocus');

	if (toFocus)
		toFocus.focus();
};

export const hideActivityPicker = ({state, updateState}) => {
	const activityPickerData = get(state, 'activityPicker', {});
	updateState({
		activityPicker: {...activityPickerData, show: false}
	});
};

export const clearPickerData = ({updateState}) => {
	updateState({
		activityPicker: '',
		actionPicker: ''
	});
};

export const setFocusAndClearPickerData = ({state, dispatch}) => {
	const activityPickerData = get(state, 'activityPicker');
	if (!isObjectWithValues(activityPickerData))
		return;

	returnFocusToLaneFooterButton(state);
	dispatch(ActionTypes.DISMISS_PICKER);
};

export const checkShouldFocusCard = ({state, action, updateState}) => {
	const cardHost = get(action, 'payload.cardHost', '');
	const cardId = get(action, 'payload.cardId', '');
	const focusCardId = get(state, 'focusCardId', '');

	if (!(cardId && cardHost && focusCardId))
		return;

	if (cardId === focusCardId) {
		const card = cardHost.shadowRoot.querySelector('now-card');

		if (card)
			card.focus();

		updateState({
			focusCardId: ''
		});
	}
};

export const addNewCardToPlaybook = ({action: {payload}, state, updateProperties, dispatch}) => {
	if (responseHasErrorsOrNoData(payload, dispatch))
		return;

	const card = get(payload, 'data.GlideRecord_Mutation.insert_sys_pd_activity');

	if (isObjectWithValues(card)) {
		let transformedCard = GraphQLHelpers.removeGlideGraphQLBoilerplate(card);
		transformedCard = GraphQLHelpers.flattenValues(transformedCard);
		transformedCard = {...transformedCard, lane_id: transformedCard.lane};

		//Call transformCard to set the activity inputs to what the app expects
		transformedCard = transformCard(transformedCard);

		const cardSysId = transformedCard.sys_id;
		const playbook = get(state, 'properties.playbook', {});
		let stateCards = {...get(playbook, 'cards', {})};
		stateCards[cardSysId] = transformedCard;

		let stateLanes = {...get(playbook, 'lanes', {})};
		let updatedLane = {...stateLanes[transformedCard.lane]};
		updatedLane.cards = updatedLane.cards || [];
		updatedLane.cards.push(cardSysId);
		stateLanes[transformedCard.lane] = updatedLane;

		updateProperties({
			playbook: {...playbook, cards: stateCards, lanes: stateLanes},
			focusCardId: cardSysId
		});

		dispatch(ActionTypes.POPULATE_CONFIGURATION_PANEL, {configType: Shared.ACTIVITY, data: transformedCard});

		dispatch(ActionTypes.REORDER_CARDS_DELETE_INVALID_PILLS, {
			type: Shared.INSERT,
			movedCardId: cardSysId,
			movedCardOrder: transformedCard.order,
			cards: stateCards
		});
	}
};

export const openFlowDesigner = ({action : {payload : {data : card}}}) => {
	const activityDefinition = card.activity_definition;
	openInNewWindow(createFlowDesignerUrl(activityDefinition));
};

export function getFirstLaneOfBrandNewPlaybook(playbook) {
	const lanes = get(playbook, 'lanes', {});
	const laneKeys = Object.keys(lanes);
	if (laneKeys.length > 1)
		return false;

	const firstLane = lanes[laneKeys[0]];
	const isNewPlaybook = isObjectWithValues(playbook) && firstLane && firstLane.created === playbook.updated;

	return isNewPlaybook && firstLane;
}

export const updateDerivedData = (state, payload, updateProperties) => {
	const playbook = payload.value;
	if (isObjectWithValues(playbook)) {
		const laneStartRules = get(state, 'properties.laneStartRules', {});
		const activityStartRules = get(state, 'properties.activityStartRules', {});
		const dependencies = getPlaybookDependencyRelationships(playbook, laneStartRules, activityStartRules);
		const activitiesDependedOn = getActivityPillDependencies(playbook.cards);
		updateProperties({
			...state.properties,
			laneDependencies: get(dependencies, 'cardDepsByLane'),
			cardDependenciesByLane: get(dependencies, 'cardDepsByLane'),
			boardData: getBoardData(playbook, dependencies),
			activitiesDependedOn
		});
	}
};

export const checkIfPlaybookUpdated = ({state, action, updateProperties, dispatch}) => {
	const {payload} = action;

	if (!payload)
		return;

	if (payload.name === Shared.PLAYBOOK_PROPERTY_NAME) {
		updateDerivedData(state, payload, updateProperties);

		// If playbook has never been updated since creation, open config panel for the first lane.
		const playbook = get(state, 'properties.playbook', {});
		const lane = getFirstLaneOfBrandNewPlaybook(playbook);
		if (lane)
			dispatch(ActionTypes.POPULATE_CONFIGURATION_PANEL, {configType: Shared.LANE, data: lane});
	}
};

export const handleValidatePlaybookSucceeded = ({action: {payload}, dispatch}) => {
	const errors = get(payload, 'data.GlideProcessAutomation_Query.validate', []);
	if (isArrayWithValues(errors))
		dispatch(ActionTypes.PLAYBOOK_VALIDATION_ERROR_OCCURRED, {errors});
};

export const handleShowAllPlaybooksClicked = ({state, updateProperties, dispatch}) => {
	const sysId = get(state, 'properties.playbook.sys_id');
	if (sysId)
		dispatch(ActionTypes.SUBSCRIBE_PLAYBOOK, {sysId, subscribe: false});

	updateProperties({
		playbook: {},
		configurationState: {},
		laneDependencies: {},
		cardDependenciesByLane: {},
		activitiesDependedOn: {},
		boardData: {},
		modal: {},
		alerts: [],
		showStartProcessElements: false
	});
};

export function handleSubscribePlaybookUpdated({action: {payload}, state, updateProperties}) {
	const results = GraphQLHelpers.removeGlideGraphQLBoilerplate(payload);
	const playbooksObject = GraphQLHelpers.flattenValues(get(results, 'sys_pd_process_definition', {}));
	const updatedDisplayValue = get(playbooksObject, '[0].updatedDisplayValue');
	const updated = get(playbooksObject, '[0].updatedValue');
	const currentUpdated = get(state, 'properties.playbook.updatedDisplayValue');

	if (!isString(updatedDisplayValue) || updatedDisplayValue === currentUpdated)
		return;

	updateProperties({
		playbook: {
			...get(state, 'properties.playbook', {}),
			updatedDisplayValue,
			updated
		}
	});
}
