import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import {preUserData} from '../utils/behaviors/presource.behaviors';

import {getInitialSessionInfo, getClientInfo} from './util';

import {SESSION_DATA_INITIAL_LOAD,
	SESSION_STORAGE_DATA_INIT,
	SESSION_STORAGE_DATA_PUT,
	CANVAS_SESSION_TIME_LEFT,
	CANVAS_SESSION_TIME_OUT} from '../constants/constants';


createCustomElement('sn-pd-session', {
	renderer:{
		type: snabbdom
	},
	view(){
		return null;
	},
	initialState:{
		session:{
			info:{
				isLoggedIn: false
			}
		}
	},
	properties: {
		session: {
			selectable: true,
			default: {
				info: getInitialSessionInfo(),
				client: getClientInfo(),
				user: preUserData.initialState
			}
		}
	},
	behaviors: [
		preUserData
	],
	actionHandlers: {
		[SESSION_DATA_INITIAL_LOAD]: () => {},
		[CANVAS_SESSION_TIME_LEFT]: () => {},
		[CANVAS_SESSION_TIME_OUT]: () => {},
		[SESSION_STORAGE_DATA_INIT]: () => {},
		[SESSION_STORAGE_DATA_PUT]: () => {}
	}
});
