export const playbookLaneMutation = `
mutation($sysId: String!, $label: String, $description: String, $active: String, $order: String, $start_rule: String) {
  GlideRecord_Mutation {
    update_sys_pd_lane(sys_id: $sysId, label: $label, description: $description, active: $active,
    start_rule: $start_rule, order: $order) {
      sys_id { value }
      label { value }
      name { value }
      description { value }
      active { value }
      laneCondition { value }
      order { value }
      start_rule { value displayValue }
      updated: sys_updated_on { value }
    }
  }
}`;

export const playbookLaneCreation = `
mutation($label: String, $description: String, $active: String, $start_rule: String, $order: String, $playbook: String, $name: String) {
  GlideRecord_Mutation {
    insert_sys_pd_lane(label: $label, description: $description, name: $name, active: $active,
    start_rule: $start_rule, order: $order, process_definition: $playbook) {
      sys_id { value }
      label { value }
      name { value }
      description { value }
      active { value }
      laneCondition { value }
      order { value }
      start_rule { value displayValue }
      process_definition { value }
      updated: sys_updated_on { value }
    }
  }
}`;

export const playbookLaneDeleteMutation = `
mutation($sysId: String!) {
  GlideRecord_Mutation {
    delete_sys_pd_lane(sys_id: $sysId) {
      modified_count
    }
  }
}`;
