import {
	TEMPLATE_ROW_KEY_SELECTED,
	TEMPLATE_ROW_VALUE_SELECTED
} from '../../../constants/action-types';

export function handleKeySelected({properties: {rowIndex: idx}, action: {payload: {value, item: {label}}}, dispatch}) {
	dispatch(TEMPLATE_ROW_KEY_SELECTED, {idx, value, label});
}

export function handleValueSelected({action: {payload: {pillValue, pillLabel}}, properties, dispatch}) {
	const {rowIndex: idx} = properties;

	dispatch(TEMPLATE_ROW_VALUE_SELECTED, {idx, value: pillValue, displayValue: pillLabel});
}
