import * as ActionTypes from '../constants/action-types';
import * as Selectors from '../util/selectors';
import * as Shared from '../constants/shared';
import * as Modal from '../constants/modal-constants';
import * as Util from '../util/general-utils';
import * as PillUtils from '../util/pill-utils';
import {flattenValues} from '../datasource/graphql/helpers.graphql';
import {INSERT_BEFORE} from '../components/board/lane-header/lane-header-constants';
import {reorderTheItems} from './rearrange-actions';
import {getStartRules, getStartRuleIdByName} from '../components/configuration-components/config-utils';
import {getConfigurationDefaults, removeCardsFromPlaybook, removeLaneFromPlaybook} from '../util/transforms';
import {closeConfigPanelForDeletedItem} from './configuration-actions';
import {get, sortBy, find, isEmpty} from 'lodash';
import {isArrayWithValues} from '../util/general-utils';
import {setFocusToConfigTriggerElement} from '../util/accessibility-utils';

export const createNewLane = (newLaneOrder, properties, dispatch) => {
	const playbookId = get(properties, 'playbook.sys_id');
	const lanes = Selectors.getLanesFromProperties(properties);
	const laneStartRules = getStartRules(Shared.LANE, properties);
	const previousLane = find(lanes, ['order', newLaneOrder - 1]);
	const isFirstLane = newLaneOrder === 1;
	const start_rule = isFirstLane ?  Shared.IMMEDIATE_START_RULE: Shared.DEFAULT_START_RULE;
	const dependent = isFirstLane ? '' : previousLane && previousLane.sys_id;

	const config = {
		order: newLaneOrder,
		playbook: playbookId,
		start_rule: getStartRuleIdByName(laneStartRules, start_rule),
		dependent
	};

	dispatch(ActionTypes.CREATE_PLACEHOLDER_LANE_REQUESTED, config);
};

export const handleCreateLaneClicked = ({properties, dispatch}) => {
	const lanes = Selectors.getLanesFromProperties(properties);
	const newLaneOrder = Util.getMaxValueOfObjectProp(lanes, 'order') + 1;

	createNewLane(newLaneOrder, properties, dispatch);
};

export const insertLaneClicked = ({action: {payload}, dispatch, properties}) => {
	const {laneOrder, insertLocation} = payload.data;
	const orderOfNewLane = insertLocation === INSERT_BEFORE ? laneOrder : laneOrder + 1;

	createNewLane(orderOfNewLane, properties, dispatch);
};

/*
 * Reorder after lane insertion before or after a lane, or lane deletion
 */

export function getSortedLanes(lanes) {
	return sortBy(Object.values(lanes), ['order']).map(lane => {
		return lane.sys_id;
	});
}

export const reorderLanesAndOpenConfigPanel = ({action: {payload}, state, dispatch, updateProperties}) => {
	if (Util.responseHasErrorsOrNoData(payload, dispatch))
		return;

	const newLane = get(payload, 'data.GlideRecord_Mutation.insert_sys_pd_lane', '');
	const playbook = get(state, 'properties.playbook', {});
	const lanes = get(playbook, 'lanes', {});
	const stateLanes = updateLaneInLanesObject(lanes, newLane);
	const formattedLane = flattenValues(newLane);

	dispatch(ActionTypes.POPULATE_CONFIGURATION_PANEL, {configType: Shared.LANE, data: formattedLane});

	// if only one lane, no need to reorder.
	if (Object.keys(stateLanes).length === 1) {
		updateProperties({
			playbook: {...playbook, lanes: stateLanes},
			showStartProcessElements: false
		});

		return;
	}

	let sortedLanesArray = getSortedLanes(lanes);
	sortedLanesArray.splice(formattedLane.order - 1, 0, formattedLane.sys_id);
	let reorderedLanes = reorderTheItems(sortedLanesArray, stateLanes);

	const laneStartRules = get(state, 'properties.laneStartRules', {});
	const immediateStartRuleId = getStartRuleIdByName(laneStartRules, Shared.IMMEDIATE_START_RULE);

	let combinedUpdatedLanes = {...stateLanes};
	reorderedLanes.forEach((lane, index) => {
		if (lane.order === formattedLane.order + 1 && lane.start_rule !== immediateStartRuleId)
			reorderedLanes[index] = {...lane, dependent: formattedLane.sys_id};

		combinedUpdatedLanes[lane.sys_id] = reorderedLanes[index];
	});

	// update state before save to prevent jumpy UI
	updateProperties({
		playbook: {...playbook, lanes: combinedUpdatedLanes}
	});

	reorderedLanes.forEach(lane => {
		const config = {order: lane.order, sysId: lane.sys_id, dependent: lane.dependent};
		dispatch(ActionTypes.SAVE_AFTER_LANE_REARRANGE_REQUESTED, config);
	});
};

export const handleEditLaneClicked = ({action, dispatch}) => {
	const {
		payload: {
			configType, data
		},
		meta: {
			event: {
				path
			}
		}
	} = action;

	const triggerParent = isArrayWithValues(path) && path[0];
	const triggerElement = triggerParent && triggerParent.shadowRoot.querySelector('now-dropdown');
	const payload = {configType, data, triggerElement};

	dispatch(ActionTypes.POPULATE_CONFIGURATION_PANEL, payload);
};

export const handleSaveLaneConfigSucceeded = ({action, state, updateProperties, dispatch, properties: {configurationState}}) => {
	setFocusToConfigTriggerElement(configurationState);

	if (Util.responseHasErrorsOrNoData(action.payload, dispatch))
		return;

	const lane = getLaneFromResponse(action);
	if (Util.isObjectWithValues(lane)) {
		const playbook = get(state, 'properties.playbook', {});
		const lanes = get(playbook, 'lanes', {});
		const newLanes = updateLaneInLanesObject(lanes, lane);

		updateProperties({
			configurationState: getConfigurationDefaults(),
			playbook: {...playbook, lanes: newLanes}
		});
	}
};

export const updateLaneOrderSucceeded = ({action, state, updateProperties, dispatch}) => {
	if (Util.responseHasErrorsOrNoData(action.payload, dispatch))
		return;

	const updatedLane = getLaneFromResponse(action);
	const playbook = get(state, 'properties.playbook', {});
	const lanes = get(playbook, 'lanes', {});
	const newLanes = updateLaneInLanesObject(lanes, updatedLane);

	updateProperties({
		playbook: {...playbook, lanes: newLanes}
	});
};

export function getLaneFromResponse(action) {
	const updatedLane = get(action, 'payload.data.GlideRecord_Mutation.update_sys_pd_lane', '');
	const createdLane = get(action, 'payload.data.GlideRecord_Mutation.insert_sys_pd_lane', '');

	return updatedLane || createdLane;
}

export function updateLaneInLanesObject(lanes, updatedLane) {
	if (!Util.isObjectWithValues(updatedLane))
		return lanes;

	let transformedLane = flattenValues(updatedLane);
	transformedLane.isNew = true;
	const laneSysId = transformedLane.sys_id;
	let newLanes = {...lanes};
	newLanes[laneSysId] = {...newLanes[laneSysId], ...transformedLane};
	return newLanes;
}

export const handleDeleteLaneSucceeded = ({action, updateProperties, dispatch, properties}) => {
	if (Util.responseHasErrorsOrNoData(action.payload, dispatch))
		return;

	let deletedIds = []; // Collect sys_id of deleted lane and activities inside it
	const laneId = Selectors.getVariableFromActionMetaOptions(action, 'sysId');
	deletedIds.push(laneId);
	const allLanes = Selectors.getLanesFromProperties(properties);
	const cardsToDelete = allLanes[laneId].cards;
	let modifiedPlaybook = Selectors.getPlaybookFromProperties(properties);
	modifiedPlaybook = removeLaneFromPlaybook(modifiedPlaybook, laneId);

	// activities are deleted via cascade delete when a lane is deleted
	// we only need to remove them from the playbook object.
	if (Util.isArrayWithValues(cardsToDelete)) {
		deletedIds = [...deletedIds, ...cardsToDelete];
		modifiedPlaybook = removeCardsFromPlaybook(modifiedPlaybook, cardsToDelete);
	}

	// If we have remaining lanes, clear invalid pills and reorder lanes and cards
	// This will result in the playbook updating and reflecting the leftover lanes and cards
	if (!isEmpty(modifiedPlaybook.lanes)) {
		const payload = {modifiedPlaybook, deletedCards: cardsToDelete};
		dispatch(ActionTypes.REMOVE_PILLS_AND_REORDER_AFTER_LANE_DELETION, payload);
	}
	// If no lanes left, just update the playbook
	else
		updateProperties({
			playbook: {...modifiedPlaybook},
			showStartProcessElements: true
		});

	// Hide Modal and close config panel
	const configurationState = Selectors.getConfigurationStateFromProperties(properties);
	closeConfigPanelForDeletedItem(configurationState, deletedIds, dispatch);
	dispatch(ActionTypes.CLOSE_MODAL);
};

export const handleDeleteLaneButtonClicked = ({action, properties, dispatch}) => {
	const lanes = Selectors.getLanesFromProperties(properties);
	const laneId = get(action, 'payload.data.sys_id', '');
	const lane = lanes[laneId];
	const activitiesToDelete = lane && lane.cards;
	const payload = {sysId: laneId};

	if (lane && Util.isArrayWithValues(activitiesToDelete)) {
		const activitiesWithDependents = Selectors.getActivitiesDependedOnFromProperties(properties);
		// Activities that will have invalid pills after lane deletion
		const affectedActivities = PillUtils.getActivitiesAffectedByLaneDeletion(activitiesWithDependents, activitiesToDelete);

		let warningMessage = '';
		// Inform the user if there are pills on activities that reference activities in this lane
		if (!isEmpty(affectedActivities))
			warningMessage = PillUtils.createWarningMessage(affectedActivities, Modal.DELETE_LANE_WITH_ACTIVITIES_THAT_HAVE_DEPENDENCIES);
		else
			warningMessage = Modal.DELETE_LANE_CONFIRMATION_MODAL_CONTENT;
		// Display modal to confirm deletion of lane with activities
		dispatch(ActionTypes.OPEN_CONFIRM_MODAL_REQUESTED, {...Modal.DeleteLaneModalProperties(warningMessage), appendToPayload: payload});
	}
	else
		// No warning needed. Just delete the lane.
		dispatch(ActionTypes.DELETE_LANE_REQUESTED, payload);
};
