import get from 'lodash/get';

export const getScopeFromProperties = (properties) => {
	return get(properties, 'playbook.scope.value', '');
};

export const getErrorsFromPayload = (payload) => {
	return get(payload, 'errors', []);
};

export const getDataFromPayload = (payload) => {
	return get(payload, 'data', null);
};

export const getVariableFromActionMetaOptions = (action, variable) => {
	return get(action, `meta.options.variables.${variable}`, '');
};

export const getLanesFromProperties = (properties) => {
	return {...get(properties, 'playbook.lanes', {})};
};

export const getCardsFromProperties = (properties) => {
	return {...get(properties, 'playbook.cards', {})};
};

export const getPlaybookFromProperties = (properties) => {
	return {...get(properties, 'playbook', {})};
};

export const getConfigurationStateFromProperties = (properties) => {
	return get(properties, 'configurationState', {});
};

export const getActivitiesDependedOnFromProperties = (properties) => {
	return {...get(properties, 'activitiesDependedOn', {})};
};

export const getUserRoles = (userData) => {
	return get(userData, 'user.roles', []);
};

export const getInputDisplayPrefsFromActivity = (activity) => {
	return get(activity, 'activity_definition.input_display_preferences', '');
};

export const getTriggerDotWalkData = (playbook) => {
	return {...get(playbook, 'triggerRecordData')};
};

export const getDisplayPrefsEnabled = (sessionUserData) => {
	return get(sessionUserData, 'info.displayPrefsEnabled', false);
};

export const getConditionFieldsEnabled = (sessionUserData) => {
	return get(sessionUserData, 'info.conditionFieldsEnabled', false);
};
