import {closeModal} from '../util/modal-util';
import {resetModal, initGlideFrame} from '../util/iframe-utils';
import * as ActionTypes from '../constants/action-types';
import {OPENED} from '../constants/modal-constants';

export const handleOpenConfirmationModal = ({action, dispatch}) => {
	const data = action.payload;
	dispatch(ActionTypes.OPEN_MODAL_REQUESTED, {data});
};

export const handleCloseConfirmModal = ({updateProperties}) => {
	closeModal(updateProperties);
};

export function handlePropertyChanges({action: {payload: {name}}}) {
	if (name === OPENED || name === 'iframe')
		handleOpenedChange(arguments[0]);
}

function handleOpenedChange({state, action, dispatch}) {
	const {properties: {iframe}} = state;
	const {payload: {name, value, host: {shadowRoot}}} = action;

	// on close of an iframe modal, reset the iframe specific CSS applied to the now-modal.
	if (iframe && !value || !iframe && value)
		resetModal(shadowRoot);

	// on open where it is an iframe modal, initialize the iframe with specific properties.
	if (iframe && value)
		initGlideFrame(
			shadowRoot,
			name === 'iframe' ? value.onSubmit : iframe.onSubmit,
			value,
			dispatch
		);
}

export const focusModalCloseButton = ({action: {payload: {modalElement}}}) => {
	const modalElShadowRoot = modalElement && modalElement.shadowRoot;
	const nowBtnIconic = modalElShadowRoot && modalElShadowRoot.querySelector('.now-modal-header now-button-iconic');
	const nowBtnIconicShadowRoot = nowBtnIconic && nowBtnIconic.shadowRoot;
	const closeButton = nowBtnIconicShadowRoot && nowBtnIconicShadowRoot.querySelector('button[aria-label="Close dialog"]');
	closeButton && closeButton.focus();
};
