import * as AlertConst from '../alert-list-constants';
import get from 'lodash/get';


export const handleActionClicked = ({action: {payload}, dispatch}) => {
	const type = get(payload, 'action.type', null);

	if (type === AlertConst.DISMISS) {
		const alertId = get(payload, 'alertId');
		dispatch(AlertConst.ALERT_DISMISSED, {alertId});
	}
};

export const handleDismissAll = ({dispatch}) => {
	dispatch(AlertConst.ALERT_DISMISSED, {alertId: null});
};
