import {
	NEW_PLAYBOOK_MODAL_CANCEL,
	OPEN_SELECT_TRIGGER_MODAL
} from '../../../constants/action-types';
import {GlideFormModal} from '../../../constants/modal-constants';
import {CANCEL} from '../../../constants/shared';
import * as CreationConsts from './creation-constants';

export const CreationModal = (isPrefilled) => {
	return {
		footerActions: [
			{label: CreationConsts.SELECT_TRIGGER_LABEL, variant: 'primary', clickActionType: OPEN_SELECT_TRIGGER_MODAL, disabled: !isPrefilled},
			{label: CANCEL, variant: 'secondary', clickActionType: NEW_PLAYBOOK_MODAL_CANCEL},
		],
		headerLabel: CreationConsts.NEW_PROCESS_MODAL_LABEL,
		opened: false,
		size: 'lg'
	};
};

export const TriggerInstanceModal = (triggerInstanceId, view, forced = true, headerLabel) => {
	let url = `/sys_pd_trigger_instance.do?sys_id=${triggerInstanceId}`;

	if (view)
		url = url + `&sysparm_view=${view}`;

	if (forced)
		url = url + '&sysparm_view_forced=true';

	return {...GlideFormModal(
		headerLabel,
		url,
		{
			iframe: {
				renderFormHeaderBar: false,
				onSubmit: () => {},
				size: 'lg',
				recordType: CreationConsts.TRIGGER
			}
		})
	};
};
