import pathToRegexp from 'path-to-regexp';
import qs from 'qs';
//import {trackEvent} from '../utils'; Todo need to move utils from workspace
import {isEmpty, camelCase} from 'lodash';
import * as constants from '../constants/constants';
import {queryParamsEnabled, getLandingPath} from './sn-pd-router.config';


export const BASE_ATTR = 'data-sn-href';
const BASE_TITLE = window.document.title;

function getGlobal() {
	if (typeof self !== 'undefined') {
		return self;
	}

	if (typeof window !== 'undefined') {
		return window;
	}

	if (typeof global !== 'undefined') {
		return global; // eslint-disable-line no-undef
	}

	throw new Error('Unable to locate global object');
}

export function getLocation(){
	return getGlobal().location;
}

function removeTrailingSlash(pathname) {
	const last = pathname.length - 1;
	return pathname.substring(last) === '/' ? pathname.substring(0, last) : pathname;
}

function removeFirstSlash(pathname) {
	return pathname.substring(0, 1) === '/' ? pathname.substring(1) : pathname;
}

function trimSlashes(pathname) {
	return removeTrailingSlash(removeFirstSlash(pathname));
}

function removeRelativePathParts(href){
	return href.split('/').filter((part) => {
		return part !== '..';
	}).join('/');
}

function stripBase(pathname) {
	const base = getBasePath();
	return pathname.replace(base, '');
}

function getPathname(url){
	const parsed = document.createElement('a');
	parsed.href = url;
	return (parsed.pathname || url).replace(/(^\/?)/, '/');

}

function keysToParams(match, keys) {
	const params = {};
	let index = 1;
	for (let k in keys) {
		params[keys[k].name] = match[index];
		index++;
	}

	return params;
}

function resolveHref(href) {
	const base = getBasePath();
	if (!href.indexOf('/')) // absolute
		return `${base}${href}`;

	const hrefParts = href.split('/');
	const pathname  = trimSlashes(getLocation().pathname);

	if (hrefParts[0] === '.') // relative, cwd
		return `/${pathname}/${hrefParts.slice(1).join('/')}`;
	if (hrefParts[0] === '..') { // relative, up
		const up = hrefParts.reduce((acc, part) => {
			if (part === '..')
				acc++;

			return acc;
		}, 0);

		if (base && trimSlashes(base).split('/').length >= up) {
			return `${base}/${trimSlashes(removeRelativePathParts(href))}`;
		}

		return up >= pathname.split('/').length ? '/' : `/${pathname.split('/').slice(0, (up - 1)).join('/')}/${hrefParts.slice(up).join('/')}`;
	}

	return `/${pathname}/${hrefParts.join('/')}`; // relative, cwd, without a prefix ('.')
}

function push(href) {
	getGlobal().history.pushState({}, null, href);
}

export function replace(href) {
	getGlobal().history.replaceState({}, null, href);
}

export function navigateUrl(url){
	push(resolveHref(url));
	//trackEvent('navigate', url);
	setPageTitle(url);
}

export function setPageTitle(url) {
	try {
		const title = url.split('/')[1];
		if (title && title.length > 3) {
			const pageTitle = title.charAt(0).toUpperCase() + title.substr(1);
			window.document.title = `${pageTitle} | ${BASE_TITLE}`;
		}
	}
	catch (e) { () => {};}
}

export function initBaseUrl() {
	
	const {pathname} = getLocation();
	const base = getBasePath();
	const ERROR_NOTFOUND_PATH = '/error/NotFound';
	
	if(!pathname || pathname === '' || pathname === '/' || pathname === base || pathname === base+'/'){
		replace(base+(getLandingPath() ? '/'+trimSlashes(getLandingPath()) : ''));
		//trackEvent('redirect', url);
		setPageTitle(BASE_TITLE);
	}
	else if((''+pathname).indexOf(getBasePath()) === -1){
		redirectUrl(ERROR_NOTFOUND_PATH);
		setPageTitle(ERROR_NOTFOUND_PATH);
	}
}

export function redirectUrl(url) {
	if (!url || typeof(url) != 'string')
		return;
	replace(resolveHref(url));
	//trackEvent('redirect', url);
	setPageTitle(url);
}

export function getParametersFromPath(path,  url){
	const keys = [];
	const regex = pathToRegexp(path, keys); 
	if(url){
		const queryIdx = url.indexOf('?');
		const match = queryIdx > -1 ? regex.exec(stripBase(url.substring(0, queryIdx))) : regex.exec(stripBase(url));
		const search =  queryIdx > -1 ? url.substring(queryIdx) : null;
		
		return {
			params: {
				path: match ? keysToParams(match, keys) : {},
				search
			}
		};
	}
	const { pathname, hash, search } = getLocation();
	const match = regex.exec(stripBase(pathname));

	return { pathname, hash, params: {
		query: getParamsInCamelCase(qs.parse(search.substring(1))),
		search,
		path: match ? keysToParams(match, keys) : {}
	}};
}

let basePath = null;
export function getBasePath() {
	if(!basePath) {
		const base = document.querySelector('base');
		if (base) {
			// data-sn-href is a custom base path tag used by UX Framework. Use it if it exists, or the native href attribute otherwise
			const pathname = getPathname(base.getAttribute(BASE_ATTR) || base.href);
			basePath = pathname ? removeTrailingSlash(pathname) : '';
		} else
			basePath = '';
	}

	return basePath;
}

/**
 * Construct a params object from an array of URL slugs
 * @param options
 */
export function getParamsFromUri(options){
	let params = {};
	let paramFound = false;
	let paramName = null;
	if(queryParamsEnabled()){
		for(let i = 0; i < options.length; i++) {
		// Stop parsing if we land on a SUB keyword
			if (options[i] === constants.SUB_URL)
				break;

			if(options[i].indexOf(constants.QUERY_PARAMS_DEVIDER) > -1) {
				params = getParamsInCamelCase(qs.parse(options[i].substring(options[i].indexOf(constants.QUERY_PARAMS_DEVIDER)+1)));
				break;
			}
		}
	}else{
		for(let i = 0; i < options.length; i++) {
		// Stop parsing if we land on a SUB keyword
			if (options[i] === constants.SUB_URL)
				break;

			if(options[i] === constants.PARAMS_DIVIDER) {
			// Stop parsing if we encounter a second PARAMS keyword
				if(paramFound)
					break;

				paramFound = true;
				continue;
			}

			// No params found yet
			if(!paramFound)
				continue;

			if(paramName === null)
				paramName = camelCase(decodeURIComponent(options[i]));
			else {
				params[paramName] = decodeURIComponent(options[i]);
				paramName = null;
			}
		}
	}

	if(isEmpty(params))
		return;

	return params;
}
	
function getParamsInCamelCase(paramsInKababCase){
	const paramsInCamelCase = {};
	for(let [key, value] of Object.entries(paramsInKababCase)){
		paramsInCamelCase[camelCase(key)] = value;
	}
	return paramsInCamelCase;
}




