import {get} from 'lodash';
import '@servicenow/now-record-list-connected';
import '../sn-pd-modal';
import {PLAYBOOK_TABLE, LIST_TITLE} from '../../constants/shared';
import {isArrayWithValues} from '../../util/general-utils';
import {creationForm} from './create-new-process/creation-form-wrapper';

export const view = (state, {dispatch}) => {
	const {modal, properties: {userPrefs, alerts}, listRefreshRequested} = state;
	const rowcount = Number(get(userPrefs.find(pref => pref.name === 'rowcount'), 'value', '20'));

	return (
		<div id='sn-pd-list-view'>
			{
				isArrayWithValues(alerts) ?
					<sn-pd-alert-list
						alerts={alerts}
						component-id='alert-list'
					/> : ''
			}
			<sn-pd-modal {...modal}>
				<div>
					{creationForm(state, dispatch)}
				</div>
			</sn-pd-modal>

			<now-record-list-connected table={PLAYBOOK_TABLE}
				listTitle={LIST_TITLE}
				workspaceConfigId=''
				columns='label,sys_scope,status,active,trigger,sys_updated_by,sys_updated_on'
				hideQuickEdit
				hideMultiEdit
				hideMenuButton
				hidePanelAdvanced
				refreshRequested={listRefreshRequested}
				limit={rowcount}
				query=''
			/>
		</div>
	);
};
