import {renderFactoryBasedElement, isPillField, isTemplateValueField} from './dynamic-form-helpers';
import {STRING_TYPES, MULTILINE_TYPES} from './dynamic-form-constants';
import '../pill/pill-field';
import '../sn-pd-template-value';
import '../pill/pill-container';

const makeComponentId = (sysId, fieldName, index) => `${sysId}_${fieldName}_${index}`;

const renderField = (field, dispatch, onChangeActionType, dotWalkData, sysId, index, openInModal) => {

	const {
		uiType,
		name,
		label
	} = field;

	const componentId = makeComponentId(sysId, name, index);
	const fieldKey = `dyn-form-field-${componentId}`;

	if (isTemplateValueField(field)) {
		const {glideVarFields: {variableMapping, variableValue}, dependentOnTable} = field;

		return <sn-pd-template-value
			key={fieldKey}
			append-to-payload={{isGlideVar: true}}
			table={dependentOnTable}
			label={label}
			encodedValue={variableMapping || variableValue}
			onChangeActionType={onChangeActionType}
			field={{...field, componentId}}
			dotWalkData={dotWalkData}
			openInModal={openInModal}
		/>;
	}

	if (isPillField(field) && STRING_TYPES.includes(uiType)) {
		//copy props.. need to just define this and use it all over
		const {
			glideVarFields: {
				variableMapping,
				variableValue,
			},
		} = field;

		//the value we will pass to the component, either the mapping value or regular value
		const inputsValue = variableMapping || variableValue;

		const multiLine = MULTILINE_TYPES.includes(uiType);

		const pillContainerProps = {
			name,
			label,
			multiLine,
			dotWalkData,
			updateActionName: onChangeActionType,
			inputValue: inputsValue,
			componentId: componentId,
		};

		return (
			<sn-pd-pill-container
				key={fieldKey}
				append-to-payload={{isGlideVar: true}}
				{...pillContainerProps}
			/>
		);

	} else if (isPillField(field))
		return (
			<sn-pill-field
				key={fieldKey}
				append-to-payload={{isGlideVar: true}}
				field={{...field, componentId}}
				dotWalkData={dotWalkData}
				onChangeAction={onChangeActionType}
			/>
		);

	else
		return renderFactoryBasedElement(field, componentId, dispatch, onChangeActionType);
};

export const view = ({properties: {fields, onChangeActionType, dotWalkData, sysId, openInModal}}, dispatch) => {
	return (
		<div className='dynamic-form-container'>
			{ fields.map((field, i) => renderField(field, dispatch, onChangeActionType, dotWalkData, sysId, i, openInModal)) }
		</div>
	);
};
