import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import * as ActionTypes from '../../../constants/action-types';
import {GlideFormModal} from '../../../constants/modal-constants';
import {getConfigureModalHeader, getAdvancedConfigLabel} from '../config-utils';
import {t} from 'sn-translate';
import {ACTIVITY} from '../../../constants/shared';

export const advancedConfigButton = (type, url, dispatch, language, useSeismicModal) => {

	// We are using a click handler here instead of bubbling an action dispatch because
	// we don't want to createCustomElement (seismicify) this component to achieve simple functionality.
	// Additionally, this allows us the timing to prevent href firings which can reload the page whereas the
	// text link according to docs doesn't send the browser event with it's payload.
	const handleJellyClick = e => {
		//stop href from refreshing the page when fired
		e.preventDefault();
		const onSubmit = () => {
			dispatch(ActionTypes.CLOSE_MODAL);
			dispatch(ActionTypes.FETCH_PLAYBOOK, {language});
			dispatch(ActionTypes.HIDE_CONFIGURATION_PANEL_REQUESTED);
		};

		const modalHeaderLabel = getConfigureModalHeader(type);
		dispatch(ActionTypes.OPEN_CONFIRM_MODAL_REQUESTED, {...GlideFormModal(modalHeaderLabel, url, {iframe: {renderFormHeaderBar: false, onSubmit}})});
	};

	const handleClick = () => {
		dispatch(ActionTypes.SHOW_CONFIG_MODAL);
	};

	const onClick = useSeismicModal && type === ACTIVITY ? handleClick : handleJellyClick;
	const buttonLabel = useSeismicModal && type === ACTIVITY ? t('Show advanced properties') : getAdvancedConfigLabel(type);

	return (
		<Fragment>
			<now-icon icon='pencil-outline' size='md' />
			<now-button-bare
				label={buttonLabel}
				variant='primary'
				on-click={onClick}
				size='sm'
			/>
		</Fragment>
	);
};
