import {get, toArray} from 'lodash';
import {querySelectorDeep} from '../util/general-utils';
import {getBaseUrl} from './url-utils';
import {
	HTML_STYLE_ATTR,
	MODAL_HEIGHT,
	MODAL_WIDTH,
	NOW_MODAL_CLASS, NOW_MODAL_DIALOG_CLASS,
	RESET_BUTTON_TEXT
} from '../constants/iframe-constants';
import * as ActionTypes from '../constants/action-types';
import * as CreationConsts from '../components/sn-pd-list/create-new-process/creation-constants';



//store the initial url in order to only close the modal on form submission at the opened url.
//This way the user can operate on all related forms to the initial record
let initialUrl;
let initialFormId;

/**
 * General glide form modal initialization.
 *
 * @param modal
 * @param submitFn
 * @param config {renderFormHeaderBar}
 * @returns {null}
 */
export function initGlideFrame(modal, submitFn, config, dispatch) {
	if (!modal) {
		console.warn('Need an iframe element in order to initialize the frame.');
		return null;
	}

	resetModal(modal);
	const nowModal = getNowModalElement(modal);

	if (!nowModal)
		return null;

	if (!get(config, 'size'))
		setModalDimensions(nowModal, MODAL_WIDTH, MODAL_HEIGHT);

	const iframeEl = getIframeElement(nowModal);

	if (!iframeEl)
		return null;

	attachOnLoad(iframeEl, submitFn, config, dispatch);
}

function attachOnLoad(iframeEl, submitFn, config, dispatch) {
	iframeEl.onload = () => {
		const gForm = get(iframeEl, 'contentWindow.g_form', null);

		//if we end up on a non-form view, reload to the initial url. The user has probably ended up in a bad state
		//due to the history stack.
		if (!gForm) {
			if (initialUrl)
				iframeEl.src = initialUrl;
			else
				return null;
		}

		if (!initialUrl)
			initialUrl = get(iframeEl, 'contentWindow.location.href');

		if (!initialFormId)
			initialFormId = gForm.getUniqueValue();

		// only add back to home button when current page isn't home.
		const url = get(iframeEl, 'contentWindow.location.href');
		if (url !== initialUrl)
			addHomeButton(iframeEl);

		hideAllDeleteButtons(iframeEl);
		handleConfiguration(iframeEl, config, url);
		handleFrameSubmit(iframeEl, submitFn, dispatch);
	};
}

/**
 * Configuration can take in any number of args depending on the readme for this component.
 * @param iframeEl
 * @param config
 */
function handleConfiguration(iframeEl, config, url) {
	if (!config || config.length === 0)
		return;

	const {renderFormHeaderBar, recordType} = config;
	const formatForTriggerModal = recordType === CreationConsts.TRIGGER;

	if (url.includes('sys_id') && formatForTriggerModal)
		addExtraTriggerDefFormatting(iframeEl);

	if (!renderFormHeaderBar) {
		// buttons in the trigger footer need to look like seismic buttons, so additional magic must occur
		showButtonsInFooter(iframeEl, formatForTriggerModal);
		hideFrameHeader(iframeEl);
		hideRelatedLists(iframeEl);
	}
}

/**
 * Any customizations done to now modal need to be removed in order for the modal to function
 * properly in a non-iframe use case.
 * @param modal
 */
export function resetModal(modal) {
	resetModalDimensions(modal);
	initialFormId = null;
	initialUrl = null;
}

/**
 * Appends necessary params for use in an iframe.
 *
 * @param url
 * @returns {URL}
 */
export function buildGlideFormUrl(url) {
	const glideUrl = getBaseUrl() + url;
	const constructedUrl = new URL(glideUrl);
	constructedUrl.searchParams.append('sysparm_nameofstack', 'pdhistory');
	return constructedUrl;
}

export function getTriggerInstanceRecordUrl(sysId) {
	return !sysId ? null : `/sys_pd_trigger_instance.do?sys_id=${sysId}&sysparm_view=pad_properties&sysparm_view_forced=true`;
}

/**
 * Get the now-modal element using a deep shadowRoot search from within the sn-pd-modal
 * This element is required to be passed to some iframe util functions.
 * @param modalNode
 * @returns {*}
 */
export function getNowModalElement(modalNode) {
	if (!modalNode) {
		console.warn('Must supply a modal node to retrive the now-modal.');
		return null;
	}

	const modalElements = querySelectorDeep(NOW_MODAL_CLASS, modalNode);
	if (modalElements.length > 0)
		return modalElements[0];

	return null;
}

/**
 * The now-modal only allows for certain predefined sizes: sm, md, lg.
 * These sizes are not idea for glide form modals. This function will overwrite the
 * default sizes by setting a custom width and height to the now-modal.
 *
 * CSS can't be used to achieve this because the /deep/ and ::shadow selectors are deprecated
 * to force encapsulation.
 *
 * @param modalElement
 * @param width
 * @param height
 */
export function setModalDimensions(modalElement, width = '', height = '') {
	if (!modalElement) {
		console.warn('Must supply a now-modal element to set dimensions');
		return null;
	}

	const maxWidth = width ? `max-width: ${width};` : '';
	const maxHeight = height ? `max-height: ${height}` : '';

	const dialogElement = modalElement.querySelector(NOW_MODAL_DIALOG_CLASS);

	if (dialogElement)
		dialogElement.setAttribute(HTML_STYLE_ATTR, `${maxWidth}${maxHeight}`);
}

export function addHomeButton(iframe) {
	const headerTarget = iframe.contentDocument.querySelector('.navbar .container-fluid');

	if (!headerTarget)
		return null;

	const backButton = headerTarget.querySelector('.navbar-header');

	const div = document.createElement('div');

	const button = document.createElement('button');
	button.innerHTML = RESET_BUTTON_TEXT;
	button.onclick = e => {
		e.preventDefault();
		iframe.contentWindow.location.href = initialUrl;
	};
	button.style.float = 'left';
	div.appendChild(button);

	headerTarget.insertBefore(div, backButton);
}

/**
 * Get Iframe element from within a now-modal
 *
 * @param modalElement
 * @returns {Element}
 */
export function getIframeElement(modalElement) {
	const slot = modalElement.querySelector('slot');
	return slot.querySelector('iframe');
}

function addExtraTriggerDefFormatting(iframeEl) {
	const iframeBody = iframeEl.contentDocument.querySelector('body');
	iframeBody.setAttribute('style', 'margin-top: -50px');
	const navBar = iframeEl.contentDocument.querySelector('.section_secondary');
	navBar.setAttribute('style', 'height: 0px; border: none');
}

function showButtonsInFooter(iframeEl, formatButtons) {
	hideTimer(iframeEl);

	const headerButtons = toArray(iframeEl.contentDocument.querySelectorAll('.navbar_ui_actions button'));
	const footer = iframeEl.contentDocument.querySelector('.form_action_button_container');
	const footerButtons = toArray(footer.querySelectorAll('button'));
	const footerActionNames = footerButtons.map(button => get(button, 'dataset.actionName'));

	if (!footer && !footer.appendChild)
		return;

	if (footerButtons.length === headerButtons.length) {
		if (formatButtons)
			footerButtons.forEach(button => button.setAttribute('style', 'border-radius: 0 !important; font-size: 16px;'));
	} else {
		for (let i = 0; i < headerButtons.length; i++) {
			const button = headerButtons[i];
			if (formatButtons)
				button.setAttribute('style', 'border-radius: 0 !important; font-size: 16px;');

			if (footerActionNames.indexOf(get(button, 'dataset.actionName')) === -1)
				footer.appendChild(button.cloneNode(true));
		}
	}

	if (formatButtons) {
		const content = iframeEl.contentDocument.querySelector('.section-content');
		const buttonDiv = iframeEl.contentDocument.createElement('div');
		buttonDiv.setAttribute('style', 'text-align: right; margin-top: 20px');
		buttonDiv.appendChild(footer);
		content.appendChild(buttonDiv);
	}
}

function hideTimer(iframeEl) {
	const timer = iframeEl.contentDocument.querySelector('#page_timing_div');
	hideElement(timer);
}

function hideRelatedLists(iframe) {
	const relatedListsTabsList = iframe.contentDocument.querySelectorAll('#tabs2_list');
	hideAllElement(relatedListsTabsList);

	const relatedListsTabsSpacer = iframe.contentDocument.querySelectorAll('#tabs2_spacer');
	hideAllElement(relatedListsTabsSpacer);
}

function hideAllElement(elements) {
	if (elements.length > 0)
		elements.forEach(el => { hideElement(el); });
}

export function hideFrameHeader(iframe) {
	const header = iframe.contentDocument.querySelector('.form_title');
	hideElement(header);
}

export function hideAllDeleteButtons(iframe) {
	// hide buttons at the bottom of the form
	const deleteButton = iframe.contentDocument.querySelector('#sysverb_delete_bottom');
	hideElement(deleteButton);

	//hide the button in the form's top bar
	const headerDeleteButton = get(iframe, 'iframe.contentWindow.window.sysverb_delete');
	if (headerDeleteButton)
		headerDeleteButton.hide();
}

/**
 * Wrap the GlideForm submission function (gsftSubmit) provided on the window object
 * of any form.
 *
 * Submission is only allowed on the original URL upon which the modal was opened.
 * This allows the user to utilize all of the Glide UI in order to view previews,
 * open references, etc.
 *
 * @param iframe
 * @param submitFn
 */
export function handleFrameSubmit(iframe, submitFn, dispatch) {
	const gsftSubmit = get(iframe, 'contentWindow.gsftSubmit', null);

	if (!gsftSubmit)
		return;

	iframe.contentWindow.gsftSubmit = iframe.contentWindow.gsftSubmit.wrap((callOriginal, control, form, action_name) => {
		const gForm = get(iframe, 'contentWindow.g_form', null);
		const controlId = getActionNameFromClickedControl(control);

		//buttons that have cancel in their action name should dispatch early
		//in order to let the modal close right away. I'm still allowing them to
		//submit their original script from a UI Action in case certain cases require it.
		if (controlId.indexOf('cancel') > -1)
			handleSubmitActions(controlId, dispatch, gForm);

		if (isValidSubmission(submitFn, controlId, gForm, iframe)) {
			handleSubmitActions(controlId, dispatch, gForm);

			if (controlId !== 'sysverb_view_change')
				setTimeout(submitFn, 1000);
		}

		return callOriginal(control, form, action_name);
	});
}

export function hideElement(element) {
	if (element)
		element.setAttribute(HTML_STYLE_ATTR, 'display: none;');
}

export function resetModalDimensions(modal) {
	const nowModal = getNowModalElement(modal);

	if (nowModal)
		nowModal.removeAttribute(HTML_STYLE_ATTR);
}

function isValidSubmission(submitFn, controlId, gForm, iframe) {
	const mandatoryFieldsNotEmpty = gForm &&
		gForm.elements.filter(field => field.mandatory).every(getFieldValue.bind(null, iframe));

	return typeof submitFn === 'function' &&
	controlId !== 'sysverb_back' &&
	gForm && initialFormId === gForm.getUniqueValue() &&
	mandatoryFieldsNotEmpty;
}

/**
 * Different interactions on the form, have a different place they store the action name.
 *
 * @param control - form control that was clicked
 */
function getActionNameFromClickedControl(control) {
	let controlId;
	// if the type of interaction clicked is a form link, grab the action name from the control
	if (control.getAttribute('class').indexOf('navigation_link') >= 0)
		controlId = control.getAttribute('gsft_action_name');

	// attempts to get the action name from the view change that happens through a top bar or context menu
	// of a form.
	if (!controlId)
		controlId = control.getAttribute('id');

	//Buttons at the bottom and top of the form use the data-action-name attribute to fill in their action name.
	if (controlId !== 'sysverb_view_change')
		controlId = control.getAttribute('data-action-name');

	return controlId;
}

function handleSubmitActions(action, dispatch, gForm) {
	switch(action) {
	case 'sys_pd_trigger_instance_submit':
		dispatch(ActionTypes.IFRAME_TRIGGER_INSTANCE_SUBMITTED);
		break;
	case 'sys_pd_trigger_instance_cancel': // return to the trigger selection screen
		dispatch(ActionTypes.IFRAME_TRIGGER_INSTANCE_CANCELLED);
		break;
	case 'sys_pd_go_to_designer': // fires from the instance form
		dispatch(ActionTypes.INSTANCE_IFRAME_GO_TO_DESIGNER);
		break;
	case 'sys_pd_trigger_def_cancel': // return to the trigger selection screen
		dispatch(ActionTypes.IFRAME_TRIGGER_DEF_CANCELED);
		break;
	case 'sys_pd_trigger_def_submit': // continue to trigger def screen 2
		dispatch(ActionTypes.IFRAME_TRIGGER_DEF_NEXT, {sysId: gForm.getUniqueValue(), label: gForm.getValue('label')});
		break;
	case 'sys_pd_save_and_load_designer': // save changes and go to designer view
		dispatch(ActionTypes.IFRAME_TRIGGER_DEF_SUBMITTED);
		break;
	default:
		return;
	}
}

function getFieldValue(iframe, field) {
	let fetchedNode = Object.values(field.fetchedNodes)[0];
	return fetchedNode &&
		(get(iframe.contentDocument.getElementById('sys_mapping.' + fetchedNode.id), 'value') ||
			fetchedNode.value);
}
