import {createCustomElement} from '@servicenow/ui-core';
import styles from './pill-field.scss';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './pill-field-view';

createCustomElement('sn-pill-field', {
	renderer: {type: snabbdom},
	view,
	properties: {
		field: {},
		dotWalkData: {},
		onChangeAction: {},
	},
	actionHandlers: {
		['NOW_PILL#DISMISSED']: ({state, dispatch}) => {
			const {field: {name}, onChangeAction} = state.properties;

			//Pill is dismissed so dispatch a change event with an empty value
			dispatch(onChangeAction, {name, value: ''});
		},
	},
	styles
});
