import {FieldType} from '@devsnc/sn-glide-form-controls';
import {getFormControl} from './form-controls';
import {SUPPORTED_TYPES} from './dynamic-form-constants';

export const onValueChange = (field, dispatch, actionType) => {
	return (e, name, value, displayValue) => {
		if (field.value !== value || field.displayValue !== displayValue) {
			let updatedField = {};

			// There will likely be more cases here
			switch (field.internalType) {
			case FieldType.GLIDE_LIST:
				updatedField = {
					...field,
					value,
					oldValue: field.value,
					displayValue: displayValue.join(', '),
					display_value_list: displayValue || value
				};
				break;
			default:
				updatedField = {
					...field,
					value,
					oldValue: field.value,
					displayValue
				};
			}

			dispatch(actionType, updatedField);
		}
	};
};

export const ensureSupportedFieldType = (fieldType) => {
	if (SUPPORTED_TYPES.includes(fieldType))
		return fieldType;

	return FieldType.STRING;
};

export const renderFactoryBasedElement = (field, componentId, dispatch, actionType) => {
	const type = ensureSupportedFieldType(field.internalType);
	const formControl = getFormControl(
		componentId,
		type,
		field,
		onValueChange(field, dispatch, actionType),
		onValueChange(field, dispatch, actionType)
	);

	const {Control, props} = formControl;

	if (field.messages)
		props.messages = field.messages;

	return (
		<div className='template-field-value factory-input-container'>
			<Control {...props} />
		</div>
	);
};

export const isFieldValid = (field) => {
	if (!field.mandatory ||
		(field.value && field.value.trim() ||
		field.displayValue && field.displayValue.trim()))
		return true;
	return false;
};

export const isPillField = field => field.hasOwnProperty('glideVarFields');

export const isTemplateValueField = field => field && field.internalType === 'template_value';
