import '@servicenow/now-alert';

export const view = ({properties}) => {
	const {alertItems} = properties;

	if (!alertItems.length)
		return null;

	return (
		<div className='alert-list-container'>
			<now-alert-list
				config-aria-live='polite'
				items={alertItems}>
			</now-alert-list>
		</div>
	);
};
