import get from 'lodash/get';
import {isReferenceType} from '../../util/general-utils';
import * as TypeConsts from '../../constants/glide-types';

export function getDotWalkData(state) {
	const stateData = get(state, 'data');
	const initialData = get(state, 'properties.initialData', {});
	return stateData ? {...stateData} : {...initialData};
}

export function getDotWalkPath(state) {
	const path = get(state, 'walkerPath');
	return path ? [...path] : null;
}

export function getTriggerPath(state) {
	const dotWalkData = getDotWalkData(state);
	return [...get(dotWalkData, 'firstColumn.trigger.path', [])];
}

export function getFieldType(state) {
	return get(state, 'properties.fieldType');
}

/**
 * @param {string} itemType - glide type of the dot walk item
 * @param {string} glideType - glide type required for the form field
 * @returns {boolean}
 */
export function isItemDisabled(itemType, glideType) {
	return !canAcceptType(glideType, itemType);
}

/**
 * @param {string} field - glide type of element to get mapped
 * @param {string} map - glide type of element being applied onto field
 * @returns {boolean}
 */
export function canAcceptType(field, map) {
	if (map === TypeConsts.GLIDE_VAR)
		return false;

	// strings fields can accept anything
	if (field === TypeConsts.STRING)
		return true;

	if (map === TypeConsts.CREATE_LINK)
		return true;

	if ((field === TypeConsts.CURRENCY || field === TypeConsts.PRICE))
		return TypeConsts.MONEY_COMPATIBLE_TYPES.includes(map);

	if (isReferenceType(map) && isReferenceType(field))
		return true;

	// leave a loophole for now allowing strings to be applied to any field type
	// this may or may not be necessary for say, mapping a json result to a reference
	if (map === TypeConsts.STRING)
		return true;

	if ((field === TypeConsts.JOURNAL || field === TypeConsts.JOURNAL_INPUT) && map !== TypeConsts.STRING)
		return false;

	if (field === TypeConsts.JOURNAL_INPUT)
		return false;

	if (map === field)
		return true;

	if (field === TypeConsts.CHOICE && map === TypeConsts.STRING)
		return true;

	if ((field === TypeConsts.SIMPLE_NAME_VALUES || field === TypeConsts.USER_INPUT) && map === TypeConsts.DOCUMENT_ID)
		return true;

	if ((field === TypeConsts.SHORT_TABLE_NAME || field === TypeConsts.USER_INPUT) && map === TypeConsts.TABLE_NAME)
		return true;

	if (TypeConsts.SIMPLE_NAME_VALUE_TYPES.includes(field) && map === 'simple_name_values')
		return true;

	if (TypeConsts.DATE_TIME_TYPES.includes(field))
		return TypeConsts.DATE_TIME_TYPES.includes(map);

	return false;
}
