import {filter, get} from 'lodash';
import {SYS_HUB_FLOW_TABLE, SUBFLOW_TYPE, FLOW_DESGINER_EDIT_URL} from '../constants/shared';

//maps flow designer type to url fragment
const getFlowDesignerType = source => {
	const sourceTable = Object.keys(source);
	if (sourceTable.includes(SYS_HUB_FLOW_TABLE)) {
		const flowType = get(source, `${SYS_HUB_FLOW_TABLE}[0].type`, 'flow');
		return flowType && flowType === SUBFLOW_TYPE ? 'sub-flow' : 'flow';
	} else return 'action';
};

/**
 *
 * Construct a relative path to a flow designer page.  Used
 * to launch flow designer for a flow/subflow/action from an
 * activity definition.
 *
 * @param activityDefinition activity definition object to map to source sys_hub_flow or sys_hub_action_type_definition
 */
export const createFlowDesignerUrl = activityDefinition => {
	const sourceType = get(activityDefinition, 'source_type');
	const sourceInfo = filter(activityDefinition, elem => {return elem && elem[sourceType];});
	const sourceId = get(sourceInfo[0], `${sourceType}[0].sys_id`);

	return `${FLOW_DESGINER_EDIT_URL}${getFlowDesignerType(sourceInfo[0])}-designer/${sourceId}`;
};

/**
 * This function is temporary, eventually this open link capability
 * will be provided by the canvas.
 */
export const openInNewWindow = (url) => {
	window.open(url, '_blank');
};

/**
 * Get the base of the URL in the browser.
 * ex) http://localhost:8000/ from http://localhost:8000/sys_pd_activity.do?something...
 * @returns {string}
 */
export function getBaseUrl() {
	const _location = window.location;
	return _location.protocol + '//' + _location.hostname + (_location.port? ':' +_location.port : '');
}
