import {get, set} from 'lodash';
import {isArrayWithValues, guid} from '../util/general-utils';

export const handleShowAlerts = ({action: {payload}, updateProperties}) => {
	const alerts = get(payload, 'alerts', []);

	if (!isArrayWithValues(alerts))
		return;

	const alertsWithIds = alerts.map(alert => {
		return set(alert, 'id', guid());
	});

	updateProperties({alerts: alertsWithIds});
};

export const handleAlertDismissed = ({action: {payload}, properties, updateProperties}) => {
	const alertId = get(payload, 'alertId', null);

	// If no alertId, we are dismissing the entire list of alerts. We want to clear them all
	if (!alertId)
		return updateProperties({alerts: []});

	const alerts = get(properties, 'alerts', []);
	const filteredAlerts = alerts.filter(alert => alert.id !== alertId);

	updateProperties({alerts: filteredAlerts});
};
