import {actionTypes, createCustomElement} from '@servicenow/ui-core';
import get from 'lodash/get';
import {view} from './card-view';
import styles from './card.scss';
import {FOCUS_CARD, NOW_DROPDOWN_PANEL_ITEM_CLICKED, OPEN_CARD_MOVE_MODAL_REQUESTED, MOVE_ACTIVITY_CLICKED} from '../../../constants/action-types';
import {ACTIVITY, SESSION_USER, USER_PREFERENCES} from '../../../constants/shared';
import {dropDownIdToActionMap} from './card-dropdown-config';
import {getSelectableProp, isArrayWithValues} from '../../../util/general-utils';
import {SN_CANVAS_SESSION, SN_CANVAS_USER_PREFS} from '../../canvas/constants/constants';

const handleCardActionDropdownClick = ({action, state, dispatch}) => {
	const {
		payload: {
			action: {
				id
			}
		},
		meta: {
			event: {
				path
			}
		}
	} = action;

	const triggerParent = isArrayWithValues(path) && path[0];
	const triggerElement = triggerParent && triggerParent.shadowRoot.querySelector('now-dropdown-custom-target');

	const actionData = {
		configType: ACTIVITY,
		data: state.properties.data,
		triggerElement
	};

	dispatch(dropDownIdToActionMap[id], actionData);
};

createCustomElement('sn-pd-card', {
	view,
	properties: {
		data: {
			reflect: true // necessary to make this prop available on cloned elem while dragging - from component-board
		},
		userData: {
			get default() {return getSelectableProp(SN_CANVAS_SESSION, SESSION_USER);}
		},
		userPrefs: {
			get default() {return getSelectableProp(SN_CANVAS_USER_PREFS, USER_PREFERENCES);}
		}
	},
	styles: [styles],
	actionHandlers: {
		[NOW_DROPDOWN_PANEL_ITEM_CLICKED]: handleCardActionDropdownClick,
		[MOVE_ACTIVITY_CLICKED]: {
			effect({dispatch, action:{payload}}) {
				const cardId = get(payload, 'data.sys_id', '');
				dispatch(OPEN_CARD_MOVE_MODAL_REQUESTED, {
					id: cardId 
				});
			}
		},
		[actionTypes.COMPONENT_RENDERED]: {
			effect({state, dispatch, action}) {
				const cardId = get(state, 'properties.data.sys_id', '');
				const cardHost = get(action, 'payload.host', '');

				if (cardId)
					dispatch(FOCUS_CARD, {cardId, cardHost});
			}
		}
	}
});

