import {isEqual} from 'lodash';
import {createCustomElement} from '@servicenow/ui-core';
import {snabbdom} from '@servicenow/ui-renderer-snabbdom';
import {actionHandlers, lifeCycleHandlers, updateUrl} from './sn-pd-router.effects';
import {defaultConfig} from './sn-pd-router.config';
import {resolveHref, shouldNavigateWithInApp} from './sn-pd-router.interceptors';
import {getSelectedContentFromUrl} from './sn-pd-router.urlParser';
import {getLocation} from './sn-pd-router.util';
import {URL_CONTENT_LOADED} from '../constants/constants';

const {origin} = getLocation();
export default createCustomElement('sn-pd-router', {
	type: snabbdom,
	view: () => null,
	initialState: {
		initialUrlLoaded: false
	},
	properties: {
		selectedContent: {
			default: null,
			onChange(val, preVal, dispatch){
				if (!isEqual(val, preVal)) {
					updateUrl(val, dispatch);
					dispatch(URL_CONTENT_LOADED, val);
				}
			}
		},
		urlContent: {
			default: null,
			onChange(val, dispatch){
				const {url, target} = val;
				const resolvedUrl = resolveHref(url);
				if (shouldNavigateWithInApp(resolvedUrl)){
					const selectedContent = getSelectedContentFromUrl(null, resolvedUrl.substring(origin.length));
					if (selectedContent)
						dispatch(URL_CONTENT_LOADED, selectedContent);
					else
						window.open(url, target);
				}
			}
		},
		routerConfig : {
			default : {defaultConfig: true, ...defaultConfig}
		}
	},
	actionHandlers : {...lifeCycleHandlers, ...actionHandlers}
});

