import * as ShareConst from '../constants/shared';
import {isObjectWithValues, isArrayWithValues} from '../util/general-utils';
import * as ActionTypes from '../constants/action-types';
import * as PillUtils from '../util/pill-utils';
import {closeModal} from '../util/modal-util';
import {confirmRearrangeActivityWithDependency, UNABLE_TO_MODIFY_ACTIVITY_MODAL_CONTENT} from '../constants/modal-constants';
import * as Selectors from '../util/selectors';
import {get, find, sortBy, isEmpty, values, uniqBy} from 'lodash';
import {getSortedLanes} from './lane-actions';
import {createStartRuleByName, getStartRules, getStartRuleIdByName} from '../components/configuration-components/config-utils';

export const handleAftermathOfAddOrDeleteCard = ({action:{payload}, dispatch, properties}) => {
	const type = get(payload, 'type', '');
	const movedCardId = get(payload, 'movedCardId', '');
	const movedCardOrder = get(payload, 'movedCardOrder');
	const stateCards = get(payload, 'cards');
	const cardsWithInvalidPills = get(properties, `activitiesDependedOn.${movedCardId}.dependents`);
	const lanes = Selectors.getLanesFromProperties(properties);

	let cardsWithUpdatedInputs = {...stateCards};

	if (cardsWithInvalidPills)
		cardsWithUpdatedInputs = PillUtils.removeInvalidPills(cardsWithInvalidPills, stateCards, [movedCardId]);

	const cardsToUpdate = resetOrderAfterAddOrDelete(cardsWithUpdatedInputs, movedCardId, movedCardOrder, type);

	if (cardsToUpdate.length) {
		const activityStartRules = getStartRules(ShareConst.ACTIVITY, properties);
		rearrangeCards(dispatch, cardsToUpdate, lanes, activityStartRules);
	}
};

export function resetOrderAfterAddOrDelete(cards, movedCardId, movedCardOrder, resetType) {
	if (!isObjectWithValues(cards))
		return [];

	const cardIds = Object.keys(cards);

	let cardsToUpdate = [];

	for (let i = 0; i < cardIds.length; i++) {
		const thisId = cardIds[i];
		const card = cards[thisId];
		const isMovedCard = thisId === movedCardId;

		if (card.order < movedCardOrder || isMovedCard)
			continue;

		const newOrder = resetType === ShareConst.DELETION ? card.order - 1 : card.order + 1;

		cardsToUpdate.push({...card, order: newOrder});
	}

	return cardsToUpdate;
}

/*
 * Handles drag and drop
 */
export const handleActivityRearrange = (card, data, dispatch, properties) => {
	const {laneId, itemId, dropIndex} = data;

	if (!isObjectWithValues(card) || !laneId || !itemId)
		return;

	const cardIsMovingWithinLane = card.lane_id === laneId;

	const activityStartRules = getStartRules(ShareConst.ACTIVITY, properties);
	const playbook = Selectors.getPlaybookFromProperties(properties);
	const configurationState = Selectors.getConfigurationStateFromProperties(properties);
	let lanes = {...get(playbook, 'lanes', {})};
	let cards = {...get(playbook, 'cards', {})};
	const cardSysId = card.sys_id;

	if (cardIsMovingWithinLane) {
		const thisLane = lanes[laneId];
		const thisLaneCards = thisLane && thisLane.cards;

		// if we haven't actually moved, just dropped in same position
		if (thisLaneCards && thisLaneCards[dropIndex] && thisLaneCards[dropIndex] === cardSysId)
			return;
	}

	if (!cardIsMovingWithinLane)
		lanes = removeCardFromOldLane(lanes, card.lane_id, itemId);

	const lane = updateLaneCards(lanes[laneId], itemId, dropIndex);
	lanes[laneId] = lane;

	// updating this here is if card is returned in cardsToUpdate below, the correct lane is present
	const updatedCard = {...card, lane_id: laneId};
	cards[cardSysId] = updatedCard;

	const sortedLanesArray = sortBy(Object.values(lanes), ['order']);
	let allCardIdsInOrderByLane = [];

	sortedLanesArray.map(lane => {
		if (isArrayWithValues(lane.cards))
			allCardIdsInOrderByLane = [...allCardIdsInOrderByLane, ...lane.cards];
	});

	const movedCardNewOrder = allCardIdsInOrderByLane.indexOf(card.sys_id) + 1;
	const activitiesDependedOn = Selectors.getActivitiesDependedOnFromProperties(properties);
	let movedCardsOwnInvalidPills = {};

	const isMovingUp = movedCardNewOrder < card.order;
	if (isMovingUp)
		movedCardsOwnInvalidPills = PillUtils.checkIfCardMovedAboveDependency(allCardIdsInOrderByLane, {...card}, activitiesDependedOn);

	const dependentsWithInvalidPills = PillUtils.getActivityDependentsByOrder(cardSysId, movedCardNewOrder, activitiesDependedOn);
	let cardsWithInvalidPills = {...dependentsWithInvalidPills};
	if (isObjectWithValues(get(movedCardsOwnInvalidPills, 'cardWithAllInputs')))
		cardsWithInvalidPills[cardSysId] = movedCardsOwnInvalidPills.cardWithAllInputs;
	const dataToSave = {playbook, cards, lanes, configurationState};

	if (isObjectWithValues(cardsWithInvalidPills)) {
		const errorMessage = PillUtils.createWarningMessage(cardsWithInvalidPills, UNABLE_TO_MODIFY_ACTIVITY_MODAL_CONTENT);
		const cardsWithInvalidPillsToClear = {dependentsWithInvalidPills, movedCardsOwnInvalidPills};
		const appendToPayload = {allCardIdsInOrderByLane, cardsWithInvalidPillsToClear, cardSysId, dataToSave};
		dispatch(ActionTypes.OPEN_MODAL_REQUESTED, {data: {...confirmRearrangeActivityWithDependency(errorMessage), appendToPayload}});
	} else
		saveAfterRearrange(allCardIdsInOrderByLane, updatedCard, dataToSave, activityStartRules, dispatch);
};


export function saveAfterRearrange(allCardIdsInOrderByLane, updatedCard, dataToSave, activityStartRules, dispatch) {
	const {playbook, cards, lanes, configurationState} = dataToSave;

	const cardsWithWrongStartRule = getFirstCardsInLanesWithWrongStartRule(lanes, cards, activityStartRules);
	const cardsWithWrongOrder = reorderTheItems(allCardIdsInOrderByLane, cards);
	// dupes from cardsWithWrongStartRule will be removed so the card with the correct order remains
	let cardsToUpdate = [...cardsWithWrongOrder, ...cardsWithWrongStartRule];
	cardsToUpdate = !isArrayWithValues(cardsToUpdate) ? [updatedCard] : uniqBy(cardsToUpdate, 'sys_id');

	cardsToUpdate.forEach(card => {
		let thisCard = {...card};
		thisCard.start_rule = cardIsFirstInLane(card, lanes) ? createStartRuleByName(activityStartRules, ShareConst.IMMEDIATE_START_RULE) : get(card, 'start_rule', '');
		cards[card.sys_id] = thisCard;
	});
	const updatedConfigState = updateConfigStateWithUpdatedCardInfo(cards, configurationState);
	// updating the playbook before saving prevents the UI from jumping around
	dispatch(ActionTypes.UPDATE_UI_AFTER_REARRANGE, {playbook: {...playbook, lanes, cards}, configurationState: updatedConfigState});
	rearrangeCards(dispatch, cardsToUpdate, lanes, activityStartRules);

	dispatch(ActionTypes.CLOSE_MODAL);
}

export const rearrangeActivityWithDependency = ({action:{payload}, properties, dispatch, updateState}) => {
	closeModal(updateState);

	const {allCardIdsInOrderByLane, cardsWithInvalidPillsToClear, cardSysId, dataToSave} = payload;
	const {dependentsWithInvalidPills, movedCardsOwnInvalidPills} = cardsWithInvalidPillsToClear;
	const {cards} = dataToSave;

	let cardsWithUpdatedInputs = {...cards};
	if (isObjectWithValues(dependentsWithInvalidPills))
		cardsWithUpdatedInputs = PillUtils.removeInvalidPills(dependentsWithInvalidPills, cards, [cardSysId]);

	if (isObjectWithValues(movedCardsOwnInvalidPills)) {
		const {cardWithAllInputs, idsToRemove} = movedCardsOwnInvalidPills;
		let cardWithAllInputsObject = {};
		cardWithAllInputsObject[cardSysId] = cardWithAllInputs;
		cardsWithUpdatedInputs = PillUtils.removeInvalidPills(cardWithAllInputsObject, cards, idsToRemove);
	}

	const updatedDataToSave = {...dataToSave, cards: cardsWithUpdatedInputs};

	const activityStartRules = getStartRules(ShareConst.ACTIVITY, properties);
	saveAfterRearrange(allCardIdsInOrderByLane, cards[cardSysId], updatedDataToSave, activityStartRules, dispatch);
};

/*
 * Reaches this if no sibling card in lane
 */
export const getCardOrderFromAdjacentLane = (lanes, laneId, cards) => {
	if (!isObjectWithValues(lanes))
		return 1;

	const currentLaneOrder = lanes[laneId] && lanes[laneId].order;
	const isFirstLane = currentLaneOrder === 1;

	if (isFirstLane)
		return 1;

	// since we are not the first lane, always try to get a lane before the current lane
	const adjacentLaneWithCards = getClosestLaneWithCards(lanes, currentLaneOrder - 1);

	if (!adjacentLaneWithCards)
		return 1;

	// get last card of preceding lane
	const adjacentLaneCards = adjacentLaneWithCards.cards;
	const cardIdToLookUp = adjacentLaneWithCards && adjacentLaneCards[adjacentLaneCards.length -1];
	const lookUpCard = cards[cardIdToLookUp];
	const lookUpCardOrder = get(lookUpCard, 'order', '');

	return lookUpCardOrder + 1;
};

/*
 * Does the previous lane have cards?  If not go back until we find a lane with cards or run out of lanes
 */
export function getClosestLaneWithCards(lanes, orderToCheck) {
	if (!isObjectWithValues(lanes))
		return false;

	const adjacentLane = find(Object.values(lanes), {'order': orderToCheck});

	if (!adjacentLane)
		return false;

	const adjacentLaneCards = get(adjacentLane, 'cards', []);

	if (!isArrayWithValues(adjacentLaneCards))
		return getClosestLaneWithCards(lanes, orderToCheck - 1);
	else
		return adjacentLane;
}

/*
 * Updates array of card ids belonging to a lane
 */
export function updateLaneCards(lane, cardId, dropIndex) {
	if (!isObjectWithValues(lane))
		return {};

	if (!cardId)
		return lane;

	let laneCards = isArrayWithValues(lane.cards) ? [...lane.cards] : [];
	const cardIndex = laneCards.indexOf(cardId);

	if (cardIndex < 0) // card is new to lane
		laneCards.splice(dropIndex, 0, cardId);
	else
		laneCards = reorderLaneCardArray(laneCards, dropIndex, cardIndex, cardId);

	return {...lane, cards: laneCards};
}

export function removeCardFromOldLane(lanes, laneId, cardId) {
	if (!isObjectWithValues(lanes))
		return {};

	let newLanes = {...lanes};
	let lane = {...newLanes[laneId]};

	if (!isObjectWithValues(lane))
		return lanes;

	let laneCards = [...lane.cards];

	laneCards = laneCards.filter(id => id !== cardId);
	lane = {...lane, cards: laneCards};
	newLanes[laneId] = lane;

	return newLanes;
}

export function reorderLaneCardArray(items, newIndex, oldIndex, item) {
	let newItems = isArrayWithValues(items) ? [...items] : [];
	if (!item || isNaN(newIndex) || isNaN(oldIndex))
		return newItems;

	const itemsLength = newItems.length;

	newItems.splice(oldIndex, 1);

	if (newIndex === itemsLength - 1)
		newItems.push(item);
	else
		newItems.splice(newIndex, 0, item);

	return newItems;
}

export function reorderTheItems(allIdsInOrder, allItemsObject) {
	let itemsToUpdate = [];

	if (!isArrayWithValues(allIdsInOrder) || !isObjectWithValues(allItemsObject))
		return itemsToUpdate;

	allIdsInOrder.map((id, index) => {
		const item = allItemsObject[id];
		const expectedOrder = index + 1;

		if (item && item.order != expectedOrder)
			itemsToUpdate.push({...item, order: expectedOrder});
	});

	return itemsToUpdate;
}

export function getFirstCardsInLanesWithWrongStartRule(lanes, cards, activityStartRules) {
	let cardsWithWrongStartRule = [];

	if (!isObjectWithValues(cards) || !isObjectWithValues(lanes))
		return cardsWithWrongStartRule;

	for (let id in lanes) {
		const firstCardInLaneId = get(lanes, `${id}.cards[0]`);

		if (!firstCardInLaneId)
			continue;

		const firstCard = get(cards, `${firstCardInLaneId}`, {});
		const firstCardStartRule = get(firstCard, 'start_rule.value', '');
		const immediateStartRuleId = getStartRuleIdByName(activityStartRules, ShareConst.IMMEDIATE_START_RULE);
		if (firstCard && firstCardStartRule !== immediateStartRuleId)
			cardsWithWrongStartRule.push(firstCard);
	}

	return cardsWithWrongStartRule;
}

export function cardIsFirstInLane(card, lanes) {
	if (!isObjectWithValues(card) || !isObjectWithValues(lanes))
		return false;

	const laneId = get(card, 'lane_id');
	const cardId = get(card, 'sys_id', '');
	const laneCards = get(lanes, `${laneId}.cards`, []);

	return laneCards.indexOf(cardId) === 0;
}


/*
 * Remove pills and reset order on all lanes and cards and persist those changes
 */
export function removePillsAndReorderAfterLaneDeletion({action, properties, dispatch, updateProperties}) {
	const modifiedPlaybook = get(action, 'payload.modifiedPlaybook', {});
	const deletedCards = get(action, 'payload.deletedCards', []);
	const cardsWithDependents = Selectors.getActivitiesDependedOnFromProperties(properties);
	const affectedCards = PillUtils.getActivitiesAffectedByLaneDeletion(cardsWithDependents, deletedCards);
	const modifiedCards = PillUtils.removeInvalidPills(affectedCards, modifiedPlaybook.cards, deletedCards);

	// reorderTheItems returns an empty array if nothing was reordered
	const reorderedLanes = reorderTheItems(getSortedLanes(modifiedPlaybook.lanes), modifiedPlaybook.lanes);
	const reorderedCards = reorderTheItems(Object.keys(modifiedCards), modifiedCards);
	const updatedCards = isArrayWithValues(reorderedCards) ? reorderedCards : values(modifiedCards);

	const laneStartRules = getStartRules(ShareConst.LANE, properties);
	// If lanes were reordered, update them
	if (isArrayWithValues(reorderedLanes)) {
		reorderedLanes.forEach(({order, sys_id: sysId}) => {
			const start_rule = getStartRuleIdByName(laneStartRules, ShareConst.IMMEDIATE_START_RULE);
			const payload = order === 1 ? {order, sysId, start_rule} : {order, sysId};
			dispatch(ActionTypes.SAVE_AFTER_LANE_REARRANGE_REQUESTED, payload);
		});
	}

	// If pills were removed or items were reordered
	if (!isEmpty(affectedCards) || isArrayWithValues(reorderedCards)) {
		const lanes = reorderedLanes.length ? reorderedLanes : modifiedPlaybook.lanes;
		const activityStartRules = getStartRules(ShareConst.ACTIVITY, properties);
		rearrangeCards(dispatch, updatedCards, lanes, activityStartRules);
	} else
		// No additional changes were made (can happen when the end lane is deleted), so just updateProperties
		updateProperties({playbook: {...modifiedPlaybook}});
}

export function updateConfigStateWithUpdatedCardInfo(updatedCards, configurationState) {
	const cardOpenedInConfig = getCardOpenedInConfig(updatedCards, configurationState);
	if (!cardOpenedInConfig)
		return configurationState;

	const fields = [...get(configurationState, 'config.fields', [])];

	ShareConst.CONFIG_PANEL_EDITABLE_FIELDS.forEach(fieldName => {
		const field = fields.find((field) => field.name === fieldName);
		if (!field)
			return;

		if (fieldName === 'start_rule') {
			field.value = cardOpenedInConfig[fieldName].value;
			field.displayValue = cardOpenedInConfig[fieldName].displayValue;
		} else
			field.value = cardOpenedInConfig[fieldName];
	});

	return {...configurationState, config: {...configurationState.config, fields}};
}

export function getCardOpenedInConfig(cards, configState) {
	const configItemId = get(configState, 'config.sys_id', '');
	const cardIds = Object.keys(cards);

	if (!configItemId || !configState.panelOpen || !configState.type || configState.type !== ShareConst.ACTIVITY || !cardIds.includes(configItemId))
		return null;

	return {...cards[configItemId]};
}

function rearrangeCards(dispatch, cardsToUpdate, lanes, activityStartRules) {
	dispatch(ActionTypes.SAVE_AFTER_ACTIVITY_REARRANGE_REQUESTED, {
		activities: cardsToUpdate.map(card => ({
			order: card.order,
			sys_id: card.sys_id,
			lane: card.lane_id,
			inputs: PillUtils.getGlideVarFieldValues(card),
			label: card.label,
			description: card.description,
			start_rule: cardIsFirstInLane(card, lanes) ? getStartRuleIdByName(activityStartRules, ShareConst.IMMEDIATE_START_RULE) : get(card, 'start_rule.value', '')
		}))
	});
}
