import '@servicenow/now-modal';

const modalView = (state) => {
	const {
		content,
		footerActions,
		headerLabel,
		iframe,
		opened,
		size,
		appendToPayload = {}
	} = state.properties;

	const renderSlot = () => {
		return content ? null : <slot />;
	};

	return (
		<now-modal
			content={content}
			footer-actions={footerActions}
			header-label={headerLabel}
			content-full-width={!!iframe}
			opened={opened}
			size={size}
			append-to-payload={appendToPayload}
			manage-opened
		>
			{renderSlot()}
		</now-modal>
	);
};

export {modalView};
