import {createHttpEffect} from '@servicenow/ui-effect-http';
import * as ActionTypes from '../constants/action-types';
import * as Shared from '../constants/shared';

export const requestScopesEffect = createHttpEffect('api/now/ui/concoursepicker/application', {
	method: Shared.HTTP_GET,
	dataParam: Shared.DATA_PARAM,
	successActionType: ActionTypes.REQUEST_USER_SCOPES_SUCCEEDED,
	errorActionType: ActionTypes.NETWORK_ERROR_OCCURRED
});

export const savePlaybookConfigEffect = createHttpEffect(Shared.GRAPHQL_ENDPOINT, {
	...Shared.DEFAULT_HTTP_EFFECT_CONFIG,
	successActionType: ActionTypes.SAVE_PLAYBOOK_CONFIGURATION_SUCCEEDED
});

// creates a trigger instance
export const createPlaybookEffect = createHttpEffect(Shared.GRAPHQL_ENDPOINT, {
	...Shared.DEFAULT_HTTP_EFFECT_CONFIG,
	successActionType: ActionTypes.CREATE_PLAYBOOK_SUCCEEDED
});

// only creates a playbook (process definition) record
export const createBasicPlaybookEffect = createHttpEffect(Shared.GRAPHQL_ENDPOINT, {
	...Shared.DEFAULT_HTTP_EFFECT_CONFIG,
	successActionType: ActionTypes.CREATE_BASIC_PLAYBOOK_RECORD_SUCCEEDED
});

// get additional information for a field like it's choices and dependent fields
export const requestFieldMetaEffect = createHttpEffect(Shared.FIELD_META_API_ENDPOINT, {
	method: Shared.HTTP_GET,
	pathParams: ['table'],
	dataParam: 'fieldMeta',
	successActionType: ActionTypes.REQUEST_FIELD_META_SUCCEEDED,
	errorActionType: ActionTypes.NETWORK_ERROR_OCCURRED
});


export const getDotWalkData = createHttpEffect('api/now/pddotwalk/dotwalk/:id', {
	method: Shared.HTTP_GET,
	pathParams: ['id'],
	successActionType: ActionTypes.REQUEST_DOT_WALK_DATA_SUCCEEDED,
	errorActionType: ActionTypes.NETWORK_ERROR_OCCURRED
});
