import get from 'lodash/get';
import {createPreSourceBehavior} from 'sn-uxpage-presource';

const USER_DATA_KEY = 'userData';
const USER_PREFS_KEY = 'userPrefs';

export const preUserData = createPreSourceBehavior('sn-pd-session', {
	name: USER_DATA_KEY,
	transform: data => get(data, 'data.GlideDomain_Query.user', {})
});

export const preUserPreferences = createPreSourceBehavior('sn-pd-user-pref', {
	name: USER_PREFS_KEY,
	transform: data => get(data, 'data.GlideDomain_Query.user.preferences', [])
});
