import {get, intersection} from 'lodash';
import {getKeyValuePairs} from '../../../util/transforms';
import {getUserRoles} from '../../../util/selectors';
import {CREATE_TRIGGER_ROLES} from '../../../constants/shared';

export const validateLabelApplication = (state) => {
	const {userScopes, newPlaybookForm} = state;
	const {label, application} = getKeyValuePairs(newPlaybookForm);

	// label and application can't be empty
	if (!label || !application)
		return false;

	// userScopes is an array
	if (!Array.isArray(userScopes))
		return false;

	// application value is one of the userScopes
	return userScopes.some(s => s.value && s.value === application);
};

export const validateTrigger = (state) => {
	const {triggers, newPlaybookForm} = state;
	const {trigger} = getKeyValuePairs(newPlaybookForm);

	// trigger can't be empty
	if (!trigger)
		return false;

	// triggers is an array
	if (!Array.isArray(triggers))
		return false;

	// trigger value is one of the triggers
	return triggers.some(t => t.value && t.value === trigger);
};

export const findCurrentScopeDisplayValue = (filteredUserScopes, currentScopeSysId) => {
	if (!Array.isArray(filteredUserScopes) || !currentScopeSysId)
		return 'Global';
	return get(filteredUserScopes.find(scope => scope.value && scope.value === currentScopeSysId), 'displayValue', 'Global');
};

export const canUserCreateTrigger = (userData) => {
	const userRoles = getUserRoles(userData);
	return intersection(userRoles, CREATE_TRIGGER_ROLES).length > 0;
};
