/**
 * Transforms field data for now-select field
 *
 * @param fieldMeta
 * @returns {{id: *, label: *}[]}
 */
export function transformFieldMetaForNowSelect(fieldMeta) {
	const fields = Object.keys(fieldMeta).sort();

	return fields.map(field => {
		const {name: id, label} = fieldMeta[field];
		return {id, label};
	});
}

/**
 * Encoded Value:
 * field1=field1value^field2=field2value^field3=field3value
 *
 * Decoded Value:
 * [
 * {
 *     name: field1
 *     label: Field 1,
 *     value: field1value
 *     variableValue: field1value
 *     type: 'string'
 * }
 *
 * ]
 *
 * @param encodedValue
 * @param fieldMeta
 */
export function decodeTemplateValue(encodedValue, fieldMeta) {
	if (!encodedValue)
		return;

	const fieldDelimiter = '^';
	const valueDelimiter = '=';
	const endQueryDelimiter = 'EQ';

	const fields = encodedValue.split(fieldDelimiter);
	const results = [];

	fields.forEach(field => {
		//this check is done here in to handle cases where ^ is the last character,
		//resulting in an empty array split
		if (field && field !== endQueryDelimiter) {
			const pairs = field.split(valueDelimiter);
			const [key, value] = pairs;
			// combine field meta information like type into the decodedValues
			const meta = fieldMeta[key];

			results.push({
				name: key,
				value,
				...meta
			});
		}
	});

	return results;
}

/**
 * Takes a decodedValue object and converts it into a template value encoded string to send
 * to the database.
 *
 * @param decodedValue
 * @returns {string}
 */
export function encodeTemplateValue(decodedValue) {
	let encodedValue = '';

	decodedValue.forEach(field => {
		const {name, value} = field;

		//can be empty strings for new fields or non-selections which shouldn't be added to the server value
		if (name && value)
			encodedValue += `${name}=${value}^`;
	});

	return encodedValue;
}

/**
 * Given an array of field objects with names, remove a field based on an input of field name.
 *
 * @param decodedValue
 * @param indexToRemove
 * @returns {*}
 */
export function removeRow(decodedValue, indexToRemove) {
	let rows = decodedValue.filter((field, idx) => idx !== indexToRemove);

	// always retain 1 row at all times
	if (rows.length === 0)
		rows = addRow(rows);

	return rows;
}

/**
 * Add an empty row to the decodedValue object
 *
 * @param decodedValue
 * @returns {*[]}
 */
export function addRow(decodedValue) {
	return [...decodedValue, {name: '', value: ''}];
}
