import {get} from 'lodash';
import '@servicenow/now-alert';
import * as NewProcessConsts from './creation-constants';
import {createProcessDetailsForm} from './process-details-form';
import {createSelectTriggerForm} from './select-trigger-form';

export const creationForm = (state, dispatch) => {
	const renderStep = () => {
		const modalStep = get(state, 'modal.step');

		if (modalStep === NewProcessConsts.SELECT_TRIGGER_STEP)
			return createSelectTriggerForm(state, dispatch);

		return createProcessDetailsForm(state);
	};

	return (
		<div id='new-playbook-form-container'>
			{renderStep()}
		</div>
	);
};
