import {BEHAVIOR_NAME} from 'sn-component-error-handler';

export default (derivedState) => {
	const {selectedContent, urlContent} = derivedState.properties;
	const ROUTER_CONFIG = {
		name: {value: 'Process Automation Designer Router Configuration'},
		landing_path: {value: 'home'},
		query_params: {value: false},
		default_route: {value: '{"page":"page_id"}'},
		routes: {value: null}
	};

	return (
		<div id="pd-root">
			<div id="pd-providers">
				<sn-pd-router
					selected-content={selectedContent}
					url-content={urlContent}
					router-config={ROUTER_CONFIG}
				>
				</sn-pd-router>

				<slot name="session" />
				<sn-canvas-error-modal {...derivedState.behaviors[BEHAVIOR_NAME]}/>
			</div>
			<div id="pd-preferences">
				<slot name="preferences" />
			</div>
			<div id="pd-content">
				<slot name="header" />
				<slot name="main" />
			</div>

		</div>
	);
};
