import {get} from 'lodash';
import {CreationModal, TriggerInstanceModal} from './create-new-process/creation-modal';
import * as CreationConsts from './create-new-process/creation-constants';
import * as ActionTypes from '../../constants/action-types';
import {CREATE_NEW, PLAYBOOK_TABLE} from '../../constants/shared';
import {validateLabelApplication, validateTrigger, findCurrentScopeDisplayValue} from './create-new-process/util';
import {isArrayWithValues, responseHasErrorsOrNoData} from '../../util/general-utils';
import {getKeyValuePairs, formatScopeData} from '../../util/transforms';
import {playbookCreation, basicPlaybookCreation} from '../../datasource/graphql/playbook-insert';

export const handleItemSelected = ({action: {stopPropagation}}) => {
	// stop propagation so it won't be captured by canvas router.
	stopPropagation();
};

export const handleRowClicked = ({action: {stopPropagation, payload}, dispatch}) => {
	stopPropagation();
	const table = get(payload, 'table', '');
	const sysId = get(payload, 'row.sys_id.value', '');

	if (sysId) {
		if (table === PLAYBOOK_TABLE)
			dispatch(ActionTypes.ITEM_SELECTED, {type: 'page', page_id: 'designer', params: {sysId}});
		else
			window.open(`/${table}.do?sys_id=${sysId}`, '_table_record');
	}
};


///// CREATION FUNCTIONS /////

export const startTriggerCreationPath = ({state, dispatch}) => {
	const newPlaybookForm = getKeyValuePairs(get(state, 'newPlaybookForm'));
	const playbookObject = {
		label: get(newPlaybookForm, 'label'),
		application: get(newPlaybookForm, 'application'),
		description: get(newPlaybookForm, 'description')
	};

	// first we need to create the process so we can use the id for the instance
	dispatch(ActionTypes.CREATE_BASIC_PLAYBOOK_RECORD_REQUESTED, playbookObject);
};

/*
 * After a basic process definition record is created in the trigger instance creation path (above),
 * get the sys_id from the response so that we can create a trigger instance record.
 */
export const handleSaveAndConfigureInstance = ({action: {payload}, state, updateState, dispatch}) => {
	// update state with the new process def id
	const newProcessId = get(payload, 'data.GlideRecord_Mutation.insert_sys_pd_process_definition.sys_id.value', '');
	updateState({selectedPlaybook: newProcessId});

	// create a trigger instance with the new process id and the trigger type selected in the modal
	const triggerType = get(state, 'newPlaybookForm.triggerType.value');
	dispatch(ActionTypes.CREATE_TRIGGER_INSTANCE_REQUESTED, {trigger_type: triggerType, process_definition: newProcessId});
};

/*
 * After an instance has been created in the background, open the modal so the conditions can be edited
 */
export const handleTriggerInstanceCreated = ({action: {payload}, state, updateState}) => {
	const triggerInstanceId = get(payload, 'data.GlideRecord_Mutation.insert_sys_pd_trigger_instance.sys_id.value', '');
	updateState({selectedTrigger: triggerInstanceId, modal: {...state.modal, step: 'process_conditions'}});
	openModal(TriggerInstanceModal(triggerInstanceId, 'pad_view', true, CreationConsts.TRIGGER_INSTANCE_HEADER), updateState);
};

export const handleIframeTriggerDefNext = ({action: {payload}, state, updateState}) => {
	const {sysId: value, label: displayValue} = payload;
	const trigger = {value, displayValue};
	updateState({selectedTriggerDefinition: trigger.value});

	updateState({modal: {...state.modal, step: 'choose-trigger-advanced', headerLabel: CreationConsts.TRIGGER_TEMPLATE_CONDITIONS_HEADER}});
};

// Modal Step 1
export const handleOpenNewPlaybookModal = ({action: {stopPropagation, payload}, dispatch}) => {
	stopPropagation();
	const actionName = get(payload, 'action.name');
	if (CREATE_NEW === actionName)
		dispatch(ActionTypes.REQUEST_USER_SCOPES);
};

export const getSelectTriggerModalButtons = (state, selectionType) => {
	const GO_TO_DESIGNER_BUTTON = {label: CreationConsts.GO_TO_DESIGNER_BUTTON_TEXT, variant: 'primary', clickActionType: ActionTypes.NEW_PLAYBOOK_MODAL_SAVE, disabled: true};
	const EDIT_TRIGGER_CONDITIONS_BUTTON = {label: CreationConsts.TRIGGER_CONDITION_BUTTON_TEXT, variant: 'primary', clickActionType: ActionTypes.START_TRIGGER_INSTANCE_CREATION_PATH, disabled: true};
	const BACK_BUTTON = {label: CreationConsts.BACK_TO_PROCESS_BUTTON_TEXT, variant: 'secondary', clickActionType: ActionTypes.RETURN_TO_PROCESS_DETAILS, disabled: false};

	if (!selectionType)
		selectionType = state.triggerSelectionType;

	if (selectionType === CreationConsts.TRIGGER) {
		const fieldValue = get(state, `newPlaybookForm.${selectionType}.value`, null);
		return [{...GO_TO_DESIGNER_BUTTON, disabled: !fieldValue}, {...BACK_BUTTON}];
	}

	if (selectionType === CreationConsts.TRIGGER_TYPE_SELECTION) {
		const fieldValue = get(state, `newPlaybookForm.${selectionType}.value`, null);
		return [{...EDIT_TRIGGER_CONDITIONS_BUTTON, disabled: !fieldValue}, {...BACK_BUTTON}];
	}

	return [{...EDIT_TRIGGER_CONDITIONS_BUTTON, disabled: true}, {...BACK_BUTTON}];
};

// Modal Step 2
export const handleOpenTriggerSelectionModal = ({state, action, updateState, dispatch}) => {
	action.stopPropagation();

	const modalElement = get(action, 'meta.event.path[0]', null);
	const modalButtons = getSelectTriggerModalButtons(state);

	updateState({
		modal: {
			...state.modal,
			headerLabel: CreationConsts.SELECT_TRIGGER_LABEL,
			footerActions: modalButtons,
			step: CreationConsts.SELECT_TRIGGER_STEP
		}
	});

	if (modalElement)
		dispatch(ActionTypes.FOCUS_MODAL_CLOSE_BUTTON, {modalElement});
};

export const returnToProcessDetails = ({updateState}) => {
	openModal({...CreationModal(true), step: 'new-pd'}, updateState);
};

export const cleanUpRecords = ({dispatch, updateState, state}) => {
	const {selectedPlaybook, selectedTriggerDefinition} = state;

	if (selectedPlaybook) {
		dispatch(ActionTypes.DELETE_PROCESS_REQUESTED, {sysId: selectedPlaybook});
		updateState({selectedPlaybook: ''});
	}

	if (selectedTriggerDefinition) {
		dispatch(ActionTypes.DELETE_TRIGGER_DEF_REQUESTED, {sysId: selectedTriggerDefinition});
		updateState({selectedTriggerDefinition: ''});
	}
};

export const handleCloseNewPlaybookModal = ({action: {stopPropagation}, dispatch, state, updateState}) => {
	stopPropagation();

	cleanUpRecords({dispatch, updateState, state});
	updateState({path: 'newPlaybookForm', value: {}, operation: 'set'});

	dispatch(ActionTypes.REFRESH_PLAYBOOK_LIST_REQUESTED);
	closeModal(state, updateState);
};

export const returnToTriggerSelection = ({action: {stopPropagation}, dispatch, updateState, state}) => {
	stopPropagation();

	dispatch(ActionTypes.REQUEST_USER_TRIGGERS);

	// use the previous trigger check to add the new buttons that need to be at this step.
	const modalButtons = getSelectTriggerModalButtons(state);

	updateState({
		modal: {
			...CreationModal(),
			opened: true,
			headerLabel: CreationConsts.SELECT_TRIGGER_LABEL,
			footerActions: modalButtons,
			step: CreationConsts.SELECT_TRIGGER_STEP
		}
	});
};

export const cleanUpRecordsAndGoBack = ({action: {stopPropagation}, dispatch, updateState, state}) => {
	stopPropagation();

	returnToTriggerSelection({action: {stopPropagation}, dispatch, updateState, state});
	cleanUpRecords({action: {stopPropagation}, dispatch, updateState, state});
};

export const clearStateValues = (updateState) => {
	updateState({modal: {}, newPlaybookForm: {}, selectedTrigger: '', selectedTriggerDefinition: '', selectedPlaybook: ''});
};

export const handleIframeTriggerDefSubmission = ({action: {stopPropagation}, dispatch, updateState, state}) => {
	stopPropagation();

	updateState({newPlaybookForm: {...state.newPlaybookForm, trigger: {value: get(state, 'selectedTriggerDefinition')}}});
	dispatch(ActionTypes.NEW_PLAYBOOK_MODAL_SAVE);
};

export const handleGoToDesigner = ({state, updateState, dispatch}) => {
	closeModal(state, updateState);

	// open details of newly created playbook in a new browser tab
	const {selectedPlaybook: sysId} = state;
	if (sysId) {
		clearStateValues(updateState);
		dispatch(ActionTypes.ITEM_SELECTED, {type: 'page', page_id: 'designer', params: {sysId}});
	}
};

export const handleNewPlaybookFormFieldChange = ({state, action: {stopPropagation, payload}, dispatch, updateState}) => {
	stopPropagation();
	const {name, value, displayValue} = payload;
	const {triggerSelectionType} = state;

	// When a trigger type or trigger definition field is changed, we need to set the trigger selection type accordingly in order
	//to evaluate which buttons to render etc. Field names map directly to field selection types.
	const isTypeOrDefinitionField = name === CreationConsts.TRIGGER_TYPE_SELECTION ||
		name === CreationConsts.TRIGGER;

	if (isTypeOrDefinitionField && triggerSelectionType !== name)
		dispatch(ActionTypes.TRIGGER_SELECTION_TYPE_CHANGED, {value: name});

	updateState({path: `newPlaybookForm.${name}`, value: {value, displayValue: displayValue}, operation: 'set'});

	if (name === CreationConsts.TRIGGER)
		dispatch(ActionTypes.NEW_PLAYBOOK_MODAL_UPDATE_STEP_TWO_BUTTONS, {field: CreationConsts.TRIGGER});
	else
		dispatch(ActionTypes.NEW_PLAYBOOK_MODAL_TOGGLE_STEP_ONE_NEXT_BUTTON_STATUS);
};

export const handleOpenIFrameTriggerInstance = ({state, action: {payload}, updateState}) => {
	const {processDefSysId, triggerInstanceSysId} = payload;
	updateState({selectedPlaybook: processDefSysId, selectedTrigger: triggerInstanceSysId, modal: {...state.modal, step: 'process_conditions'}});
	openModal(TriggerInstanceModal(triggerInstanceSysId, 'pad_view', true, CreationConsts.TRIGGER_INSTANCE_HEADER), updateState);
};

// this could be used by either applications or triggers
export const handleAppTypeAheadFilterChanged = (coeffects) => {
	const {action: {stopPropagation, payload},
		state: {userScopes, triggers, newPlaybookForm}, dispatch, updateState} = coeffects;
	stopPropagation();
	const {name, value} = payload;

	if (name === CreationConsts.APPLICATION) {
		if (!isArrayWithValues(userScopes))
			return;

		const filteredUserScopes = !value ? userScopes : userScopes.filter(scope => {
			return scope.displayValue.toLowerCase().includes(value.toLowerCase());
		});

		updateState({
			filteredUserScopes,
			newPlaybookForm: {...newPlaybookForm, application: {value, displayValue: value}}
		});

		dispatch(ActionTypes.NEW_PLAYBOOK_MODAL_TOGGLE_STEP_ONE_NEXT_BUTTON_STATUS);
	} else if (name === CreationConsts.TRIGGER) {
		if (!isArrayWithValues(triggers))
			return;

		const filteredTriggers = !value ? triggers : triggers.filter(trigger => {
			return trigger.displayValue.toLowerCase().includes(value.toLowerCase());
		});

		updateState({
			filteredTriggers,
			newPlaybookForm: {...newPlaybookForm, trigger: {value, displayValue: value}}
		});

		dispatch(ActionTypes.NEW_PLAYBOOK_MODAL_UPDATE_STEP_TWO_BUTTONS, {field: CreationConsts.TRIGGER});
	}
};

export const handleToggleStepOneNextButtonStatus = ({state, updateState}) => {
	updateState({
		path: 'modal.footerActions[0].disabled',
		value: !validateLabelApplication(state),
		operation: 'set'
	});
};

export const handleTriggerSelectionTypeChange = ({state, updateState, action: {stopPropagation, payload}}) => {
	stopPropagation();

	const {triggerDefOptions, triggerTypeOptions, modal} = state;

	const triggerSelectionType = get(payload, 'value', null);
	let newTriggerDefOptions = [...triggerDefOptions];
	let newTriggerTypeOptions = [...triggerTypeOptions];
	let newModal = {...modal};

	//This works around a bug in the seismic radio buttons where setting a value,
	//would not properly override the "checked" value in the options. Instead, we control the checked
	//manually in the options set and pass it directly into the component. This should work even when the original
	//radio buttons gets fixed by the design system team.
	if (triggerSelectionType) {
		if (triggerSelectionType === CreationConsts.TRIGGER) {
			newTriggerDefOptions[0].checked = true;
			newTriggerTypeOptions[0].checked = false;
		}

		if (triggerSelectionType === CreationConsts.TRIGGER_TYPE_SELECTION) {
			newTriggerDefOptions[0].checked = false;
			newTriggerTypeOptions[0].checked = true;
		}

		//handle modal buttons showing, hiding, and disabling.
		newModal.footerActions = getSelectTriggerModalButtons(state, triggerSelectionType);
		updateState({triggerSelectionType, triggerTypeOptions: newTriggerTypeOptions, triggerDefOptions: newTriggerDefOptions, modal: newModal});
	}
};

/*
 * Step two in the creation process is selecting or creating a trigger.  This function updates
 * the next button based on if the user has selected an existing trigger or a trigger type.
 * If a trigger is selected, the next step is to go to the designer.  If a trigger type is selected,
 * they are creating a trigger instance and must then select a table and conditions.
 */
export const handleToggleStepTwoNextButtonStatus = ({state, updateState}) => {
	updateState({
		path: 'modal.footerActions[0].disabled',
		value: !validateTrigger(state),
		operation: 'set'
	});
};

export const handleCreateNewPlaybook = ({action: {stopPropagation}, dispatch, state}) => {
	stopPropagation();

	const trigger = get(state, 'newPlaybookForm.trigger.value');

	if (validateLabelApplication(state) && trigger)
		dispatch(ActionTypes.CREATE_PLAYBOOK_REQUESTED, getKeyValuePairs(state.newPlaybookForm));
};

// In order to handle cross scope application field setting we must use
// the rest request api instead of graphql.
export const handleCreatePlaybookRequested = ({action: {payload}, dispatch}) => {
	const data = playbookCreation({...payload});
	dispatch(ActionTypes.CREATE_PLAYBOOK, {data, sysparm_transaction_scope: payload.application});
};

export const handleCreateBasicPlaybookRequested = ({action: {payload}, dispatch}) => {
	const data = basicPlaybookCreation({...payload});
	dispatch(ActionTypes.CREATE_BASIC_PLAYBOOK, {data, sysparm_transaction_scope: payload.application});
};

export const handleCreatePlaybookSucceeded = ({action: {payload}, dispatch, updateState, state}) => {
	if (responseHasErrorsOrNoData(payload, dispatch))
		return;

	closeModal(state, updateState);

	const sysId = get(payload, 'data.GlideProcessAutomation_Mutation.createProcessDefinition.sys_id');

	if (sysId) {
		clearStateValues(updateState);
		dispatch(ActionTypes.ITEM_SELECTED, {type: 'page', page_id: 'designer', params: {sysId}});
	}
};

export function openModal(data, updateState) {
	const modalData = {...data, opened: true};
	updateState({modal: modalData});
}

export function closeModal(state, updateState) {
	updateState({
		modal: {},
		newPlaybookForm: {},
		...resetTriggerSelectionData(),
		filteredUserScopes: state.userScopes,
		filteredTriggers: state.triggers
	});
}

function resetTriggerSelectionData() {
	return {
		triggerSelectionType: null,
		triggerTypeOptions: [{...CreationConsts.TRIGGER_TYPE_OPTIONS}],
		triggerDefOptions: [{...CreationConsts.TRIGGER_DEF_OPTIONS}]
	};
}

export const handleComponentBootstrapped = ({dispatch}) => {
	dispatch(ActionTypes.REQUEST_USER_TRIGGERS);
};

export const updateUserScopes = ({action: {payload}, updateState}) => {
	const scopes = get(payload, 'result.list');
	const formattedScopes = formatScopeData(scopes);
	const currentScope = get(payload, 'result.current');
	updateState({
		userScopes: formattedScopes,
		filteredUserScopes: formattedScopes,
		currentScope,
		newPlaybookForm: {
			application: {
				value: currentScope,
				displayValue: findCurrentScopeDisplayValue(formattedScopes, currentScope)
			}
		}
	});

	openModal({...CreationModal(), step: 'new-pd'}, updateState);
	updateState({
		triggerTypeOptions: [{...CreationConsts.TRIGGER_TYPE_OPTIONS}],
		triggerDefOptions: [{...CreationConsts.TRIGGER_DEF_OPTIONS}]
	});
};

export const updateUserTriggersAndTypes = ({action: {payload}, updateState, dispatch}) => {
	if (responseHasErrorsOrNoData(payload, dispatch))
		return;

	const triggers = get(payload, 'data.GlideProcessAutomation_Query.getAvailableTriggers.triggers', []);
	let triggerTypes = get(payload, 'data.GlideRecord_Query.triggerTypes._results', []);
	triggerTypes = triggerTypes.map(type => getKeyValuePairs(type));

	updateState({
		triggerTypes,
		triggers,
		filteredTriggers: triggers,
	});
};

export const handleRefreshPlaybookList = ({action:{stopPropagation}, updateState, dispatch}) => {
	stopPropagation();
	dispatch(ActionTypes.REQUEST_USER_TRIGGERS);
	updateState({listRefreshRequested: {timestamp: Date.now()}});
};
