import {createCustomElement} from '@servicenow/ui-core';
import styles from './designer.scss';
import {designer} from './designer-view';
import * as Playbook from '../../actions/playbook-actions';
import * as DnDActions from '../../actions/dnd-actions';
import * as ActionTypes from '../../constants/action-types';
import * as Lane from '../../actions/lane-actions';
import * as Modal from '../../actions/modal-actions';
import * as ConfigurationPanel from '../../actions/configuration-actions';
import * as Card from '../../actions/card-actions';
import * as Rearrange from '../../actions/rearrange-actions';
import * as Consts from '../../constants/shared';
import {getSelectableProp} from '../../util/general-utils';
import {SN_CANVAS_SESSION} from '../canvas/constants/constants';

createCustomElement('sn-pd-designer', {
	view: designer,
	initialState: {
		activityPicker: '',
		actionPicker: '',
		focusCardId: ''
	},
	properties: {
		playbook: {
			get default() {return getSelectableProp(Consts.PROVIDER, Consts.PLAYBOOK_PROPERTY_NAME);}
		},
		laneStartRules: {
			get default() {return getSelectableProp(Consts.PROVIDER, Consts.LANE_START_RULES);}
		},
		activityStartRules: {
			get default() {return getSelectableProp(Consts.PROVIDER, Consts.ACTIVITY_START_RULES);}
		},
		pillDependencies: {
			get default() {return getSelectableProp(Consts.PROVIDER, Consts.PILL_DEPENDENCIES);}
		},
		laneDependencies: {
			get default() {return getSelectableProp(Consts.PROVIDER, Consts.LANE_DEPENDENCIES);}
		},
		cardDependenciesByLane: {
			get default() {return getSelectableProp(Consts.PROVIDER, Consts.CARD_DEPENDENCIES_BY_LANE);}
		},
		boardData: {
			get default() {return getSelectableProp(Consts.PROVIDER, Consts.BOARD_DATA);}
		},
		configurationState: {
			get default() {return getSelectableProp(Consts.PROVIDER, Consts.CONFIG_PANEL_STATE);}
		},
		activitiesDependedOn: {
			get default() {return getSelectableProp(Consts.PROVIDER, Consts.ACTIVITIES_DEPENDED_ON);}
		},
		modal: {
			get default() {return getSelectableProp(Consts.PROVIDER, Consts.MODAL);}
		},
		alerts: {
			get default() {return getSelectableProp(Consts.PROVIDER, Consts.ALERTS);}
		},
		showStartProcessElements: {
			get default() {return getSelectableProp(Consts.PROVIDER, Consts.SHOW_START_PROCESS_ELEMENTS);}
		},
		userData: {
			get default() {return getSelectableProp(SN_CANVAS_SESSION, Consts.SESSION_USER);}
		},
		isLoading : {
			get default() {return getSelectableProp(Consts.PROVIDER, Consts.LOADING_PAD);}
		},
		configurationDotWalkData: {
			get default() {return getSelectableProp(Consts.PROVIDER, Consts.CONFIG_DOT_WALK_DATA);}
		}
	},
	actionHandlers: {
		// confirm modal
		[ActionTypes.OPEN_CONFIRM_MODAL_REQUESTED]: Modal.handleOpenConfirmationModal,
		// lane actions
		[ActionTypes.CREATE_LANE_CLICKED]: Lane.handleCreateLaneClicked,
		[ActionTypes.DELETE_LANE_CLICKED]: Lane.handleDeleteLaneButtonClicked,
		[ActionTypes.EDIT_LANE_CLICKED]: Lane.handleEditLaneClicked,
		[ActionTypes.INSERT_LANE_CLICKED]: Lane.insertLaneClicked,
		// dnd
		[ActionTypes.CARD_MOVED]: DnDActions.handleCardDrop,
		// configuration side panel
		[ActionTypes.SAVE_CONFIGURATION]: ConfigurationPanel.handleSaveConfiguration,
		// Handlers for using activity picker and adding card to lane
		[ActionTypes.CREATE_ACTIVITY_DEF_CLICKED]: Playbook.openCreateActivityDefModal,
		[ActionTypes.ADD_ITEM_TO_LANE_CLICKED]: Playbook.updatePickerData,
		[ActionTypes.PICKER_RESULT_SELECTED]: Playbook.handlePickerItemSelected,
		[ActionTypes.DISMISS_PICKER]: Playbook.clearPickerData,
		[ActionTypes.PICKER_WAS_DISMISSED]: Playbook.setFocusAndClearPickerData,
		[ActionTypes.HIDE_ACTIVITY_PICKER]: Playbook.hideActivityPicker,
		[ActionTypes.FOCUS_CARD]: Playbook.checkShouldFocusCard,
		[ActionTypes.NOW_MODAL_OPENED_SET]: Playbook.setFocusAndClearPickerData,
		// Rearrange after adding, deleting, reordering cards
		[ActionTypes.REARRANGE_ACTIVITY_REQUESTED]: Rearrange.rearrangeActivityWithDependency,
		// card actions
		[ActionTypes.DELETE_ACTIVITY_CLICKED]: Card.handleDeleteActivity,
		[ActionTypes.LOAD_FLOW_DESIGNER_CLICKED]: Playbook.openFlowDesigner
	},
	styles: [styles]
});
