const CHOICES =
`... on GlideLayout_ChoiceElementType {
  choices {
	displayValue
	value
  }
}`;

const BASIC_DICTIONARY_DATA =
`internalType
dependentField
dependentTable
attributes {
	name
	value
}`;

export const getPlaybookQuery = `
query ($sysId: String!) {
	GlideRecord_Query {
		...query_playbook
		...query_activity_start_rule_options
		...query_lane_start_rule_options
	}
}

fragment query_playbook on GlideRecord_QueryType {
	playbook: sys_pd_process_definition(sys_id: $sysId) {
		_results {
			...playbook_results
		}
	}
}

fragment query_activity_start_rule_options on GlideRecord_QueryType {
	activityStartRules: sys_pd_activity_start_rule_definition {
		_results {
      ...activity_start_rule_results
		}
	}
}

fragment query_lane_start_rule_options on GlideRecord_QueryType {
	laneStartRules: sys_pd_lane_start_rule_definition {
		_results {
      ...lane_start_rule_results
		}
	}
}

fragment query_lanes on GlideRecord_TableResultsType_sys_pd_process_definition {
	_query {
		lanes: GlideRecord_Query {
			sys_pd_lane(queryConditions: "process_definition=$$parent.sys_id$$^EQORDERBYorder") {
				_results {
					...lane_results
				}
			}
		}
	}
}

fragment query_activities on GlideRecord_TableResultsType_sys_pd_lane {
	_query {
		activities: GlideRecord_Query {
			sys_pd_activity(queryConditions:"lane=$$parent.sys_id$$^EQORDERBYorder^labelNOT LIKE$end^labelNOT LIKE$start^labelNOT LIKE$restart") {
				_results {
					...activity_results
				}
			}
		}
	}
}

fragment playbook_results on GlideRecord_TableResultsType_sys_pd_process_definition {
	sys_id { value }
	active { value }
	status { value }
	created: sys_created_on { value }
	description { value }
	description_meta: description { ...string_meta }
	label { value }
	label_meta: label { ...string_meta }
	name { value }
	name_meta: name { ...string_meta }
	scope: sys_scope { displayValue value }
	snapshot { ...snapshot_reference }
	trigger_condition { value }
	trigger_condition_meta: trigger_condition { ...string_meta }
	trigger_table { value }
	trigger_table_meta: trigger_table { ...string_meta }
	trigger_type { value }
	trigger_type_meta: trigger_type { ...choice_meta }
	updated: sys_updated_on { value }
	updatedDisplayValue: sys_updated_on { value: displayValue }
	...query_lanes
	_query {
		trigger_instance: GlideRecord_Query {
			sys_pd_trigger_instance(queryConditions:"process_definition=$$parent.sys_id$$") {
				_results {
					sys_id { value }
					trigger_inputs { ...glidevar_variables }
				}
			}
		}
	}
}

fragment lane_results on GlideRecord_TableResultsType_sys_pd_lane {
	sys_id { value }
	active { value }
	created: sys_created_on { value }
	description { value }
	description_meta: description { ...string_meta }
	dependent { value }
	label { value }
	label_meta: label { ...string_meta }
	lane_condition { value }
	lane_condition_meta: lane_condition { ...string_meta }
	name { value }
	name_meta: name { ...string_meta }
	order { value }
	start_rule { ...lane_start_rule_reference }
	start_rule_meta: start_rule { ...lane_start_rule_reference }
	...query_activities
}

fragment activity_results on GlideRecord_TableResultsType_sys_pd_activity {
	sys_id { value }
	active { value }
	inputs { ...glidevar_variables }
	activity_definition { ...activity_definition_reference }
	outputs { ...glidevar_variables }
	description { value }
	description_meta: description { ...string_meta }
	label { value }
	label_meta: label { ...string_meta }
	name { value }
	name_meta: name { ...string_meta }
	order { value }
	start_rule { ...activity_start_rule_reference }
	start_rule_meta: start_rule { ...activity_start_rule_reference }
	condition_to_run { value }
	_query {
	  variable_metadata: GlideLayout_Query {
		formLayout(table: "sys_pd_activity", sysId: "$$parent.sys_id$$") {
		  elementsData {
			elementName
			... on GlideLayout_GlideVarElementType {
			  meta_data: _elements {
				elementName
				elementType
				dictionaryData {
				  ${BASIC_DICTIONARY_DATA}
				}
				${CHOICES}
			  }
			}
		  }
		}
	  }
	}
}

fragment activity_definition_reference on GlideRecord_ReferenceFieldType_sys_pd_activity_definition {
	_reference { ...activity_definition_results }
}

fragment activity_definition_results on GlideRecord_TableResultsType_sys_pd_activity_definition {
	sys_id { value }
	active { value }
	activity_type { ...activity_type_reference  }
	badge { value }
	description { value }
	description_meta: description { ...string_meta }
	inputs { ...glidevar_variables }
	label { value }
	label_meta: label { ...string_meta }
	name { value }
	name_meta: name { ...string_meta }
	outputs { ...glidevar_variables }
	source_id: source { value }
	source_type { value }
	input_display_preferences { value }
	...action_flow_subflow_results
}

fragment activity_type_reference on GlideRecord_ReferenceFieldType_sys_pd_activity_type {
	_reference {
		name { value }
		icon { value }
		color { value }
	}
	value
}

fragment action_flow_subflow_results on  GlideRecord_TableResultsType_sys_pd_activity_definition {
	_query {
	  action_type_definition: GlideRecord_Query {
		sys_hub_action_type_definition(queryConditions: "sys_id=$$parent.source$$") {
		  _results {
			name { value }
			access { value }
			sys_id { value }
		  }
		}
	  }
	}
	_query {
	  flow_subflow_definition: GlideRecord_Query {
		sys_hub_flow(queryConditions: "sys_id=$$parent.source$$") {
		  _results {
			name { value }
			access { value }
			type { value }
			sys_id { value }
		  }
		}
	  }
	}
}

fragment snapshot_reference on GlideRecord_ReferenceFieldType_sys_pd_snapshot {
	_reference {
		created: sys_created_on { value }
	}
	value
}

fragment activity_start_rule_reference on GlideRecord_ReferenceFieldType_sys_pd_activity_start_rule_definition {
	_reference {
      ...activity_start_rule_results
	}
}

fragment lane_start_rule_reference on GlideRecord_ReferenceFieldType_sys_pd_lane_start_rule_definition {
	_reference {
      ...lane_start_rule_results
	}
}

fragment activity_start_rule_results on GlideRecord_TableResultsType_sys_pd_activity_start_rule_definition {
	value: sys_id { value }
	name { value }
    displayValue: label { value }
}

fragment lane_start_rule_results on GlideRecord_TableResultsType_sys_pd_lane_start_rule_definition {
	value: sys_id { value }
	name { value }
    displayValue: label { value }
}

fragment choice_meta on GlideRecord_ChoiceListFieldType {
	canRead
	canWrite
	isMandatory
	label
	isChoice
}

fragment glidevar_variables on GlideRecord_GlideVarFieldType {
	variables {
		value: displayValue
		displayValue
		internalType
		label
		variableName
		variableValue
		variableMapping: valueMapping
		mandatory: isMandatory
	}
}

fragment string_meta on GlideRecord_FieldType_String {
	canRead
	canWrite
	isMandatory
	label
}`;

export const isPlaybookNameAvailable = `
query ($name: String!) {
	GlideProcessAutomation_Query {
		isPDNameAvailable(name: $name) {
			name
			available
		}
	}
}`;

export const getPlaybookNameHeaderQuery = `
query ($sysId: String!) {
	GlideRecord_Query {
		playbook_header: sys_pd_process_definition(sys_id: $sysId) {
			_results {
				label { value }
				name { value }
			}
		}
	}
}`;
