/**
 * Glide Form Modal specifically is for loading glide form records into an iframe within the now-modal.
 *
 * Using a modal of this type helps apply specific loaders on bootstrap that modify the jelly glide form
 * in order to prevent the user from navigating to places that could be irrecoverable.
 *
 * @param headerLabel - text that appears in the modal header
 * @param url - src url for the iframe (this should point to a glide list or form)
 * @param config - show or hide the grey top bar that appears on service now instance forms
 * @returns {*} - properties to be applied to sn-pd-modal
 * @constructor
 */
export const GlideFormModal = (headerLabel, url, config = {iframe: {}}) => {
	if (!headerLabel || !url) {
		console.warn('Must pass header label and a url for an iframe.');
		return null;
	}

	const glideUrl = buildGlideFormUrl(url);

	return {
		content: (
			<iframe
				src={glideUrl}
				style={{width: '100%', height: '100%', minHeight: '80vh'}}
				frame-border='0'
				border='0'
			/>
		),
		headerLabel,
		opened: false,
		size: 'lg',
		...config,
		iframe: {
			type: 'form',
			renderFormHeaderBar: true,
			onSubmit: () => {},
			...config.iframe
		}
	};
};


