import {COMPONENT_ID, SN_PORTAL_ROOT} from '../constants/constants';

let componentIds = {};

/**
 * Get any selectable properties from <sn-pd-root />.
 * @param propName
 * @returns {{}}
 */
export function getSelectableRootProps(propName) {
	return getSelectableProp(SN_PORTAL_ROOT, propName);
}

/**
 * This utility function is being provided to work around the fact that we don't want to make any metadata
 * changes for defining selectable properties on components. As a result on the first component bootstrap, we will
 * manually get the component id from the component the implementor cares about and create the selectable property string
 * and apply it to the component.
 *
 * Usage:
 * getSelectableProp('sn-pd-root', 'selectedContent');
 *
 * Returns:
 * '@[component-id of layout]/tabsData'
 *
 * @param componentName
 * @param propName
 * @returns {*}
 */
export function getSelectableProp(componentName, propName) {
	let componentId = componentIds[componentName] || null;
	if (!componentId) {
		const element = document.getElementsByTagName(componentName);
		if (!element.length)
			return {};
		componentId = element[0].getAttribute(COMPONENT_ID);
		componentIds[componentName] = componentId;
	}

	return `@${componentId}/${propName}`;
}
