import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import actions from './actions';
import styles from './_sn-pd-root.scss';

import {createErrorHandlerBehavior} from 'sn-component-error-handler';

createCustomElement('sn-pd-root', {
	type: snabbdom,
	view,
	styles,
	properties: {
		selectedContent: {
			selectable: true,
			default: null
		},
		urlContent: {
			default:null
		},
		changedUserPref : {
			selectable: true
		}
	},
	behaviors: [createErrorHandlerBehavior()],
	actionHandlers: {...actions}
});
