import get from 'lodash/get';

//Util function for client info
export const clientUtil = (() => {
	const _isMobile = window.navigator.userAgent.match(/(iPad)|(iPhone)|(iPod)|(android)|(webOS)/i);
	
	const _getIEVersion = () => {
		let ua = window.navigator.userAgent;
		let msie = ua.indexOf('MSIE ');
		if (msie > 0)
			return parseInt(ua.substring(msie + 5, ua.indexOf('.', msie)), 10);
		
		let trident = ua.indexOf('Trident/');
		if (trident > 0) { // IE 11 => return version number
			let rv = ua.indexOf('rv:');
			return parseInt(ua.substring(rv + 3, ua.indexOf('.', rv)), 10);
		}
		
		let edge = ua.indexOf('Edge/');
		if (edge > 0) {
			// Edge (IE 12+) => return version number
			return parseInt(ua.substring(edge + 5, ua.indexOf('.', edge)), 10);
		}
		return -1;
	};
	
	const isBrowserSupported = () => {
		let supported = true;
		const IEVer = _getIEVersion();
		
		if (IEVer > 0 && IEVer < 11)
			supported = false;
		else
			supported = !_isMobile;
		
		return supported;
	};
	
	const getTimeZoneName = () => new Date().toLocaleString('en', {timeZoneName:'short'}).split(' ').pop();
	const getUserAgentInfo = () => get(navigator, 'userAgent', '');
	
	return {
		getTimeZoneName,
		getUserAgentInfo,
		isBrowserSupported
	};
})();
