import '@servicenow/now-button';

const view = (state) => {
	const {properties: {open, side}} = state;

	const renderPanel = () => {
		if (!open)
			return null;
		return (
			<div className={`panel panel-${side}`}>
				<div className='panel-title-container'>
					<slot name='panel-title' className='panel-title'>
					</slot>
				</div>
				<slot name='panel-heading' />
				<div className='panel-content-container'>
					<slot name='panel-content'></slot>
				</div>
			</div>
		);
	};

	return (
		<div className={`panel-container panel-container-${side}`}>
			<div className={`sidebar-${side}`}>
				<slot name='sidebar-buttons' className='sidebar-buttons'>
				</slot>
			</div>
			{renderPanel()}
		</div>
	);
};


export {view};
