import {createCustomElement} from '@seismic/core';
import {view} from './brand-banner-view';
import styles from './_brand-banner.scss';
import {getSelectableProp} from '../../util/general-utils';
import * as Consts from '../../constants/shared';
import get from 'lodash/get';

createCustomElement('sn-pd-brand-banner', {
	view,
	properties: {
		playbook: {
			get default() {return getSelectableProp(Consts.PROVIDER, Consts.PLAYBOOK_PROPERTY_NAME);}
		},
		playbookName: {
			computed({properties}) {
				return get(properties, 'playbook.label', '');
			}
		}
	},
	styles
});
