import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import {modalView} from './modal-view';
import {handlePropertyChanges} from '../../actions/modal-actions';
import {NOW_RADIO_VALUE_SET, TRIGGER_SELECTION_TYPE_CHANGED} from '../../constants/action-types';


createCustomElement('sn-pd-modal', {
	view: modalView,
	properties: {
		content: {},
		headerLabel: {},
		footerActions: {},
		opened: {},
		size: {},
		appendToPayload: {},
		iframe: {}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: handlePropertyChanges,
		[NOW_RADIO_VALUE_SET]: ({dispatch, action: {payload: {value}}}) => {
			//This needs to be dispatched from here in order to work around a bug where the modal
			//seems to stop the propogation of actions further up the chain. This needs to be investigated further.
			dispatch(TRIGGER_SELECTION_TYPE_CHANGED, {value});
		}
	}
});
