import {get, isEmpty} from 'lodash';
import {t} from 'sn-translate';
import '@servicenow/now-button';
import '@servicenow/now-condition-builder';
import '@servicenow/now-modal';
import * as Modal from '../../constants/modal-constants';
import * as ActionTypes from '../../constants/action-types';

export function view({properties, modalOpen = false}, {updateState}) {
	const {field} = properties;
	const sysId = get(field, 'sysId', '');
	if (isEmpty(sysId))
		return '';

	return <div className="panel-condition-container">
		<label htmlFor="condition-to-run">
			{field.label}
		</label>
		<input id="condition-to-run" type="text" value={field.value} className="now-a11y-label"/>
		<now-button
			label={t('Modify condition')}
			variant="secondary"
			size="md"
			on-click={() => updateState({modalOpen: true})}
		/>
		<now-modal
			headerLabel={t('Modify condition to run')}
			size="lg"
			opened={modalOpen}
			contentFullWidth={true}
			footerActions={[
				{
					'label': t('Modify'),
					'variant': 'primary',
					'clickActionType': ActionTypes.MODIFY_CONDITION_TO_RUN
				},
				{
					'label': Modal.CONFIRMATION_MODEL_ACTION_CANCEL,
					'variant': 'secondary',
					'clickActionType': ActionTypes.CLOSE_CONDITION_MODAL
				}
			]}
		>
			{/*Conditionally rendering the condition builder or else it will retain
			   a staged value a user canceled*/}
			{modalOpen ?
				<div style={{height: '450px'}}>
					<now-condition-builder
						tableName={`v_pd_activity_condition_to_run__sys_id__${sysId}`}
						encodedQuery={field.value}
						hideRelatedListQueryConditions={true}
						hideSets={true}
						legacy={true}
					/>
				</div>
				: ''}
		</now-modal>
	</div>;
}
