import {createCustomElement} from '@servicenow/ui-core';
import {view} from './dot-walk-view';
import styles from './dot-walk.scss';
import * as WalkerConsts from './dot-walk-constants';
import * as Actions from './dot-walk-action-handlers';

createCustomElement('sn-pd-dot-walker', {
	view,
	initialState: {
		showWalker: false,
		data: '',
		walkerPath: ''
	},
	properties: {
		initialData: {},
		onPillSelectAction: '',
		fieldType: ''
	},
	actionHandlers: {
		[WalkerConsts.DOT_WALKER_OPENED]: Actions.toggleWalker,
		[WalkerConsts.DOT_WALK_TRIGGERED]: Actions.handleDotWalkTriggered,
		[WalkerConsts.DOT_WALK_FETCH_REQUESTED]: Actions.fetchTableData,
		[WalkerConsts.DOT_WALK_FETCH_SUCCEEDED]: Actions.updatePickerData,
		[WalkerConsts.DOT_WALK_ITEM_SELECTED]: Actions.formatAndAnnounceSelection,
		[WalkerConsts.CLOSE_AND_CLEAR_WALKER]: Actions.closeAndClearWalker,
		[WalkerConsts.DOT_WALKER_HIDDEN]: Actions.closeAndClearWalker
	},
	styles: [styles]
});
