import {actionTypes} from '@servicenow/ui-core';

import {getUrlFromSelectedContent} from './sn-pd-router.contentParser';
import {getSelectedContentFromUrl} from './sn-pd-router.urlParser';
import {getParametersFromPath, initBaseUrl, navigateUrl, redirectUrl, setPageTitle} from './sn-pd-router.util';
import {initConfig, queryParamsEnabled} from './sn-pd-router.config';
import {attachAnchorLinkInterceptor, detachAnchorLinkInterceptor} from './sn-pd-router.interceptors';

import {
	URL_CHANGED,
	URL_CONTENT_LOADED, INITIAL_URL_LOADED
} from '../constants/constants';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_DISCONNECTED } = actionTypes;


// Router Life cycle effects
const onPopState = ({state, dispatch}) => dispatch(URL_CHANGED, {...state.properties.selectedContent});

const routerBootStrappedEffect = ({state, action, dispatch}) => {
	window.addEventListener('popstate', onPopState.bind(window,{state, action, dispatch}), false);
	attachAnchorLinkInterceptor(dispatch);

	const {initialUrlLoaded, routerConfig, selectedContent} = state.properties;
	if(!initialUrlLoaded)
		initConfig(routerConfig);
	initBaseUrl();
	dispatch(INITIAL_URL_LOADED, {...selectedContent});
};

const routerDisconnectedEffect = () => {
	window.removeEventListener('popstate', onPopState, false);
	detachAnchorLinkInterceptor();
};


//Router Action handlers
export function updateUrl(selectedContent){
	const url = getUrlFromSelectedContent(selectedContent);
	if (url == null)
		return;

	// Don't navigate if we're already at that URL. NOTE: getParamatersFromPath factors in base URL, and domain
	const {params:{path: {type}, search}} = getParametersFromPath('/:type*');
	if (url == '/' + type + (queryParamsEnabled() ? search : ''))
		setPageTitle(url);
	else if(selectedContent.redirect) {
		redirectUrl(url);
		//dispatch(CLEAR_REDIRECT);
	}
	else
		navigateUrl(url);
}

function urlChangedEffect({action: {payload: {prevSelectedContent, url}}, dispatch}){
	const selectedContent = getSelectedContentFromUrl(prevSelectedContent, url);
	if(selectedContent)
		dispatch(URL_CONTENT_LOADED, selectedContent);
}

function initialUrlLoadedInterceptor({effects, coeffects}){
	return {
		coeffects,
		effects: [...effects, {
			effect: urlChangedEffect,
			args: [coeffects]
		}]
	};
}

export const actionHandlers = {
	[URL_CHANGED]: urlChangedEffect,
	[INITIAL_URL_LOADED]: {
		effect: ({updateState}) => updateState({path: 'properties.initialUrlLoaded', value: true, operation: 'set'}),
		interceptors: [initialUrlLoadedInterceptor]
	}
};

export const lifeCycleHandlers = {
	[COMPONENT_BOOTSTRAPPED]: routerBootStrappedEffect,
	[COMPONENT_DISCONNECTED]: routerDisconnectedEffect
};
