import {createCustomElement, actionTypes as SeismicActions} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {view} from './list-view';
import styles from './_list.scss';
import * as Handlers from './list-action-handlers';
import * as HTTP from '../../actions/http-effects';
import * as GraphQL from '../../actions/graphql-effects';
import * as Modal from '../../actions/modal-actions';
import {requestScopesEffect} from '../../actions/http-effects';
import * as ActionTypes from '../../constants/action-types';
import {SN_CANVAS_SESSION, SN_CANVAS_USER_PREFS} from '../canvas/constants/constants';
import {getSelectableProp} from '../../util/general-utils';
import * as Consts from '../../constants/shared';
import * as Error from '../../actions/error-actions';

createCustomElement('sn-pd-list', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	initialState: {
		modal: {},
		newPlaybookForm: {},
		alerts: [],
		userScopes: [],
		currentScope: 'global',
		filteredUserScopes: [],
		triggers: [],
		filteredTriggers: [],
		selectedScope: {},
		listRefreshRequested: {},
		selectedPlaybook: null,
		selectedTrigger: null,
		selectedTriggerDefinition: null,
		triggerTypes: [],
		triggerSelectionType: null,		// this determines whether or not the user is choosing an existing triggerType or triggerDefinition.
		triggerTypeOptions: [],
		triggerDefOptions: []
	},
	properties: {
		activated: {
			onChange(newActivated, prevActivated, dispatch) {
				// Only Refresh Playbook list on coming back to list view
				// based on activated property changes from FALSE to TRUE
				if (newActivated && !prevActivated)
					dispatch(ActionTypes.REFRESH_PLAYBOOK_LIST_REQUESTED);
			}
		},
		userPrefs: {
			get default() {
				return getSelectableProp(SN_CANVAS_USER_PREFS, 'userPrefs');
			}
		},
		userData: {
			get default() {
				return getSelectableProp(SN_CANVAS_SESSION, Consts.SESSION_USER);
			}
		},
		alerts: {
			get default() {return getSelectableProp(Consts.PROVIDER, Consts.ALERTS);}
		},
	},
	actionHandlers: {
		[SeismicActions.COMPONENT_BOOTSTRAPPED]: Handlers.handleComponentBootstrapped,
		[ActionTypes.ITEM_SELECTED]: Handlers.handleItemSelected,
		[ActionTypes.ROW_CLICKED]: Handlers.handleRowClicked,
		[ActionTypes.REFRESH_PLAYBOOK_LIST_REQUESTED]: Handlers.handleRefreshPlaybookList,
		// CREATE NEW PROCESS
		// request required data
		[ActionTypes.REQUEST_USER_SCOPES]: requestScopesEffect,
		[ActionTypes.REQUEST_USER_SCOPES_SUCCEEDED]: Handlers.updateUserScopes,
		[ActionTypes.REQUEST_USER_TRIGGERS]: GraphQL.requestTriggersAndTypesEffect,
		[ActionTypes.REQUEST_USER_TRIGGERS_SUCCEEDED]: Handlers.updateUserTriggersAndTypes,
		// field updated actions
		[ActionTypes.SN_RECORD_INPUT_VALUE_CHANGED]: Handlers.handleNewPlaybookFormFieldChange,
		[ActionTypes.SN_TYPEAHEAD_FILTER_CHANGED]: Handlers.handleAppTypeAheadFilterChanged,
		[ActionTypes.SN_TYPEAHEAD_VALUE_CHANGED]: Handlers.handleNewPlaybookFormFieldChange,
		[ActionTypes.NOW_RECORD_FIELD_LOOKUP_CONNECTED_VALUE_CHANGED]: Handlers.handleNewPlaybookFormFieldChange,
		// modal actions
		[ActionTypes.DECLARATIVE_ACTION_CLICKED]: Handlers.handleOpenNewPlaybookModal,
		[ActionTypes.NOW_MODAL_OPENED_SET]: Handlers.handleCloseNewPlaybookModal,
		[ActionTypes.NEW_PLAYBOOK_MODAL_CANCEL]: Handlers.handleCloseNewPlaybookModal,
		[ActionTypes.NEW_PLAYBOOK_MODAL_SAVE]: Handlers.handleCreateNewPlaybook,
		[ActionTypes.NEW_PLAYBOOK_MODAL_TOGGLE_STEP_ONE_NEXT_BUTTON_STATUS]: Handlers.handleToggleStepOneNextButtonStatus,
		[ActionTypes.NEW_PLAYBOOK_MODAL_UPDATE_STEP_TWO_BUTTONS]: Handlers.handleToggleStepTwoNextButtonStatus,
		[ActionTypes.RETURN_TO_PROCESS_DETAILS]: Handlers.returnToProcessDetails,
		[ActionTypes.OPEN_SELECT_TRIGGER_MODAL]: Handlers.handleOpenTriggerSelectionModal,
		[ActionTypes.CREATE_PLAYBOOK_REQUESTED]: Handlers.handleCreatePlaybookRequested,
		[ActionTypes.CREATE_PLAYBOOK]: HTTP.createPlaybookEffect,
		[ActionTypes.CREATE_PLAYBOOK_SUCCEEDED]: Handlers.handleCreatePlaybookSucceeded,
		[ActionTypes.FOCUS_MODAL_CLOSE_BUTTON]: Modal.focusModalCloseButton,
		// actions created from seismic pertaining to the trigger instance creation path
		[ActionTypes.START_TRIGGER_INSTANCE_CREATION_PATH]: Handlers.startTriggerCreationPath,
		[ActionTypes.CREATE_BASIC_PLAYBOOK_RECORD_REQUESTED]: Handlers.handleCreateBasicPlaybookRequested,
		[ActionTypes.CREATE_BASIC_PLAYBOOK]: HTTP.createBasicPlaybookEffect,
		[ActionTypes.CREATE_BASIC_PLAYBOOK_RECORD_SUCCEEDED]: Handlers.handleSaveAndConfigureInstance,
		[ActionTypes.CREATE_TRIGGER_INSTANCE_REQUESTED]: GraphQL.createTriggerInstanceEffect,
		[ActionTypes.CREATE_TRIGGER_INSTANCE_SUCCEEDED]: Handlers.handleTriggerInstanceCreated,
		[ActionTypes.TRIGGER_SELECTION_TYPE_CHANGED]: Handlers.handleTriggerSelectionTypeChange,
		// trigger instance iframe actions
		[ActionTypes.INSTANCE_IFRAME_GO_TO_DESIGNER]: Handlers.handleGoToDesigner,
		[ActionTypes.IFRAME_TRIGGER_INSTANCE_CANCELLED]: Handlers.cleanUpRecordsAndGoBack,
		[ActionTypes.IFRAME_OPEN_TRIGGER_INSTANCE_MODAL]: Handlers.handleOpenIFrameTriggerInstance,
		[ActionTypes.DELETE_PROCESS_REQUESTED]: GraphQL.deletePlaybookEffect,
		// trigger def iframe actions
		[ActionTypes.IFRAME_TRIGGER_DEF_SUBMITTED]: Handlers.handleIframeTriggerDefSubmission,
		[ActionTypes.IFRAME_TRIGGER_DEF_NEXT]: Handlers.handleIframeTriggerDefNext,
		[ActionTypes.IFRAME_TRIGGER_DEF_CANCELED]: Handlers.cleanUpRecordsAndGoBack,
		[ActionTypes.DELETE_TRIGGER_DEF_REQUESTED]: GraphQL.deleteTriggerDefEffect,
		[ActionTypes.ADD_NOTIFICATIONS]: Error.handleDeclarativeActionAlert,
	}
});
