import '@servicenow/now-select';
import '@servicenow/now-button';
import '../../pill/pill-container';
import '../../pill/pill-field';
import {t} from 'sn-translate';
import {STRING_TYPES} from '../../dynamic-form/dynamic-form-constants';

import {
	PILL_VALUE_SELECTED,
	TEMPLATE_FIELD_REMOVE_ROW
} from '../../../constants/action-types';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';

export const view = (state, {dispatch}) => {
	const {rowIndex, keyItems, field, dotWalkData, key, openInModal} = state.properties;
	const {name, type, value, displayValue} = field;
	const rowClassName = openInModal ? 'template-value-row-modal' : 'template-value-row';
	const removeRow = () => dispatch(TEMPLATE_FIELD_REMOVE_ROW, {idx: rowIndex});

	const renderRemoveRowButton = () => {
		return (
			<Fragment>
				<now-button-iconic 
					icon="close-outline" 
					tooltipContent={t('Remove field')} 
					size="md" variant="tertiary" bare={true} 
					configAria={{'aria-label':'Remove field'}}
					on-click={removeRow}
				/>
				<div className='line-break'></div>
			</Fragment>
		);
	};

	const renderDotWalkField = () => {
		if (STRING_TYPES.includes(type)) {
			return <sn-pd-pill-container inputValue={value} dotWalkData={dotWalkData} updateActionName={PILL_VALUE_SELECTED} />;
		}

		const hasPillValue = value && value.indexOf('{{') > -1;
		const staticValue = hasPillValue ? '' : value;
		const variableMapping = hasPillValue ? value : '';

		const newField = {
			...field,
			displayValue: displayValue || value,
			name,
			uiType: type,
			value: staticValue,
			glideVarFields: {
				variableMapping: variableMapping
			},
			label: ''
		};

		const fieldKey = key + '-' + field;

		return <sn-pill-field
			key={fieldKey}
			append-to-payload={{isGlideVar: hasPillValue}}
			field={newField}
			dotWalkData={dotWalkData}
			onChangeAction={PILL_VALUE_SELECTED}
		/>;
	};

	return (
		<div className={`template-value-row-${rowIndex} ${rowClassName}`}>
			<now-select
				items={keyItems}
				selectedItem={name}
				messages={[t('Choose a field')]}
				manageSelectedItem
				search
			/>

			{openInModal ? null : renderRemoveRowButton()}
			
			{name && renderDotWalkField()}

			{openInModal ? renderRemoveRowButton() : null}
		</div>
	);
};
