import {get, set, isEmpty} from 'lodash';
import {responseHasErrorsOrNoData} from '../util/general-utils';
import {DeleteActivityWithDependency, UNABLE_TO_DELETE_ACTIVITY_MODAL_CONTENT} from '../constants/modal-constants';
import * as ActionTypes from '../constants/action-types';
import {DELETION} from '../constants/shared';
import * as PillHelpers from '../util/pill-utils';
import {closeConfigPanelForDeletedItem} from './configuration-actions';
import {
	getPlaybookFromProperties,
	getVariableFromActionMetaOptions,
	getConfigurationStateFromProperties
} from '../util/selectors';
import {getConfigurationDefaults, transformInputs} from '../util/transforms';
import {setFocusToConfigTriggerElement} from '../util/accessibility-utils';

const ACTIVITY_RESPONSE_PATH = 'data.GlideProcessAutomation2_Mutation.updateActivity';

export const handleSaveCardConfigSucceeded = ({action: {payload}, state, updateProperties, dispatch, properties: {configurationState}}) => {
	setFocusToConfigTriggerElement(configurationState);

	if (responseHasErrorsOrNoData(payload, dispatch))
		return;

	const updatedCard = get(payload, ACTIVITY_RESPONSE_PATH);
	const shouldClearConfig = get(payload, 'clearConfig', true);

	if (!updatedCard)
		return;

	const cardSysId = updatedCard.sys_id;
	const playbook = get(state, 'properties.playbook', {});
	let stateCards = {...get(playbook, 'cards', {})};
	const updatedCardInputs = get(updatedCard, 'inputs', []);
	const cardInputs = get(stateCards, `["${cardSysId}"].inputs`, []);
	stateCards[cardSysId] = {
		...stateCards[cardSysId],
		...updatedCard,
		inputs: transformInputs(updatedCardInputs, null, cardInputs),
		start_rule: {
			value: updatedCard.start_rule,
			displayValue: updatedCard.start_rule_display
		},
		start_rule_display: undefined
	};
	const newConfigurationState = shouldClearConfig ? getConfigurationDefaults() : configurationState;

	updateProperties({
		configurationState: newConfigurationState,
		playbook: {...playbook, cards: stateCards}
	});
};

export const handleRearrangedCardUpdate = ({action, state, updateProperties, dispatch}) => {
	if (responseHasErrorsOrNoData(action.payload, dispatch))
		return;

	const changedActivity = get(action, 'payload.data.GlideProcessAutomation2_Mutation.updateActivities', []);
	changedActivity.forEach(changedActivity => {
		let payload = {clearConfig: false};
		if (!isEmpty(get(changedActivity, 'error', ''))) {
			dispatch(ActionTypes.GRAPHQL_ERROR_OCCURRED);
			return;
		}

		set(payload, ACTIVITY_RESPONSE_PATH, changedActivity);
		const newAction = {...action, payload};
		handleSaveCardConfigSucceeded({action: newAction, state, updateProperties, dispatch});
	});
};

export const handleCardDeleteSuccess = ({action, properties, updateProperties, dispatch}) => {
	if (responseHasErrorsOrNoData(action.payload, dispatch))
		return;

	//Grab the sysId out of the variables used for the graphql mutation
	const sysId = getVariableFromActionMetaOptions(action, 'sysId');
	const configurationState = getConfigurationStateFromProperties(properties);

	closeConfigPanelForDeletedItem(configurationState, [sysId], dispatch);

	if (sysId) {
		const playbook = getPlaybookFromProperties(properties);
		const cards = {...get(playbook, 'cards', [])};
		const card = get(cards, `${sysId}`, {});
		const laneId = card.lane_id;
		let lane = {...get(playbook, `lanes[${laneId}]`, {})};
		let laneCards = get(lane, 'cards', []);

		//remove card from lanes cards array
		const indexOfCardInLane = laneCards.indexOf(sysId);
		laneCards.splice(indexOfCardInLane, 1);

		let lanes = {...get(playbook, 'lanes', {})};
		lanes[laneId] = lane;

		//remove card from playbooks card map
		delete cards[sysId];

		updateProperties({
			playbook: {
				...playbook,
				cards,
				lanes
			}
		});

		dispatch(ActionTypes.REORDER_CARDS_DELETE_INVALID_PILLS, {
			type: DELETION,
			movedCardId: sysId,
			movedCardOrder: card.order,
			cards
		});
	}

	dispatch(ActionTypes.CLOSE_MODAL);
};

export const handleDeleteActivity = ({action, dispatch, properties}) => {
	const sysId = get(action, 'payload.data.sys_id', '');
	const dependents = PillHelpers.getActivityDependents(sysId, properties.activitiesDependedOn);

	if (dependents) {
		const errorMessage = PillHelpers.createWarningMessage(dependents.dependents, UNABLE_TO_DELETE_ACTIVITY_MODAL_CONTENT);
		// This appendToPayload object will be merged with the
		// payload that is dispatched to any modal actions by the
		// sn-modal component
		const appendToPayload = { sysId };
		dispatch(ActionTypes.OPEN_MODAL_REQUESTED, {data: {...DeleteActivityWithDependency(errorMessage), appendToPayload}});
	} else
		dispatch(ActionTypes.DELETE_ACTIVITY_REQUESTED, { sysId });
};
