import '@servicenow/now-pill';
import '@servicenow/now-legacy-icon';
import '../../dot-walker';
import {isEmpty} from 'lodash';
import {PILL_ONLY_TYPES} from '../../dynamic-form/dynamic-form-constants';
import {renderFactoryBasedElement} from '../../dynamic-form/dynamic-form-helpers';
import {t} from 'sn-translate';

export default ({properties}, {dispatch}) => {

	const {
		dotWalkData,
		field: {
			displayValue: pillLabel,
			label,
			name,
			uiType,
			componentId,
			mandatory,
			glideVarFields: {
				variableMapping: pillValue
			}
		},
		onChangeAction
	} = properties;

	const hasPill = !isEmpty(pillValue);
	const wrapperComponentId = `pill_field_${componentId}`;
	const isPillOnlyField = PILL_ONLY_TYPES.includes(uiType);

	const renderPill = () => {
		return (
			<now-pill
				label={pillLabel || pillValue}
				data-pillValue={pillValue}
				size='md'
				canDismiss={true}
			/>
		);
	};

	const renderPillOnlyPlaceholder = () => {
		return (
			<input
				className='pill-only'
				type='text'
				readOnly='true'
				placeholder={t('Select ') + label}
				required={mandatory}
			/>
		);
	};

	const renderElement = () => {
		if (hasPill)
			return renderPill();

		if (isPillOnlyField)
			return renderPillOnlyPlaceholder();

		return renderFactoryBasedElement(properties.field, componentId, dispatch, onChangeAction);
	};

	if (!uiType)
		return null;
	
	return (
		<div component-id={wrapperComponentId} key={wrapperComponentId} className='pill-field'>
			<div className='input-container'>
				<div className='input-field'>
					{renderElement()}
					<div className='dot-walker'>
						<sn-pd-dot-walker
							fieldType={uiType}
							initialData={dotWalkData}
							onPillSelectAction={onChangeAction}
							append-to-payload={{isGlideVar: true, name}}
						/>
					</div>
				</div>
			</div>
		</div>
	);
};
