import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {preUserPreferences} from '../utils/behaviors/presource.behaviors';
import {USER_PREF_PROPERTY, USER_PREF_UPDATE, PROPERTIES_SET} from '../constants/constants';
import {actionHandlers} from './sn-pd-user-pref.effects';
import {changedUserPrefPropBehavior} from '../utils/behaviors/selectableProps.behaviors';

createCustomElement('sn-pd-user-pref', {
	renderer:{
		type: snabbdom
	},
	view: (derivedState, {dispatch}) => {
		const {changedUserPref, triggerPref} = derivedState.properties;

		// workaround for now, since we cannot have onChange on the changedUserPref as it is rootPoperty, we are assigning the value to
		// to triggerPref so that onChange can be triggered.
		if(changedUserPref && triggerPref !== changedUserPref)
			dispatch(PROPERTIES_SET, { triggerPref : changedUserPref});

		return null;
	},
	properties: {
		[USER_PREF_PROPERTY]: {
			selectable: true,
			default : preUserPreferences.initialState
		},
		triggerPref : {
			default : null,
			onChange(value, dispatch){
				dispatch(USER_PREF_UPDATE, value);
			}
		}
	},
	behaviors: [changedUserPrefPropBehavior],
	actionHandlers
});
