import {NOT_DROPPABLE, PLAYBOOK, RECORD, TRIGGER, STATE} from '../constants/shared';
import {get, sortBy, orderBy} from 'lodash';
import {
	isArrayWithValues,
	isObjectWithValues,
	getMaxValueOfObjectProp,
	isDependent,
	getMinValueOfObjectProp
} from './general-utils';
import {playbookDependencyLookup} from  './dependencies';
import {TABLE_NAME} from '../constants/glide-types';
import {isReferenceType, getStateDotWalkObject} from '../util/general-utils';
import {updateVariableMeta} from '../actions/configuration-actions';

export const BOARD_TYPE = 'board';
export const EMPTY_LANE = {
	uuid: NOT_DROPPABLE,
	id: NOT_DROPPABLE,
	_style_: {
		'lane-body': {
			'max-width': '0px',
			'max-height': '0px',
			'visibility': 'hidden'
		}
	}
};

export const transformPlaybookData = (data) => {
	const playbookProperties = {...data};
	const boardInfo = get(data, 'lanes.sys_pd_lane', []);

	const {playbookData} = transformData(boardInfo);
	const triggerRecordData = getTriggerInstanceData(get(data, 'trigger_instance', {}));

	return {
		playbook: {
			...playbookProperties,
			lanes: playbookData.lanes,
			cards: playbookData.cards,
			triggerRecordData
		}
	};
};

export function transformData(laneData) {
	if (!isArrayWithValues(laneData))
		return {playbookData: {lanes: {}, cards: {}}};

	let lanes = {};
	let cards = {};

	laneData.forEach((lane) => {
		let updatedLane = transformLane(lane);

		const laneSysId = get(lane, 'sys_id');
		const lanesCards = get(lane, 'activities.sys_pd_activity', []);
		let laneCardsArray = [];

		lanesCards.forEach(card => {
			const cardSysId = get(card, 'sys_id');
			let updatedCard = transformCard(card, laneSysId);

			laneCardsArray.push(cardSysId);
			cards[cardSysId] = updatedCard;
		});

		updatedLane = {...updatedLane};
		delete updatedLane.activities;

		lanes[laneSysId] = {...updatedLane, cards: laneCardsArray};
	});

	cards = addStylesForShowOrder(cards);

	return {playbookData: {lanes, cards}};
}

export const getBoardData = (playbook, dependencies) => {
	const {lanes, cards} = playbook;
	const laneKeys = isObjectWithValues(lanes) ? sortBy(Object.values(lanes), ['order'])
		.map(lane => {return lane.sys_id;}) : [];
	const cardKeys = isObjectWithValues(cards) ? Object.keys(cards) : [];
	const lastLaneOrder = getMaxValueOfObjectProp(lanes, 'order');
	const firstLaneOrder = getMinValueOfObjectProp(lanes, 'order');
	const dependencyLookup = playbookDependencyLookup(playbook, dependencies);
	const textualMessages = get(playbook, 'textualMessages', []);

	let newLanes = laneKeys.map(key => {
		const thisLane = lanes[key];
		const isLastLane = thisLane.order === lastLaneOrder;
		const isDependedOn = dependencyLookup.laneHasReferencingStartRules(thisLane.sys_id);

		const isIndependent = !isDependent(thisLane.start_rule.value);
		const isFirstLane = thisLane.order === firstLaneOrder;
		const updatedLane = {...thisLane, isDependedOn, isLastLane, isFirstLane, isIndependent};

		return transformLane(updatedLane, BOARD_TYPE);
	});

	const newCards = cardKeys.map(key => {
		const cardTextualMessages = textualMessages.filter(message => message.id === key);
		return transformCard(cards[key], null, BOARD_TYPE, cardTextualMessages);
	});

	return {
		cards: newCards,
		lanes: [...newLanes, EMPTY_LANE]
	};
};

export function transformLane(lane, transformType) {
	if (!isObjectWithValues(lane))
		return {};

	return transformType === BOARD_TYPE ? addBoardDataToLane(lane) : lane;
}

export function addBoardDataToLane(lane) {
	if (!isObjectWithValues(lane))
		return {};

	let borderStyle = !lane.isDependedOn ? '2px dashed #9aa4a5' : 'none';

	return {
		...lane,
		uuid: get(lane, 'sys_id'),
		id: get(lane, 'sys_id'),
		title: get(lane, 'label'),
		_style_: {
			'sn-lane': {
				'padding': '0px',
				'width': '308px',
				'border-right': borderStyle,
				'height': 'calc(100% - 92px)',
				'overflow-y': 'auto'
			},
			'lane-body': {
				'padding-top': '8px',
				'align-items': 'normal'
			}
		}
	};
}

export function transformCard(card, laneId, transformType, textualMessages = []) {
	if (!isObjectWithValues(card))
		return {};

	let newCard = transformType === BOARD_TYPE ? addBoardDataToCard(card) : {...card};
	const newCardInputVariables = get(card, 'inputs.variables', null);

	if (newCardInputVariables || ! isArrayWithValues(newCard.inputs))
		newCard.inputs = transformInputs(newCard.inputs, newCard.variable_metadata);

	newCard.outputs = get(newCard, 'outputs.variables', []);
	newCard.textualMessages = textualMessages;

	return laneId ? {...newCard, lane_id: laneId} : {...newCard};
}

export function transformInputs(changedInputs, variableMetadata, inputs = []) {
	let updatedInputs = [...get(changedInputs, 'variables', [])];

	if (!isArrayWithValues(updatedInputs))
		return updatedInputs;

	if (variableMetadata)
		updatedInputs = updateVariableMeta(variableMetadata, updatedInputs);

	return updatedInputs.map(updatedInput => {
		const glideVarFields = {
			variableName: updatedInput.variableName,
			variableValue: updatedInput.variableValue,
			variableMapping: updatedInput.variableMapping
		};
		const input = inputs.find(i => i.name === updatedInput.variableName);

		const formattedInput = {
			...input,
			...updatedInput,
			value: updatedInput.variableValue || updatedInput.displayValue || '',
			displayValue: updatedInput.displayValue || '',
			name: updatedInput.variableName,
			glideVarFields
		};

		delete formattedInput.variableName;
		delete formattedInput.variableValue;
		delete formattedInput.variableMapping;

		return formattedInput;
	});
}

export function addStylesForShowOrder(cards) {
	if (!isObjectWithValues(cards))
		return {};

	const maxCardOrder = getMaxValueOfObjectProp(cards, 'order');
	const numberOfCharsInOrder = maxCardOrder.toString().length;

	if (numberOfCharsInOrder === 1)
		return cards;

	let cardOrderWidth = '20px';
	cardOrderWidth = numberOfCharsInOrder === 3 ? '28px' : numberOfCharsInOrder === 4 ? '34px' : cardOrderWidth;

	let updatedCards = {...cards};
	Object.keys(cards).forEach(key => {
		updatedCards[key] = {...updatedCards[key], orderWidth: cardOrderWidth};
	});

	return updatedCards;
}

export function addBoardDataToCard(card) {
	if (!isObjectWithValues(card))
		return {};

	const newCard = ensureOrder(card);

	return {
		...newCard,
		uuid: get(newCard, 'sys_id'),
		id: get(newCard, 'sys_id'),
		title: get(newCard, 'label')
	};
}

export function ensureOrder(item) {
	//We need to know if order is undefined or null. 0 is a valid value for order.
	if (typeof item.order === 'undefined' || item.order === null)
		return Object.assign({}, item, {order: 0});

	return item;
}

export function getConfigurationDefaults() {
	return {
		type: PLAYBOOK,
		panelOpen: false,
		config: {}
	};
}

export const getKeyValuePairs = (item) => {
	if (!isObjectWithValues(item))
		return {};

	const keyValuePairObject = {};

	for (let [itemKey, itemValue] of Object.entries(item)) {
		if (isObjectWithValues(itemValue)) {
			if (itemValue.value)
				keyValuePairObject[itemKey] = itemValue.value;
			else
				keyValuePairObject[itemKey] = '';
		} else
			keyValuePairObject[itemKey] = itemValue;
	}

	return keyValuePairObject;
};

export function removeCardsFromPlaybook(playbook, cardsToDelete) {
	let modifiedPlaybook = {...playbook};
	cardsToDelete.forEach(cardId => delete modifiedPlaybook.cards[cardId]);
	return modifiedPlaybook;
}

export function removeLaneFromPlaybook(playbook, laneToRemove) {
	let modifiedPlaybook = {...playbook};
	delete modifiedPlaybook.lanes[laneToRemove];
	return modifiedPlaybook;
}

export function formatScopeData(scopeData) {
	if (!isArrayWithValues(scopeData))
		return [];

	return scopeData.map(scope => {
		return {value: scope.sysId, displayValue: formatScopeDisplayValue(scope.name)};
	});
}

// Adding a space before scopes starting with @.  Seismic components are rendering nothing
export function formatScopeDisplayValue(displayValue) {
	if (!displayValue)
		return '';

	return displayValue.startsWith('@') ? ' ' + displayValue : displayValue;
}

export function getTriggerInstanceData(triggerInstance) {
	const triggerInstanceInputs = get(triggerInstance, 'sys_pd_trigger_instance[0].trigger_inputs.variables', []);
	const triggerInstanceData = {};

	triggerInstanceInputs.forEach(input => {
		if (input.internalType === TABLE_NAME) {
			triggerInstanceData.displayValue = `${TRIGGER} - ${input.displayValue}`;
			triggerInstanceData.isReference = true;
			triggerInstanceData.disabled = true;
			triggerInstanceData.outputs = {
				record: {displayValue: `${input.displayValue} ${RECORD}`, isReference: true, referenceDataKey: input.variableValue, type: 'reference'}
			};
			triggerInstanceData.path = [{displayValue: triggerInstanceData.displayValue, field: 'trigger', parent: 'firstColumn'}];
		}
	});

	return triggerInstanceData;
}

export const transformDotWalkResponse = (response, triggerDotWalkData) => {
	const activities = get(response, 'result.act.columns');
	let dotWalkFirstColumn = isObjectWithValues(triggerDotWalkData) ? {trigger: triggerDotWalkData} : {};
	const dotWalkSecondColumns = {};
	const triggerOutputs = get(triggerDotWalkData, 'outputs');

	if (isObjectWithValues(triggerOutputs))
		dotWalkSecondColumns['trigger'] = triggerOutputs;

	if (activities) {
		let activityValues = Object.values(activities);
		activityValues = orderBy(activityValues, ['order']);

		activityValues.map(activity => {
			const activityOutputs = getActivityOutputs(activity);

			dotWalkFirstColumn[activity.name] = {
				displayValue: activity.label,
				isReference: true,
				outputs: activityOutputs,
				disabled: true,
				order: activity.order
			};

			dotWalkSecondColumns[activity.name] = activityOutputs;
		});
	}

	const isProcessState = true;
	return {firstColumn: {...dotWalkFirstColumn, state: getStateDotWalkObject(isProcessState)}, ...dotWalkSecondColumns};
};

export function getActivityOutputs(activity) {
	const outputs = get(activity, 'columns.outputs.children.columns', {});
	const outputDotWalkData = {};

	Object.keys(outputs).map(output => {
		if (output.startsWith('$'))
			return;

		const thisOutput = outputs[output];
		const dotWalkInfo = get(thisOutput, 'dot');

		outputDotWalkData[output] = {
			displayValue: thisOutput.label,
			type: thisOutput.type,
			isReference: dotWalkInfo && isReferenceType(thisOutput.type),
			referenceDataKey: dotWalkInfo && get(dotWalkInfo, 'reference')
		};
	});

	outputDotWalkData[STATE] = getStateDotWalkObject();

	return outputDotWalkData;
}
