import get from 'lodash/get';
import {getPageSettings, getProperty} from 'sn-uxpage-presource';
import {clientUtil} from '../utils/client';
import {SESSION_DATA_KEY_UPDATE} from '../constants/constants';


export const getAllSessionData = (state) => get(state, 'properties.session.info', []);


export const getSessionData = (key, state, defaultVal) => {
	const sessionData = getAllSessionData(state);

	if(!sessionData || sessionData.length === 0)
		return defaultVal;

	const sessionDataKeyVal = sessionData.find(p => p.key === key);

	if(!sessionDataKeyVal)
		return defaultVal;

	return sessionDataKeyVal.value;
};

//Util functions for session info
export const sessionInfoUtil = (() => {

	const _lang = document.getElementsByTagName('html')[0].getAttribute('lang');

	const _dir = document.getElementsByTagName('html')[0].getAttribute('dir');

	const getGCKToken = () => get(window, 'g_ck', '');

	const getLanguage = () => _lang;

	const getDir = () => _dir;

	const getApplicationScope = () => get(getPageSettings('name'), 'value','');

	return {
		getGCKToken,
		getLanguage,
		getDir,
		getApplicationScope
	};
})();

//Initial session info
export const getInitialSessionInfo = () => {
	const timeOut = getProperty('glide.ui.session_timeout',60);
	const timeLeft = getProperty('glide.ui.session_timeleft',2);

	const getTimeLeft = () => {
		if ( timeLeft > timeOut ){ // if timeLeft is greater than timeOut, then set it to null, to be ignored.
			return null;
		}
		return timeLeft;
	};
	return  {
		g_ck : sessionInfoUtil.getGCKToken(),
		isLoggedIn: !!sessionInfoUtil.getGCKToken(),
		trackingEnabled : getProperty('glide.uxbuilder.tracking.enabled',false),
		forgetMe: getProperty('glide.ui.forgetme',false),
		timeOut: timeOut,
		timeLeft: getTimeLeft(),
		lang: sessionInfoUtil.getLanguage(),
		dir: sessionInfoUtil.getDir(),
		scope: sessionInfoUtil.getApplicationScope(),
		displayPrefsEnabled: getProperty('com.glide.pad.core.model.enableActivityInputViewPreferences', false),
		conditionFieldsEnabled: getProperty('com.glide.pad.core.model.enableConditionFields', false)
	};
};

//Client info
export const getClientInfo = () => {
	return {
		timeZoneName : clientUtil.getTimeZoneName(),
		userAgent: clientUtil.getUserAgentInfo(),
		isBrowserSupported: clientUtil.isBrowserSupported()
	};
};

export const setSessionDataKey = (key, value, dispatch) => dispatch(SESSION_DATA_KEY_UPDATE, {key, value});



