import {isEmpty, get} from 'lodash';

let routerConfig;

export const defaultConfig = {
	name: 'Router',
	sysId: '',
	landingPath : 'home',
	routes : {
		module : ['module_id'],
		record : ['table', 'sys_id'],
		non_record: ['renderer_type'],
		new_record: ['table','view_id'],
		page : ['page_id'],
		'full-search' : ['searchTerm'],
		error : ['reason']
				
	},
	defaultRoute: 'page',
	queryParams : false
};

const transformRouterConfigProp = (routerConfigProp) => {
	const _getDefaultRoute = (defaultValue) => {
		const defaultRouteObj = JSON.parse(defaultValue);
		for (let [key, value] of Object.entries(defaultRouteObj)) {
			return { type: key, route: {[key]: value.split(',')} };
		}
	};
	const _getRoutes = (routesValue) => {
		if(isEmpty(routesValue))
			return {};
		const routesObj = JSON.parse(routesValue);
		let routes = {};
		for (let [key, value] of Object.entries(routesObj)) {
			routes[key] = value.split(',');
		}
		return routes;
	};
	
	return {
		name: get(routerConfigProp, 'name.value', ''),
		sysId: get(routerConfigProp, 'sys_id.value', ''),
		landingPath: get(routerConfigProp, 'landing_path.value', ''),
		queryParams: get(routerConfigProp, 'query_params.value', ''),
		defaultRoute: _getDefaultRoute(get(routerConfigProp, 'default_route.value', '')).type,
		routes: {..._getDefaultRoute(get(routerConfigProp, 'default_route.value', '')).route,..._getRoutes(get(routerConfigProp, 'routes.value', ''))}
	};
};

export const initConfig = (config) => {
	if(!routerConfig)
		routerConfig = config.defaultConfig ? defaultConfig : transformRouterConfigProp(config);
};

export const getRouterConfig = () => routerConfig;
export const getTypeFieldsMap = () => routerConfig.routes;
export const getLandingPath = () => routerConfig.landingPath;
export const getDefaultType = () => routerConfig.defaultRoute;
export const queryParamsEnabled = () => routerConfig.queryParams;