import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@devsnc/sn-pill-container';
import {PILL_CONTAINER_PILL_INSERTED, PILL_CONTAINER_VALUE_CHANGED} from '../../../constants/action-types';

export const view = (state) => {

	const {dotWalkData, label, inputValue, multiLine, componentId, name} = state.properties;

	const containerProps = {
		inputValue,
		name,
		label,
		multiLine,
		componentId
	};

	return (<sn-pill-container {...containerProps}>
		<sn-pd-dot-walker slot='pill-picker'  initialData={dotWalkData} onPillSelectAction={PILL_CONTAINER_PILL_INSERTED}/>
	</sn-pill-container>);
};

/**
 * A wrapper around the sn-pill-container component.
 *
 * Responsible for accepting dot walk data and creating
 * the dot walker and slotting it into the real pill container
 * component
 */
createCustomElement('sn-pd-pill-container', {
	renderer: {type: snabbdom},
	view,
	properties: {
		name: {},
		label: {},
		inputValue: { default: '' },
		pillLabels: { default: {} },
		multiLine: { default: false },
		dotWalkData: {},
		updateActionName: {},
		componentId: {},
	},
	actionHandlers: {
		[PILL_CONTAINER_VALUE_CHANGED]: ({action: { payload }, properties: {updateActionName}, dispatch}) => {
			dispatch(updateActionName, payload);
		}
	}
});
