import {get} from 'lodash';
import {t} from 'sn-translate';
import {
	DELETE_ACTIVITY_CLICKED,
	LOAD_FLOW_DESIGNER_CLICKED,
	MOVE_ACTIVITY_CLICKED,
	POPULATE_CONFIGURATION_PANEL
} from '../../../constants/action-types';
import {
	ACTION_DESIGNER_ROLE,
	FLOW_DESIGNER_ROLE,
	SYS_HUB_FLOW_TABLE,
	ADMIN_ROLE
} from '../../../constants/shared';
import {isArrayWithValues} from '../../../util/general-utils';
export const EDIT_ACTIVITY = t('Edit activity');
export const EDIT_FLOW = t('Edit flow in Flow Designer');
export const EDIT_SUB_FLOW = t('Edit subflow in Flow Designer');
export const EDIT_ACTION = t('Edit action in Action Designer');
export const DELETE_ACTIVITY = t('Delete activity');
export const MOVE_ACTIVITY = t('Move activity');
export const ACTIVITY_DROPDOWN_LABEL = t('Activity actions');


export const EDIT_ACTIVITY_ID = 'edit_activity';
export const EDIT_IN_FLOW_DESIGNER_ID = 'edit_flow';
export const DELETE_ACTIVITY_ID = 'delete_activity';
export const MOVE_ACTIVITY_ID = 'move_activity';

/**
 * This is the mapping between id of the drop down item
 * and the application action name it should trigger
 */
const makeDropDownIdToActionMap = () => {
	let mapping = {};
	mapping[EDIT_ACTIVITY_ID] = POPULATE_CONFIGURATION_PANEL;
	mapping[EDIT_IN_FLOW_DESIGNER_ID] = LOAD_FLOW_DESIGNER_CLICKED;
	mapping[DELETE_ACTIVITY_ID] = DELETE_ACTIVITY_CLICKED;
	mapping[MOVE_ACTIVITY_ID] = MOVE_ACTIVITY_CLICKED;
	return mapping;
};

export const dropDownIdToActionMap = makeDropDownIdToActionMap();

export const activityDropdownActions = items => {
	return [
		{
			id: 'pd_activity_dropdown',
			label: ACTIVITY_DROPDOWN_LABEL,
			icon: 'ellipsis-v-fill',
			size: 'xs',
			items
		}
	];
};

export const isSubFlow = card => {
	const flowType = get(card, 'activity_definition.flow_subflow_definition.sys_hub_flow[0].type');
	return flowType && flowType === 'subflow';
};

export const buildActivityDropdownActions = (card, userRoles) => {
	const dropdownItems = [
		{id: EDIT_ACTIVITY_ID, label: EDIT_ACTIVITY},
		{id: DELETE_ACTIVITY_ID, label: DELETE_ACTIVITY},
		{id: MOVE_ACTIVITY_ID, label: MOVE_ACTIVITY}
	];

	const source = get(card, 'activity_definition.source_type');

	if (!source)
		return activityDropdownActions(dropdownItems);

	let newItems = [...dropdownItems];
	const sourceIsFlow = source === SYS_HUB_FLOW_TABLE;

	// defaulting to action as the source
	let activityDefinitionSourceLabel = EDIT_ACTION;
	let userHasAccess = false;

	if (isArrayWithValues(userRoles)) {
		userHasAccess = sourceIsFlow ? userRoles.includes(FLOW_DESIGNER_ROLE) : userRoles.includes(ACTION_DESIGNER_ROLE);
		userHasAccess = userHasAccess || userRoles.includes(ADMIN_ROLE);
	}

	if (sourceIsFlow)
		activityDefinitionSourceLabel = isSubFlow(card) ? EDIT_SUB_FLOW : EDIT_FLOW;

	if (userHasAccess)
		newItems.splice(1,0,{id: EDIT_IN_FLOW_DESIGNER_ID, label: activityDefinitionSourceLabel});

	return activityDropdownActions(newItems);
};
