import {get, set} from 'lodash';
import * as Utils from '../config-utils';
import * as AppConst from '../../../constants/shared';
import * as ActionTypes from '../../../constants/action-types';
import {getGlideVarFieldValues, getPillsArray} from '../../../util/pill-utils';
import {getConfigurationStateFromProperties} from '../../../util/selectors';
import {isFieldValid} from '../../dynamic-form/dynamic-form-helpers';


export const updatePanelFormField = ({action: {payload}, properties, updateProperties}) => {
	const allFields = get(properties, 'configurationState.config.fields', []);
	const updatedFields = allFields.map(field => {
		if (field.name === payload.name)
			return {
				...field,
				value: payload.value,
				displayValue: payload.displayValue,
				invalid: !isFieldValid(payload)
			};

		return field;
	});

	updateProperties({
		configurationState: {
			...properties.configurationState,
			config: {
				...properties.configurationState.config,
				fields: updatedFields
			}
		}
	});
};

export const prepPayloadAndDispatchSave = ({properties: {configurationState, invalidFields}, dispatch}) => {
	if (invalidFields)
		return;

	const type = get(configurationState, 'type', '');
	const config = get(configurationState, 'config', {});
	const sysId = get(config, 'sys_id', '');
	const updatedConfig = Utils.prepConfigForSaving(config);
	const basePayload = { config: { ...updatedConfig, sysId }, type };

	if (type === AppConst.LANE || type === AppConst.PLAYBOOK)
		dispatch(ActionTypes.SAVE_CONFIGURATION, basePayload);

	if (type === AppConst.ACTIVITY)
		dispatch(ActionTypes.SAVE_CONFIGURATION, {
			...basePayload,
			config: {
				...basePayload.config,
				inputs: getGlideVarFieldValues(config)
			}
		});
};

export function modifyConditionToRun(coeffects) {
	const {
		updateProperties,
		state: {properties: {configurationState}}
	} = coeffects;
	const stagedCondition = get(configurationState, 'stagedConfig.condition_to_run');
	const allFields = get(configurationState, 'config.fields', []);

	const updatedFields = allFields.map(field => {
		if (field.name === 'condition_to_run')
			return {
				...field,
				value: stagedCondition,
			};

		return field;
	});

	set(configurationState, 'stagedConfig.condition_to_run', undefined);

	updateProperties({
		configurationState: {
			...configurationState,
			config: {
				...configurationState.config,
				fields: updatedFields
			}
		}
	});
}

export function clearConditionToRunStagedChange(coeffects) {
	const {
		updateProperties,
		state: {properties: {configurationState}}
	} = coeffects;

	set(configurationState, 'stagedConfig.condition_to_run', undefined);
	updateConfigurationState(updateProperties, configurationState);
}

export function stageConditionToRunQueryChange(coeffects) {
	const {
		action: {payload: {value}},
		updateProperties,
		state: {properties: {configurationState}}
	} = coeffects;

	set(configurationState, 'stagedConfig.condition_to_run', value);
	updateConfigurationState(updateProperties, configurationState);
}

function updateConfigurationState(updateProperties, configurationState) {
	updateProperties({
		configurationState: {
			...configurationState,
		}
	});
}

export const showConfigurationModal = ({properties, updateProperties}) => {
	const configState = getConfigurationStateFromProperties(properties);
	updateProperties({configurationState: {...configState, showConfigModal: true, panelOpen: false}});
};

export const hideConfigurationModal = ({properties, updateProperties}) => {
	const configState = getConfigurationStateFromProperties(properties);
	updateProperties({configurationState: {...configState, showConfigModal: false}});
};

const setGlideVarFields = (name, {value, mappingValue}) => {
	return {
		variableName: name,
		variableValue: value,
		variableMapping: mappingValue,
	};
};

export const updatePillField = ({action : { payload }, properties, updateProperties}) => {

	//get the current inputs array
	const allInputs = get(properties, 'configurationState.config.inputs', []);
	let updatedInputs = allInputs;

	//pillLabel and pillValue are needed by pill-field
	const value = payload.pillValue || payload.value;
	const displayValue = payload.pillLabel || payload.pillValue || payload.displayValue || payload.value;

	//getPillsArray returns a boolean false or an array or null..
	const valueContainsPill = !! getPillsArray(value);

	if (valueContainsPill) {
		updatedInputs = allInputs.map(input => {
			if (input.name === payload.name)
				return {
					...input,
					value: '',
					displayValue,
					glideVarFields: setGlideVarFields(payload.name, {mappingValue: value})
				};
			else
				return input;
		});
	}
	else {
		updatedInputs = allInputs.map(input => {
			if (input.name === payload.name)
				return {
					...input,
					value,
					displayValue,
					glideVarFields: setGlideVarFields(payload.name, {value})
				};
			else
				return input;
		});
	}

	updateProperties({
		configurationState: {
			...properties.configurationState,
			config: {
				...properties.configurationState.config,
				inputs: updatedInputs
			}
		}
	});
};

export const updateFormField = (coeffects) => {
	const { action: { payload } } = coeffects;
	if (payload.isGlideVar)
		updatePillField(coeffects);
	else
		updatePanelFormField(coeffects);
};
