import {createCustomElement} from '@servicenow/ui-core';
import styles from './_textual-message.scss';
import * as Shared from '../../constants/shared';


createCustomElement('sn-pd-textual-message', {
	view: ({properties: {message, level, icon}}) => {
		return (
			<div className={`message-container ${level.toLowerCase()}`}>
				<now-icon icon={icon} size='sm' />
				<span>{message}</span>
			</div>
		);
	},
	styles,
	properties: {
		message: {default: ''},
		level: {default: Shared.ERROR},
		icon: {
			computed({properties: {level}}) {
				const icons = {
					[Shared.WARNING]: Shared.WARNING_ICON_FILL,
					[Shared.INFO]: Shared.INFO_ICON_FILL,
					[Shared.ERROR]: Shared.ERROR_ICON_FILL
				};
				return icons[level];
			},
			default: Shared.ERROR_ICON_FILL
		}
	}
});
