import {createCustomElement, actionTypes as SeismicActions} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {view} from './template-value-view';
import * as ActionTypes from '../../constants/action-types';
import {requestFieldMetaEffect} from '../../actions/http-effects';
import * as Handlers from './template-value-action-handlers';

createCustomElement('sn-pd-template-value', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		decodedValue: [],		// value used to create the view elements
		fieldMeta: [],			// field meta information for the table
		isLoading: true		// used to implement a loader while the table information isn't present
	},
	properties: {
		table: '',				// table to get field meta for
		encodedValue: '',		// carrot separated key value pair 'var1=val1^var2=val2^'
		label: '',				// label to be used for the entire field
		onChangeActionType: '',	// action fired after a encoded value change occurs
		field: {},
		dotWalkData: {},
		openInModal: false
	},
	actionHandlers: {
		[SeismicActions.COMPONENT_BOOTSTRAPPED]: Handlers.handleComponentBootstrapped,
		[SeismicActions.COMPONENT_PROPERTY_CHANGED]: Handlers.handlePropertyChanged,
		[ActionTypes.REQUEST_FIELD_META]: requestFieldMetaEffect,
		[ActionTypes.REQUEST_FIELD_META_SUCCEEDED]: Handlers.handleRequestFieldMetaSucceeded,
		[ActionTypes.TEMPLATE_FIELD_ADD_ROW]: Handlers.handleAddRow,
		[ActionTypes.TEMPLATE_FIELD_REMOVE_ROW]: Handlers.handleRemoveRow,
		[ActionTypes.TEMPLATE_ROW_KEY_SELECTED]: Handlers.handleRowKeySelected,
		[ActionTypes.TEMPLATE_ROW_VALUE_SELECTED]: Handlers.handleRowValueSelected
	}
});
