import '@servicenow/now-button';
import '../../panel-layout';
import '../../textual-message';
import {advancedConfigButton} from './advanced-config-button';
import * as ActionTypes from '../../../constants/action-types';
import * as AppConst from '../../../constants/shared';
import * as Consts from '../config-constants';
import {get, startCase, isEmpty} from 'lodash';
import {t} from 'sn-translate';
import '../../dynamic-form';
import {isArrayWithValues} from '../../../util/general-utils';
import '../../dot-walker';
import '../../condition-to-run';
import '../../configuration-components/configuration-modal';
import {getDisplayPrefsEnabled} from '../../../util/selectors';


const configurationView = (state, dispatch) => {
	const {
		properties: {
			heading,
			editLinkUrl,
			configurationState,
			formFields,
			textualMessage,
			userData,
			ariaLiveContent,
			sysId,
			dotWalkData
		},
	} = state;

	const language = get(userData, 'user.language', 'en');
	const displayPrefsEnabled = getDisplayPrefsEnabled(userData);

	const saveConfiguration = () => {
		dispatch(Consts.SAVE_CONFIGURATION_DATA_CLICKED);
	};

	const hideConfiguration = () => dispatch(ActionTypes.HIDE_CONFIGURATION_PANEL_REQUESTED);

	const handleSidebarButtonClick = ({icon, actionType}) => {
		const configType = get(configurationState, 'type') || AppConst.PLAYBOOK;

		if (icon === AppConst.SETTINGS_BUTTON_ICON && get(configurationState, 'panelOpen'))
			dispatch(ActionTypes.HIDE_CONFIGURATION_PANEL_REQUESTED);
		else
			dispatch(actionType, {configType});
	};

	const renderActionButtons = () => {
		const buttonStyle = {width : '139px'};

		return (
			<div className='action-buttons'>
				<now-button style={buttonStyle} on-click={hideConfiguration}>
					{AppConst.CANCEL}
				</now-button>
				<now-button
					style={buttonStyle}
					on-click={saveConfiguration}
					variant='primary'
				>
					{AppConst.SAVE}
				</now-button>
			</div>
		);
	};

	const renderSidebarButtons = () => {
		return Consts.CONFIG_PANEL_BUTTON_DATA.map(button => {
			let configAria = {'aria-label': button.tooltipContent};

			if (button.icon === AppConst.SETTINGS_BUTTON_ICON)
				configAria['aria-expanded'] = get(configurationState, 'panelOpen', '').toString();

			return (
				<now-button-iconic
					bare
					icon={button.icon}
					size='lg'
					tooltip-content={button.tooltipContent}
					configAria={configAria}
					variant='tertiary'
					on-click={() => handleSidebarButtonClick(button)}>
				</now-button-iconic>
			);
		});
	};

	const renderConfigPanelHeader = () => {
		return (
			<div className='panel-heading-container'>
				<div className='panel-heading'>
					{heading}
				</div>
			</div>
		);
	};

	return (
		<sn-pd-panel-layout open={configurationState.panelOpen}>
			<div slot='sidebar-buttons' tabindex='-1'>
				{renderSidebarButtons()}
			</div>

			<div slot='panel-title'>
				{t('{0} properties', startCase(configurationState.type))}
			</div>

			<div slot='panel-heading' className='panel-header-holder'>
				{renderConfigPanelHeader()}
			</div>

			<div slot='panel-content'>
				<div id='aria-context-provider' aria-live='assertive' role='alert'>{ariaLiveContent}</div>

				{ !isEmpty(textualMessage) ?
					<div className='textual-message-container'>
						<sn-pd-textual-message message={textualMessage.message} level={textualMessage.type} />
					</div> : ''
				}

				<div className='panel-form-container'>
					{
						//there are no inputs on formFields and the configurationState inputs field is empty
						configurationState.panelOpen && isArrayWithValues(formFields) ?
							<sn-pd-dynamic-form
								sysId={sysId}
								fields={formFields}
								dotWalkData={dotWalkData}
								onChangeActionType={Consts.PANEL_FORM_FIELD_UPDATED}
								openInModal={false}
							/> : null
					}
				</div>

				<div className='advanced-button-container'>
					{editLinkUrl ? advancedConfigButton(configurationState.type, editLinkUrl, dispatch, language, displayPrefsEnabled): ''}
				</div>

				{renderActionButtons()}
			</div>
		</sn-pd-panel-layout>
	);
};


export {configurationView};
