import '@servicenow/now-button';
import '@servicenow/now-icon';
import '@servicenow/now-text-link';
import startProcessImage from './process-designer-empty.png';
import * as ActionTypes from '../../constants/action-types';
import * as Shared from '../../constants/shared';
import * as Const from './start-process-constants';

export const view = (state, {dispatch}) => {
	const {href} = state.properties;

	const handleAddLaneClicked = (event) => {
		event.stopPropagation();
		dispatch(ActionTypes.CREATE_LANE_CLICKED);
	};

	return (
		<div className='start-process-container'>
			<img src={startProcessImage} width='320' height='304' id='the-dude'/>
			<div className='start-process-text'>
				<div>{Const.START_PROCESS_TEXT_PT1}</div>
				<div>{Const.START_PROCESS_TEXT_PT2}</div>
			</div>
			<div className='action-button-container'>
				<now-button
					size='md'
					on-click={handleAddLaneClicked}
					variant='primary'
				>
					<now-icon icon='plus-fill' size='md'></now-icon>
					<span>{Shared.ADD_LANE}</span>
				</now-button>
			</div>
			<div className='help-text-container'>
				<div>{Const.START_PROCESS_HELP_PT1}</div>
				<div>{Const.START_PROCESS_HELP_PT2}
					<now-text-link
						label={Const.DESIGN_YOUR_AUTOMATED_PROCESS}
						variant='primary'
						href={href}
						opens-window
					/>
				</div>
			</div>
		</div>
	);
};
