import {createCustomElement, actionTypes as SeismicActions} from '@servicenow/ui-core';
import * as ActionTypes from './constants/action-types';
import * as GraphQL from './actions/graphql-effects';
import * as HTTP from './actions/http-effects';
import * as Playbook from './actions/playbook-actions';
import * as Lane from './actions/lane-actions';
import * as Card from './actions/card-actions';
import * as Alert from './actions/alert-list-actions';
import * as Error from './actions/error-actions';
import {get} from 'lodash';
import {provider} from './designer-data-provider-view';
import * as ConfigActions from './actions/configuration-actions';
import * as Rearrange from './actions/rearrange-actions';
import * as Modal from './actions/modal-actions';
import {openModal} from './util/modal-util';
import {getSelectableProp} from './util/general-utils';
import {SN_CANVAS_SESSION} from './components/canvas/constants/constants';
import * as Consts from './constants/shared';
import {ALERT_DISMISSED} from './components/alert/alert-list-constants';

createCustomElement('sn-pd-provider', {
	view: provider,
	initialState: {},
	properties: {
		selectedContent: { default: null },
		playbook: { selectable: true },
		laneStartRules: { selectable: true },
		activityStartRules: { selectable: true },
		laneDependencies: { selectable: true },
		cardDependenciesByLane: { selectable: true },
		activitiesDependedOn: { selectable: true },
		boardData: { selectable: true },
		configurationState: { selectable: true, default: {} },
		configurationDotWalkData: { selectable: true, default: {} },
		modal: { selectable: true, default: {} },
		alerts: { selectable: true, default: [] },
		showStartProcessElements: { selectable: true, default: false },
		triggerInstanceSysId: { selectable: true },
		userData: {
			get default() {return getSelectableProp(SN_CANVAS_SESSION, Consts.SESSION_USER);}
		},
		loadingPAD: { selectable: true, default: false }
	},
	actionHandlers: {
		[ActionTypes.URL_CONTENT_LOADED]: Playbook.handlePlaybookUrlChange,
		[SeismicActions.COMPONENT_PROPERTY_CHANGED]: Playbook.checkIfPlaybookUpdated,
		// playbook actions
		[ActionTypes.FETCH_PLAYBOOK]: Playbook.handleFetchPlaybook,
		[ActionTypes.FETCH_PLAYBOOK_REQUESTED]: GraphQL.fetchPlaybookEffect,
		[ActionTypes.FETCH_PLAYBOOK_SUCCEEDED]: Playbook.handleFetchPlaybookSucceeded,
		[ActionTypes.SUBSCRIBE_PLAYBOOK]: GraphQL.subscribePlaybookEffect,
		[ActionTypes.SUBSCRIBE_PLAYBOOK_UPDATED]: Playbook.handleSubscribePlaybookUpdated,
		[ActionTypes.SAVE_PLAYBOOK_CONFIGURATION_REQUESTED]: Playbook.handleSavePlaybookConfigRequested,
		[ActionTypes.SAVE_PLAYBOOK_CONFIGURATION]: HTTP.savePlaybookConfigEffect,
		[ActionTypes.SAVE_PLAYBOOK_CONFIGURATION_SUCCEEDED]: Playbook.handleSavePlaybookConfigSucceeded,
		[ActionTypes.ACTIVATE_PLAYBOOK_REQUESTED]: GraphQL.activatePlaybookEffect,
		[ActionTypes.ACTIVATE_PLAYBOOK_RESPONSE_RECEIVED]: Playbook.handleActivatePlaybookResponse,
		[ActionTypes.DEACTIVATE_PLAYBOOK_REQUESTED]: GraphQL.deactivatePlaybookEffect,
		[ActionTypes.SHOW_ALL_PLAYBOOKS_CLICKED]: Playbook.handleShowAllPlaybooksClicked,
		[ActionTypes.VALIDATE_PLAYBOOK_REQUESTED]: GraphQL.validatePlaybookEffect,
		[ActionTypes.VALIDATE_PLAYBOOK_SUCCEEDED]: Playbook.handleValidatePlaybookSucceeded,
		// lane actions
		[ActionTypes.DELETE_LANE_REQUESTED]: GraphQL.deleteLaneEffect,
		[ActionTypes.DELETE_LANE_SUCCEEDED]: Lane.handleDeleteLaneSucceeded,
		[ActionTypes.CREATE_PLACEHOLDER_LANE_REQUESTED]: GraphQL.createPlaceHolderLaneEffect,
		[ActionTypes.SAVE_PLACEHOLDER_LANE_SUCCEEDED]: Lane.reorderLanesAndOpenConfigPanel,
		[ActionTypes.SAVE_AFTER_LANE_REARRANGE_REQUESTED]: GraphQL.saveOrderAfterRearrangeLaneEffect,
		[ActionTypes.SAVE_AFTER_LANE_REARRANGE_SUCCEEDED]: Lane.updateLaneOrderSucceeded,
		[ActionTypes.REMOVE_PILLS_AND_REORDER_AFTER_LANE_DELETION]: Rearrange.removePillsAndReorderAfterLaneDeletion,
		// configuration side panel
		[ActionTypes.POPULATE_CONFIGURATION_PANEL]: ConfigActions.showConfigurationPanel,
		[ActionTypes.HIDE_CONFIGURATION_PANEL_REQUESTED]: ConfigActions.hideConfigurationPanel,
		[ActionTypes.SAVE_ACTIVITY_CONFIGURATION_REQUESTED]: GraphQL.saveActivityEffect,
		[ActionTypes.SAVE_ACTIVITY_CONFIGURATION_SUCCEEDED]: Card.handleSaveCardConfigSucceeded,
		[ActionTypes.SAVE_LANE_CONFIGURATION_REQUESTED]: GraphQL.saveLaneEffect,
		[ActionTypes.SAVE_LANE_CONFIGURATION_SUCCEEDED]: Lane.handleSaveLaneConfigSucceeded,
		[ActionTypes.REQUEST_DOT_WALK_DATA_SUCCEEDED]: ConfigActions.updateDotWalkData,
		[ActionTypes.REQUEST_DOT_WALK_DATA]: HTTP.getDotWalkData,
		[ActionTypes.UPDATE_CONFIG_TRIGGER_ELEMENT_FOR_NEW_LANE]: ConfigActions.updateConfigTriggerElement,
		// Handlers for using activity picker and adding card to lane
		[ActionTypes.POST_NEW_LANE_CARD]: GraphQL.addNewCardToLane,
		[ActionTypes.SAVE_NEW_ACTIVITY_SUCCEEDED]: Playbook.addNewCardToPlaybook,
		[ActionTypes.REORDER_CARDS_DELETE_INVALID_PILLS]: Rearrange.handleAftermathOfAddOrDeleteCard,
		// Rearrange after adding, deleting, reordering cards
		[ActionTypes.UPDATE_UI_AFTER_REARRANGE]: {
			effect({action, updateProperties}) {
				updateProperties({
					playbook: get(action, 'payload.playbook', {}),
					configurationState: get(action, 'payload.configurationState', {})
				});
			}
		},
		[ActionTypes.SAVE_AFTER_ACTIVITY_REARRANGE_REQUESTED]: GraphQL.saveAfterRearrangeActivityEffect,
		[ActionTypes.SAVE_AFTER_ACTIVITY_REARRANGE_SUCCEEDED]: Card.handleRearrangedCardUpdate,
		// card actions
		[ActionTypes.DELETE_ACTIVITY_REQUESTED]: GraphQL.deleteActivityEffect,
		[ActionTypes.DELETE_ACTIVITY_SUCCEEDED]: Card.handleCardDeleteSuccess,
		// confirm modal
		[ActionTypes.CLOSE_MODAL]: Modal.handleCloseConfirmModal,
		[ActionTypes.OPEN_MODAL_REQUESTED]: openModal,
		[ActionTypes.NOW_MODAL_OPENED_SET]: Modal.handleCloseConfirmModal,
		// alert actions
		[ActionTypes.SHOW_ALERTS]: Alert.handleShowAlerts,
		[ALERT_DISMISSED]: Alert.handleAlertDismissed,
		// error actions
		[ActionTypes.GRAPHQL_ERROR_OCCURRED]: Error.handleGraphQLErrors,
		[ActionTypes.NETWORK_ERROR_OCCURRED]: Error.handleNetworkError,
		[ActionTypes.PLAYBOOK_VALIDATION_ERROR_OCCURRED]: Error.handlePlaybookValidationError,
	}
});
