import * as WalkerConsts from './dot-walk-constants';
import get from 'lodash/get';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import {isObjectWithValues, getStateDotWalkObject} from '../../util/general-utils';
import {TRIGGER} from '../../constants/shared';
import * as Utils from './dot-walk-utils';

export const toggleWalker = ({state, updateState}) => {
	if (state.showWalker) // if current state is true
		closeAndClearWalker({updateState});
	else
		updateState({showWalker: true});
};

export const handleDotWalkTriggered = ({state, action: {payload}, updateState, dispatch}) => {
	const {pathIndex, source, referenceSource, displayValue, fieldName} = payload;
	const fieldType = Utils.getFieldType(state);
	const dotWalkData = Utils.getDotWalkData(state);
	const currentSelection = dotWalkData.firstColumn[fieldName] || {};

	let currentSelectionChildren = {...currentSelection.outputs};
	Object.keys(currentSelectionChildren).map(key => {
		const output = currentSelectionChildren[key];
		output.disabled = Utils.isItemDisabled(output.type, fieldType);
	});

	let dotWalkPath = Utils.getDotWalkPath(state) || Utils.getTriggerPath(state);
	dotWalkPath.splice(pathIndex);
	dotWalkPath.push({displayValue, field: fieldName, parent: source});

	if (isObjectWithValues(currentSelectionChildren))
		dotWalkPath.push(createNewPath(currentSelectionChildren, fieldName));
	else
		dispatch(WalkerConsts.DOT_WALK_FETCH_REQUESTED, {table: referenceSource, appendToPayload: pathIndex});

	updateState({walkerPath: dotWalkPath});
};

export const fetchTableData = createHttpEffect('api/now/processflow/table/:table/schema?get_choices=true', {
	method: 'GET',
	pathParams: ['table'],
	successActionType: WalkerConsts.DOT_WALK_FETCH_SUCCEEDED,
	errorActionType: WalkerConsts.DOT_WALK_FETCH_FAILED
});

export const updatePickerData = ({action: {payload}, updateState, state}) => {
	const columns = get(payload, 'result.data.columns', []);
	const table = get(payload, 'result.data.table', '');
	const fieldType = Utils.getFieldType(state);

	let dotWalkData = Utils.getDotWalkData(state);
	const newDotWalkData = processTableColumns(columns, fieldType);
	dotWalkData[table] = newDotWalkData;

	let dotWalkPath = Utils.getDotWalkPath(state);
	dotWalkPath.push(createNewPath(newDotWalkData, table));

	updateState({
		data: dotWalkData,
		walkerPath: dotWalkPath
	});
};

export const formatAndAnnounceSelection = ({action: {payload}, state, dispatch}) => {
	const dotWalkPath = Utils.getDotWalkPath(state) || Utils.getTriggerPath(state);
	const pillData = createPillData(payload, dotWalkPath);
	const onPillSelectAction = get(state, 'properties.onPillSelectAction');

	if (onPillSelectAction)
		dispatch(onPillSelectAction, pillData);

	dispatch(WalkerConsts.CLOSE_AND_CLEAR_WALKER);
};

export function closeAndClearWalker({updateState}) {
	updateState({
		showWalker: false,
		data: '',
		walkerPath: ''
	});
}

export function processTableColumns(columns, fieldType) {
	let newDotWalkData = {};

	columns.forEach(column => {
		const columnDefinition = get(column, 'definition', {});

		newDotWalkData[column.name] = {
			displayValue: column.label,
			isReference: !!columnDefinition.reference,
			referenceDataKey: columnDefinition.reference,
			hintText: columnDefinition.hint,
			disabled: Utils.isItemDisabled(columnDefinition.type, fieldType)
		};
	});

	return {...newDotWalkData, state: getStateDotWalkObject()};
}

export function createNewPath(newDotWalkData, table) {
	if (!isObjectWithValues(newDotWalkData))
		return;

	const firstKey = Object.keys(newDotWalkData)[0];
	return {
		displayValue: newDotWalkData[firstKey].displayValue,
		field: firstKey,
		parent: table
	};
}

export function createPillData(payload, dotWalkPath) {
	const {displayValue, field, panelIndex} = payload;
	const isProcessState = panelIndex === 0 && field === WalkerConsts.PROCESS_STATE_FIELD;
	if (isProcessState)
		return {pillLabel: WalkerConsts.PROCESS_STATE_DISPLAY, pillValue: `{{pd.${WalkerConsts.PROCESS_STATE_FIELD}}}`};

	const isTriggerPath = dotWalkPath[0] && dotWalkPath[0].field === WalkerConsts.TRIGGER_ACTION;

	let value = isProcessState ? '{{pd.' : '{{act.';
	value = isTriggerPath ? '{{pd.inputRecord.' : value;
	let pillDisplayValue = isTriggerPath ?  TRIGGER + WalkerConsts.PILL_DELIMITER : '';

	for (let i = 0; i < dotWalkPath.length; i++) {
		const isTrigger = dotWalkPath[i].field === WalkerConsts.TRIGGER_ACTION;

		if (panelIndex !== i && !isTrigger) {
			value = `${value}${dotWalkPath[i].field}.`,
			pillDisplayValue = pillDisplayValue + dotWalkPath[i].displayValue + WalkerConsts.PILL_DELIMITER;
		}
	}

	return {
		pillValue: value + field + '}}',
		pillLabel: pillDisplayValue + displayValue
	};
}
