import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import {computeShowFromProps} from './util';
import {selectedContentInitProp} from './behaviors';

//original had behavior for user roles

createCustomElement('sn-pd-picker-option', {
	type: snabbdom,
	view,
	properties: {
		show: {
			computed({properties}){
				return computeShowFromProps(properties);
			}
		},
		pickerCondition: {
			reflect: true
		},
		selectedContent: {
			default: null
		},
		elemRef: {default: null}
	},
	behaviors: [selectedContentInitProp]
});
