import {createCustomElement} from '@servicenow/ui-core';
import {view} from './alert-list-view';
import * as AlertConst from './alert-list-constants';
import * as Actions from './actions/actions';
import {ERROR} from '../../constants/shared';

/**
 * @param (property) {Array.<Object>} alerts
 *
 * Three levels are supported: 'error', 'warning', & 'info'.
 * These correspond with the red, yellow, or blue alert.
 *
 * Usage:
 *
 * dispatch(SHOW_ALERTS, [alerts])
 *
 * Example alerts:
 *
 *  [{
 * 	  level: 'error',
 *	  message: 'My error message',
 *	},{
 *	  level: 'warning',
 *	  message: 'My warning message'
 *	},{
 *	  level: 'info',
 *	  message: 'My info message'
 *	},{
 *	   level: 'error,
 *	   message: 'My custom message',
 *	   textLinkProps: {label: string, href: string}
 *	}]
 *
 * An icon and alert header will be filled in automatically by this component if applicable.
 */

createCustomElement('sn-pd-alert-list', {
	view,
	properties: {
		alerts: {
			default: []
		},
		alertItems: {
			computed({properties: {alerts}}) {
				return alerts.map(alert => {
					return {
						...AlertConst.ALERTS[alert.level || ERROR],
						...{
							content: alert.message,
							action: {type: AlertConst.DISMISS},
							id: alert.id,
							textLinkProps: alert.textLinkProps || {}
						}
					};
				});
			}
		},
	},
	actionHandlers: {
		[AlertConst.ALERT_ACTION_CLICKED]: Actions.handleActionClicked,
		[AlertConst.DISMISS_ALL]: Actions.handleDismissAll
	}
});
