import { createCustomElement } from '@servicenow/ui-core';

createCustomElement('card-source-placeholder', {
	view: ({ properties }) => (
		<div style={{
			height: `${properties.height}px`,
			width: `${properties.width}px`
		}}>
			<div style={{
				background: '#d1d6d6',
				width: '275px',
				height: '130px'
			}} />
		</div>
	),
	properties: {
		width: { default: '274px' },
		height: { default: '130px' }
	}
});

createCustomElement('card-destination-placeholder', {
	view: ({ properties }) => (
		<div style={{
			height: `${properties.height}px`,
			width: `${properties.width}px`
		}}>
			<div style={{
				border: '2px dashed #acb4b5',
				background: '#d1d6d6',
				width: '275px',
				height: '130px'
			}} />
		</div>
	),
	properties: {
		width: { default: '274px' },
		height: { default: '130px' }
	}
});
