// Generic Glide GraphQL responses come with unnecessary wrappers that are the result
// of the underlying schema and implementation. This method tries to remove some of that.
import {isArrayWithValues} from '../../util/general-utils';

const isGlideGraphQLBoilerplate = key => key.startsWith('_') || key.endsWith('_Query') || key.endsWith('_Subscription');
const hasGlideGraphQLBoilerplateChild = obj => Object.keys(obj).length === 1 && isGlideGraphQLBoilerplate(Object.keys(obj)[0]);
export const removeGlideGraphQLBoilerplate = obj => {
	return Object.keys(obj).reduce((map, current) => {
		const currentValue = obj[current];

		if (currentValue && typeof currentValue === 'object' && isGlideGraphQLBoilerplate(current))
			Object.assign(map, removeGlideGraphQLBoilerplate(currentValue));
		else if (currentValue && typeof currentValue === 'object') {
			const child = hasGlideGraphQLBoilerplateChild(currentValue) ?
				currentValue[Object.keys(currentValue)[0]] :
				currentValue;
			map[current] = Array.isArray(child) ?
				child.map(o => removeGlideGraphQLBoilerplate(o)) :
				removeGlideGraphQLBoilerplate(child);
		}
		else
			map[current] = currentValue;

		return map;
	}, {});
};

// Due to the underlying Glide schema, getting a simple value returns a response like:
// name: { value: "Test Name" }
//
// This method recursively converts such convention to:
// name: "Test Name"
// Note: this will only convert objects that have a single property called 'value'
const isValueOnly = obj => Object.keys(obj).length === 1 && Object.keys(obj)[0] === 'value';
export const flattenValues = obj => {
	return Object.keys(obj).reduce((map, current) => {
		const currentValue = obj[current];

		if (currentValue && typeof currentValue === 'object') {
			const value = isValueOnly(currentValue) ? currentValue.value : currentValue;

			if (Array.isArray(value))
				map[current] = value.map(o => flattenValues(o));
			else if (typeof value === 'object' && value !== null)
				map[current] = flattenValues(value);
			else
				map[current] = value;
		}
		else
			map[current] = currentValue;

		return map;
	}, {});
};

export const flattenCollection = (arr) => {
	if (!isArrayWithValues(arr))
		return arr;
	return arr.map(obj => flattenValues(obj));
};
