import {get} from 'lodash';

export const isDropdownInPath = (event, dropdown) => {
	const path = typeof event.composedPath === 'function' && event.composedPath();

	if (!path)
		return false;

	return path.findIndex(node =>
		node.tagName && node.tagName.toUpperCase() === dropdown.toUpperCase()
	) !== -1;
};

export const setFocusToConfigTriggerElement = (configurationState) => {
	const elementToFocus = get(configurationState, 'triggerElement');

	if (elementToFocus)
		elementToFocus.focus();
};

