import {createCustomElement} from '@servicenow/ui-core';
import {laneHeaderView} from './lane-header-view';
import styles from './lane-header.scss';
import * as ActionTypes from '../../../constants/action-types';
import {LANE} from '../../../constants/shared';

createCustomElement('sn-pd-lane-header', {
	view: laneHeaderView,
	properties: {
		data: { default: {}},
		variant: {default: 'chevron'},
		name: '',
		subText: '',
		hideOptions: '',
		nameOnly: '',
		isLane: {default: true}
	},
	actionHandlers: {
		[ActionTypes.DROPDOWN_ITEM_CLICKED] : {
			effect: ({action:{payload}, dispatch}) => {
				const {item:{action}} = payload;
				const data = action.payload;
				dispatch(`PLAYBOOK#${action.type}_LANE_CLICKED`, {configType: LANE, data});
			},
			stopPropagation: true
		}
	},
	styles: [styles]
});
