import '@servicenow/now-modal';
import '@servicenow/now-tabs';
import {t} from 'sn-translate';
import '../../dynamic-form';
import {get} from 'lodash';
import * as Consts from '../config-constants';
import {DISMISS_CONFIG_MODAL} from '../../../constants/action-types';
import {isArrayWithValues} from '../../../util/general-utils';

export const view = (state) => {
	const {selectedTab} = state;
	const {configurationState, formFields, dotWalkData} = state.properties;
	const {fields, inputs} = formFields;
	const showModal = get(configurationState, 'showConfigModal');

	let tabItems = [{id: Consts.GENERAL_TAB, label :t('General')}];
	if (isArrayWithValues(inputs))
		tabItems.push({id: Consts.INPUT_TAB, label :t('Inputs')});

	const displayTab = () => {
		const formFieldSelection = selectedTab === Consts.GENERAL_TAB ? fields : inputs;

		return (
			<sn-pd-dynamic-form
				fields={formFieldSelection}
				dotWalkData={dotWalkData}
				onChangeActionType={Consts.PANEL_FORM_FIELD_UPDATED}
				openInModal={true}
			/>
		);
	};

	return (
		<now-modal
			className='sn-pd-config-modal'
			headerLabel={t('Additional Properties')}
			size="lg"
			opened={showModal}
			contentFullWidth={true}
			footerActions={[
				{
					'label': t('Done'),
					'variant': 'primary',
					'clickActionType': Consts.SAVE_CONFIGURATION_DATA_CLICKED
				},
				{
					'label': t('Cancel'),
					'variant': 'secondary',
					'clickActionType': DISMISS_CONFIG_MODAL
				}
			]}
		>
			<now-tabs
				items={tabItems}
				selectedItem='general'
			/>

			<div id='modal-content'>
				{displayTab()}
			</div>
		</now-modal>
	);
};
