import {get, isEmpty} from 'lodash';
import {isArrayWithValues, isObjectWithValues} from './general-utils';
import {ACTIVITY_LABEL} from '../constants/shared';

export const getActivityPillDependencies = (activities) => {
	const pillsDependedOn = {};

	if (!isObjectWithValues(activities))
		return pillsDependedOn;

	Object.values(activities).forEach(activity => {
		const { inputs = [] } = activity;

		inputs.forEach(input => {
			const mappingValue = get(input, 'glideVarFields.variableMapping');

			if (!mappingValue)
				return pillsDependedOn;

			const allPills = getPillsArray(mappingValue);

			if (allPills) {
				allPills.forEach(pill => {
					const sysId = stripOutSysId(pill);

					if (!pillsDependedOn[sysId])
						pillsDependedOn[sysId] = {dependents: {}};

					if (!pillsDependedOn[sysId].dependents[activity.sys_id])
						pillsDependedOn[sysId].dependents[activity.sys_id] = {order: activity.order, label: activity.label, inputs: {}};

					pillsDependedOn[sysId].dependents[activity.sys_id].inputs[input.name] = {label: input.label};
				});
			}
		});
	});

	return pillsDependedOn;
};

export const removeAllPillsById = (activitySysId, inputValue) => {
	const regex =  RegExp(`{{act.${activitySysId}.*?.*?}}`, 'g');

	return inputValue.replace(regex, '').trim();
};

export function stripOutSysId(pill) {
	// return text inside first set of periods after "{{act".  {{act.THIS_TEXT.dfadf}}
	const regex = RegExp('^{{act.([^.]*)');
	const pillPrefixAndSysId = regex.exec(pill);

	if (pillPrefixAndSysId)
		return pillPrefixAndSysId[1];
}

export function getPillsArray(inputValue) {
	if (!inputValue)
		return false;

	return inputValue.match(/{{.*?\..*?}}/g);
}

export const getActivityDependents = (sysId, activityDependencies) => {
	if (!isObjectWithValues(activityDependencies) || !activityDependencies[sysId])
		return false;

	return activityDependencies[sysId];
};

export const getActivityDependentsByOrder = (sysId, newOrder, activityDependencies) => {
	if (!isObjectWithValues(activityDependencies) || !activityDependencies[sysId])
		return {};

	const dependents = activityDependencies[sysId] && activityDependencies[sysId].dependents;
	let applicableDependents = {};

	if (dependents) {
		Object.keys(dependents).forEach(id => {
			const thisDependent = dependents[id];
			if (thisDependent.order <= newOrder) // card has been moved below a dependent card
				applicableDependents[id] = thisDependent;
		});
	}

	return applicableDependents;
};

export const checkIfCardMovedAboveDependency = (allCardIdsInOrderByLane, card, activitiesDependedOn) => {
	let cardWithAllInputs = {};
	let idsToRemove = [];

	if (!isObjectWithValues(card) || !isArrayWithValues(allCardIdsInOrderByLane) || !isObjectWithValues(activitiesDependedOn))
		return {cardWithAllInputs, idsToRemove};

	const cardId = card.sys_id;
	const movedCardIndex = allCardIdsInOrderByLane.indexOf(cardId);

	if (movedCardIndex < 0)
		return {cardWithAllInputs, idsToRemove};

	let activitiesObject = {};
	activitiesObject[card.sys_id] = card;

	const movedCardDependencies = getActivityPillDependencies(activitiesObject);
	if (!isObjectWithValues(movedCardDependencies))
		return {cardWithAllInputs, idsToRemove};

	const cardsAfterMovedCard = allCardIdsInOrderByLane.slice(movedCardIndex + 1);

	Object.keys(movedCardDependencies).forEach(id => {
		if (cardsAfterMovedCard.includes(id)) {
			idsToRemove.push(id);

			const cardData = movedCardDependencies[id].dependents[cardId];

			if (isEmpty(cardWithAllInputs))
				cardWithAllInputs = cardData;
			else {
				Object.keys(cardData.inputs).forEach(inputName => {
					if (!cardWithAllInputs.inputs.hasOwnProperty(inputName))
						cardWithAllInputs.inputs[inputName] = cardData.inputs[inputName];
				});
			}

		}
	});

	return {cardWithAllInputs, idsToRemove};
};

export const removeInvalidPills = (cardsWithInvalidPills, cards, movedCardIds) => {
	if (!isObjectWithValues(cards))
		return {};

	let updatedCards = {...cards};

	if (!isObjectWithValues(cardsWithInvalidPills))
		return updatedCards;

	Object.keys(cardsWithInvalidPills).forEach(cardId => {
		const inputsWithInvalidPills = Object.keys(cardsWithInvalidPills[cardId].inputs);
		let cardToUpdate = {...cards[cardId]};
		let cardVars = [...get(cardToUpdate, 'inputs', [])];

		for (const index in inputsWithInvalidPills) {
			const inputName = inputsWithInvalidPills[index];
			const inputToUpdateIndex = cardVars.findIndex((variable) => variable.name === inputName);
			const matchingInput = cardVars[inputToUpdateIndex];

			if (!matchingInput) {
				cardVars[inputToUpdateIndex] = {...cardVars[inputToUpdateIndex]};
				continue;
			}

			let newInputValue = get(matchingInput, 'glideVarFields.variableMapping', '');

			movedCardIds.forEach(id => {
				newInputValue = removeAllPillsById(id, newInputValue).trim();
			});

			cardVars[inputToUpdateIndex] = {
				...matchingInput,
				glideVarFields: {
					...matchingInput.glideVarFields,
					variableMapping: newInputValue
				}
			};
		}

		cardToUpdate.inputs = cardVars;

		updatedCards[cardId] = cardToUpdate;
	});

	return updatedCards;
};

export const createWarningMessage = (affectedActivitiesObject, content) => {
	const errors = [];

	Object.keys(affectedActivitiesObject).forEach(id => {
		const activity = affectedActivitiesObject[id];
		const inputs = activity.inputs;

		if (!isObjectWithValues(inputs))
			return errors;

		Object.keys(inputs).forEach(variable => {
			errors.push(<li>{ACTIVITY_LABEL} {activity.order}: {activity.label} [{inputs[variable].label}]</li>);
		});
	});

	return (
		<div>
			{content}
			<ul>{errors}</ul>
		</div>
	);
};

export const normalizeGlideVar = input => {
	const mappingValue = get(input, 'glideVarFields.variableMapping', '');
	const variableName = get(input, 'glideVarFields.variableName', '');
	const value = get(input, 'glideVarFields.variableValue', '');

	if (!getPillsArray(mappingValue)) {
		return {
			variableName,
			variableMapping: '',
			variableValue: mappingValue || value,
			setMapping: false
		};
	} else {
		return {
			variableName,
			variableMapping: mappingValue,
			variableValue: '',
			setMapping: true
		};
	}
};

/*
 * Responsible for cleaning up variable mapping values. If
 * an input does not contain a pill then the variableValue field
 * is set.  If autoClearMapping is set then we also inject a field
 * to 'unset' the mapping value if we are setting the normal value.
 */
export const getGlideVarFieldValues = ({ inputs = [] }) => {
	return inputs.map(normalizeGlideVar);
};

/*
 * Returns activities with dependencies on activities being deleted
 */
export function getActivitiesAffectedByLaneDeletion(activitiesWithDependents, activitiesInLaneToDelete) {
	let activitiesAffectedByLaneDeletion = {};
	for (let [key, activity] of Object.entries(activitiesWithDependents)) {
		if (activitiesInLaneToDelete.includes(key))
			activitiesAffectedByLaneDeletion = {...activitiesAffectedByLaneDeletion, ...activity.dependents};
	}
	return activitiesAffectedByLaneDeletion;
}
