import {get} from 'lodash';
import '@servicenow/now-alert';
import * as NewProcessConsts from './creation-constants';
import '@servicenow/now-text-link';
import '@servicenow/now-radio-buttons';
import {TRIGGER} from './creation-constants';
import {TRIGGER_TYPE_SELECTION} from './creation-constants';

export const createSelectTriggerForm = (state) => {
	const {newPlaybookForm, filteredTriggers, triggerTypes, triggerDefOptions, triggerTypeOptions} = state;
	const triggerDisplayValue = get(newPlaybookForm, `${TRIGGER}.displayValue`, '');
	const triggerTypeDisplayValue = get(newPlaybookForm, `${TRIGGER_TYPE_SELECTION}.displayValue`, '');
	const paddingThatStrangelyFixesExtraThickBottomBorder = {'padding-top': '1px'};

	return (
		<span className='trigger-select'>
			<div className='new-pd-form-input first'>
				<now-radio-buttons
					name='triggerTypeSelection'
					options={triggerTypeOptions}
					manageValue
				/>
				<now-record-typeahead
					options={triggerTypes}
					name={NewProcessConsts.TRIGGER_TYPE_SELECTION}
					value={triggerTypeDisplayValue}
					placeholder={NewProcessConsts.PLACEHOLDER_TRIGGER_TYPE}
					aria-label={NewProcessConsts.TRIGGER_TYPE_ARIA_LABEL}
					search='contains'
				/>
			</div>

			<div className='new-pd-form-input' style={paddingThatStrangelyFixesExtraThickBottomBorder}>
				<now-radio-buttons
					name='triggerDefinitionSelection'
					options={triggerDefOptions}
					manageValue
				/>
				<now-record-typeahead
					options={filteredTriggers}
					name={TRIGGER}
					value={triggerDisplayValue}
					placeholder={NewProcessConsts.PLACEHOLDER_TRIGGERS}
					aria-label={NewProcessConsts.TRIGGER_ARIA_LABEL}
					search='contains'
				/>
			</div>
		</span>
	);
};
