const playbookCreationQuery = `
mutation($label: String!, $description: String, $application: String!, $trigger: String) {
  GlideProcessAutomation_Mutation {
    createProcessDefinition(label: $label, description: $description, scope: $application, triggerDefinition: $trigger) {
      sys_id,
      trigger
    }
  }
}`;

const basicPlaybookCreationQuery = `
mutation($label: String!, $description: String, $application: String!) {
  GlideRecord_Mutation {
    insert_sys_pd_process_definition(label: $label, description: $description, sys_scope: $application) {
      sys_id { value }
    }
  }
}`;


export const playbookCreation = (variables) => {
	return {
		variables,
		query: playbookCreationQuery
	};
};

export const basicPlaybookCreation = (variables) => {
	return {
		variables,
		query: basicPlaybookCreationQuery
	};
};
