import '@servicenow/now-dot-walk';
import '@servicenow/now-button';
import '@servicenow/now-popover';
import * as Utils from './dot-walk-utils';

export const view = (state) => {
	const {showWalker} = state;
	const dotWalkerData = Utils.getDotWalkData(state);
	const dotWalkerPath = Utils.getDotWalkPath(state) || Utils.getTriggerPath(state);
	const popoverPositions = [
		{ target: 'top-end', content: 'bottom-end' }
	];

	return (
		<div className='sn-dot-walker'>
			<now-popover
				manage-opened
				opened={showWalker}
				positions={popoverPositions}
			>
				<button
					className='dot-walk-icon'
					slot='trigger'
				/>
				<now-dot-walk
					dot-walk-path={dotWalkerPath}
					metadata={dotWalkerData}
					slot='content'
				/>
			</now-popover>
		</div>
	);
};
