export const getAvailableTriggersAndTypes = `
query {
  GlideProcessAutomation_Query {
    getAvailableTriggers {
      triggers { value, displayValue }
    }
  },
  GlideRecord_Query {
    triggerTypes: sys_pd_trigger_type {
      _results {
        displayValue: label { value }
        value: sys_id { value }
      }
    }
  }
}
`;
