import '../configuration-panel';
import '../configuration-modal';
import {get} from 'lodash';

export const view = (state) => {
	const {configurationState, dotWalkData} = state.properties;
	const showModal = get(configurationState, 'showConfigModal');

	return (
		<div>
			<sn-pd-config-panel configurationState={configurationState} dotWalkData={dotWalkData} />
			{/*for some reason the modal was showing a stale state, so am doing this*/}
			{ showModal ? <sn-pd-config-modal configurationState={configurationState} dotWalkData={dotWalkData} /> : null }
		</div>
	);
};
