import {get, head} from 'lodash';
import {getConfigurationDefaults, transformDotWalkResponse} from '../util/transforms';
import {ACTIVITY} from '../constants/shared';
import * as ActionTypes from '../constants/action-types';
import {getConfigurationData} from '../components/configuration-components/config-utils';
import {
	getTriggerDotWalkData,
	getInputDisplayPrefsFromActivity,
	getConfigurationStateFromProperties
} from '../util/selectors';
import {CHOICE, TEMPLATE_VALUE} from '../constants/glide-types';
import {isObjectWithValues, isArrayWithValues} from '../util/general-utils';
import {setFocusToConfigTriggerElement} from '../util/accessibility-utils';

export const showConfigurationPanel = ({action, updateProperties, properties, dispatch}) => {
	const {payload: {configType, data, triggerElement}} = action;
	const config = getConfigurationData(configType, data, properties);
	const textualMessage = head(get(data, 'textualMessages', [])); // get first message
	const sysId = get(config, 'sys_id');
	let inputDisplayPrefs = '';

	if (configType === ACTIVITY && sysId) { //fetch data for dot walker
		dispatch(ActionTypes.REQUEST_DOT_WALK_DATA, {id: sysId});
		inputDisplayPrefs = getInputDisplayPrefsFromActivity(data);
	}

	const configurationState = {
		...properties.configurationState,
		type: configType,
		config,
		panelOpen: true,
		textualMessage,
		inputDisplayPrefs,
		triggerElement
	};

	updateProperties({configurationState});
};

export const hideConfigurationPanel = ({updateProperties, properties: {configurationState}}) => {
	// return focus to the element that opened the panel
	setFocusToConfigTriggerElement(configurationState);

	updateProperties({configurationState: getConfigurationDefaults()});
};

export const getActionType = (type) => {
	/*
	 	Used for the following action types:
	 	SAVE_PLAYBOOK_CONFIGURATION_REQUESTED,
	 	SAVE_ACTIVITY_CONFIGURATION_REQUESTED,
	 	SAVE_LANE_CONFIGURATION_REQUESTED
	  */
	return `PLAYBOOK#SAVE_${type.toUpperCase().replace(' ', '_')}_CONFIGURATION_REQUESTED`;
};

export const handleSaveConfiguration = ({action, dispatch}) => {
	const type = get(action, 'payload.type', '');
	const config = get(action, 'payload.config', {});
	const actionType = getActionType(type);

	dispatch(actionType, {...config});
};

export const closeConfigPanelForDeletedItem = (configurationState, deletedSysIds, dispatch) => {
	if (!configurationState.panelOpen)
		return;
	const openItemSysId = get(configurationState, 'config.sys_id', '');
	if (openItemSysId && deletedSysIds.includes(openItemSysId))
		dispatch(ActionTypes.HIDE_CONFIGURATION_PANEL_REQUESTED);
};

export const updateDotWalkData = ({action: {payload}, updateProperties, state: {properties}}) => {
	const triggerDotWalkData = getTriggerDotWalkData(get(properties, 'playbook'));
	const dotWalkData = transformDotWalkResponse(payload, triggerDotWalkData);

	updateProperties({configurationDotWalkData: dotWalkData});
};

export function updateVariableMeta(variableMetadata, inputs) {
	if (!isArrayWithValues(inputs))
		return [];

	const elementsData = get(variableMetadata, 'formLayout.elementsData', []);
	const inputsMeta = elementsData.find(data => data.elementName === 'inputs');
	const elements = inputsMeta.meta_data;

	if (!isArrayWithValues(elements))
		return inputs;

	let currentInputs = [...inputs];

	elements.forEach(element => {
		// Note: attributes contains other helpful data, such as order, etc.
		const {elementName, dictionaryData: {internalType, dependentField, attributes}, choices} = element;

		// This is needed for types such as 'object' and 'array'
		// where internalType is 'string'
		currentInputs = setUiTypes(currentInputs, attributes, elementName);

		if (internalType === CHOICE) {
			currentInputs = currentInputs.map(field => {
				if (field.name === elementName)
					return {...field, choices};

				return {...field};
			});
		}

		if (internalType === TEMPLATE_VALUE && dependentField) {
			const parts = dependentField.split('.');
			const dependentFieldName = parts[parts.length - 1];

			currentInputs = currentInputs.map(field => {
				if (field.name === elementName) {
					const dependentInput = currentInputs.find(input => input.variableName === dependentFieldName);
					return {...field, dependentOnTable: dependentInput.value};
				}

				return {...field};
			});
		}
	});

	return currentInputs;
}

export const updateConfigTriggerElement = ({properties, action: {payload: {triggerElement}}, updateProperties}) => {
	const configState = getConfigurationStateFromProperties(properties);
	updateProperties({
		configurationState: {
			...configState,
			triggerElement
		}
	});
};

export function setUiTypes(inputs, attributes, elementName) {
	if (!isArrayWithValues(inputs))
		return [];

	return inputs.map(field => {
		if (field.name === elementName) {
			const uiType = attributes.find(el => el.name === 'uiType');
			if (isObjectWithValues(uiType))
				return {...field, uiType: uiType.value};
		}
		return {...field, uiType: field.internalType};
	});
}
